// Dependencies
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import RouterPropTypes from 'react-router-prop-types';

// UI
import Button from 'UI/Button';

// Styles
import './WEGOptions.css';

import { initLocalDB, resetLocalDB } from 'dataAccess';

/**
 * A collapsible menu on the left side of the WEG page, with additional
 * options for how the WEG list is displayed
 */
class WEGOptions extends Component {
  state = {
    collapsed: true,
    resultsPerRow: 4,
    resultsPerFetch: 4,
    listView: false,
  };

  maxResultsPerRow = 7;

  maxResultsPerFetch = 30;

  storedResultsPerRow = 4;

  componentWillMount() {
    const localSettings = localStorage.getItem('weg_options');
    if (localSettings !== null) {
      const settings = JSON.parse(localSettings);
      settings.collapsed = true;
      this.setState(settings);
    }
  }

  componentDidUpdate() {
    const { onUpdate } = this.props;
    onUpdate(this.state);

    localStorage.setItem('weg_options', JSON.stringify(this.state));
  }

  /** Render a button for incrementing or decrementing settings on the WEG options.
   * @param {func} onClick A method to perform when the button is clicked
   * @param {boolean} enabled Whether or not the button can be clicked
   * @param {boolean} flipped Whether or not to flip the button's icon 180 degrees
   */
  renderButton = (onClick, enabled, flipped) => {
    const classNames = [];
    classNames.push('modifier');
    if (!enabled) classNames.push('disabled');
    if (flipped) classNames.push('flipped');

    return (
      <Button
        title=""
        className={classNames.join(' ')}
        icon="chevron"
        click={onClick}
      />
    );
  };

  /** Increments or decrements the results per row option. Does not go above
   * maxResultsPerRow or below 1.
   * @param {number} direction The direction to move in, -1 or 1
   */
  modifyResultsPerRow = (direction) => {
    const { resultsPerRow, resultsPerFetch, listView } = this.state;
    if (listView) return;
    let modified = resultsPerRow + direction;

    modified = Math.min(modified, this.maxResultsPerRow);
    modified = Math.max(modified, 1);

    this.storedResultsPerRow = modified;

    if (modified !== resultsPerRow) {
      this.setState({
        resultsPerRow: modified,
        resultsPerFetch: Math.max(resultsPerFetch, modified),
      });
    }
  };

  /*
  modifyResultsPerFetch = ( direction ) => {
    const { resultsPerRow, resultsPerFetch } = this.state;
    let modified = resultsPerFetch + direction;

    modified = Math.min( modified, this.maxResultsPerFetch );
    modified = Math.max( modified, resultsPerRow );

    if ( modified !== resultsPerFetch ) {
      this.setState({
        resultsPerFetch: modified,
      });
    }
  }
  */

  /** Toggles between list and grid view.
   * @param {boolean} toggle An optional boolean to explicitly set true (grid) or false (list)
   */
  toggleListView = (toggle) => {
    const { listView } = this.state;
    let view = listView;
    if (toggle === null) {
      view = !view;
    } else {
      view = toggle;
    }
    if (listView !== view) {
      this.setState({
        listView: view,
        resultsPerRow: view ? 1 : this.storedResultsPerRow,
      });
    }
  };

  reImportFullWEG = () => {
  };

  render() {
    const { collapsed, resultsPerRow, listView } = this.state;

    const classNames = ['weg-options'];

    if (collapsed) classNames.push('collapsed');

    return (
      <div className={classNames.join(' ')}>
        <Button
          title=""
          className={`expand-button${collapsed ? '' : ' expanded'}`}
          icon="Gear"
          click={() => {
            this.setState({ collapsed: !collapsed });
          }}
        />
        <div className="option-table">
          <div className="option-row header">
            <span style={{ margin: 'auto' }}>WEG Options</span>
          </div>
          <div className="option-row">
            <div className="option-title">Results per row</div>
            <div className="option-selector">
              {this.renderButton(
                () => this.modifyResultsPerRow(-1),
                resultsPerRow > 1 && !listView,
                true
              )}
              <input
                className="option-input"
                type="text"
                value={listView ? this.storedResultsPerRow : resultsPerRow}
                onChange={(event) =>
                  this.modifyResultsPerRow(event.target.value - resultsPerRow)
                }
                disabled={listView}
              />
              {this.renderButton(
                () => this.modifyResultsPerRow(1),
                resultsPerRow < this.maxResultsPerRow && !listView,
                false
              )}
            </div>
          </div>
          {/*
            <div className="option-row">
              <div className="option-title">
                Results per fetch
              </div>
              <div className="option-selector">
                { this.renderButton(
                  () => this.modifyResultsPerFetch( -1 ),
                  resultsPerFetch > resultsPerRow,
                  true,
                ) }
                <input
                  className="option-input"
                  type="text"
                  value={ resultsPerFetch }
                  onChange={
                    event => this.modifyResultsPerFetch( event.target.value - resultsPerFetch )
                  }
                />
                { this.renderButton(
                  () => this.modifyResultsPerFetch( 1 ),
                  resultsPerFetch < this.maxResultsPerFetch,
                  false,
                ) }
              </div>
            </div>
          */}
          <div className="option-row">
            <div className="option-title">View Type</div>
            <div className="option-selector toggle">
              <div
                className={`option-button toggle-selected${listView ? ' on' : ''}`}
              />
              <input
                className={`option-button${listView ? '' : ' selected'}`}
                type="button"
                value="Grid"
                onClick={() => {
                  this.toggleListView(false);
                }}
              />
              <input
                className={`option-button${!listView ? '' : ' selected'}`}
                type="button"
                value="List"
                onClick={() => {
                  this.toggleListView(true);
                }}
              />
            </div>
          </div>
          <div className="option-row">
            <div className="option-title">Import WEG</div>
            <div className="option-selector">
              <input
                className="option-button csv"
                type="button"
                value="Download"
                onClick={this.reImportFullWEG}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

WEGOptions.propTypes = {
  /** A method to perform when the component updates, passed in by other components. */
  onUpdate: PropTypes.func.isRequired,

  /** Represents where the app is now to determine filters (params) for preparing CSV export.
   * Provided by react-route-dom and the withRouter method
   */
  location: RouterPropTypes.location.isRequired,
};

export default withRouter(WEGOptions);
