import React from 'react';
import PropTypes from 'prop-types';

// API
import { searchDotCMS, exportAssetsCSV } from 'API';

// UI
import Scrollbars from 'UI/Scrollbars';
import Loading from 'UI/Loading';
import Button from 'UI/Button';

// Components
import SearchResultsHeader from 'Components/SearchResultsHeader';
import WEGAssetCard from 'Components/WEGAssetCard';

import './WEGSearchResults.css';

import { AuthdViewLiveContext } from 'Context';
import { getOnlineStatus, searchAssetsWithFilters } from 'dataAccess';
import { transformWEGlist } from 'Utils/Transforms/DotCMS';


const BASE_URL = import.meta.env.VITE_ODIN_API_BASE_URL;

/**
 * Displays WEG Asset search results for a given term
 */
class WEGSearchResults extends React.Component {
  static contextType = AuthdViewLiveContext;

  state = {
    totalHits: 0,
    items: [],
    isFetching: false,
    isFetchingInitial: false, // only true the first time we are fetching results
    searchOffset: 0,
    selectedHeaderOption: 'grid',
  };

  componentDidMount() {
    this.clearSearch();
  }

  componentDidUpdate(nextProps) {
    const { searchString: nextSearchString } = nextProps;
    const { searchString: prevSearchString } = this.props;
    if (prevSearchString !== nextSearchString) {
      this.clearSearch();
    }
  }

  clearSearch = () => {
    this.setState({
      totalHits: 0,
      items: [],
      searchOffset: 0,
      isFetchingInitial: true,
      isFetching: false,
    });
    const { searchString } = this.props;
    setTimeout(() => this.runSearch(searchString), 10);
  };

  /**
   * Check if we are at the bottom of the scrollbar.
   * If so attempt to fetch more data
   */
  handleScrollUpdate = (values) => {
    const { isFetching, totalHits, searchOffset, selectedHeaderOption } =
      this.state;
    const { hide } = this.props;
    if (
      selectedHeaderOption !== 'csv' &&
      !isFetching &&
      searchOffset <= totalHits &&
      !hide
    ) {
      const { searchString } = this.props;
      const { scrollTop, scrollHeight, clientHeight } = values;
      const cutoffDistance = 50;
      // t will be greater than 1 if we are about to reach the bottom
      const t = (scrollTop + cutoffDistance) / (scrollHeight - clientHeight);
      if (t > 1) {
        this.runSearch(searchString);
      }
    }
  };

  /**
   * Fetch data for a given search string
   */
  runSearch = async (searchString) => {
    console.log('RUNNING SEARCH FOR WEG:', searchString);
    const { searchOffset, isFetching } = this.state;
    const { authdViewLive } = this.context;

    if (isFetching) {
      console.info('isFetching; returning')
      return;
    }

    this.setState({ isFetching: true });
    const limit = 30;
    let assets;
    let isOnline = false
    await getOnlineStatus().then(online => {
      console.info("onlineStatus in runSearch:", online);
      isOnline = online;
      if (!online) {
        console.log("runSerach NOT online!");
        searchAssetsWithFilters({searchTerm: searchString}, searchOffset, limit)
        .then(data => {
          console.log("searchAssetsWithFilters in runSearch data:");
          console.dir(data);
          assets = data;
        });
      } else {
        console.log("runSearch online!");
      }
    });
    console.info('runSearch assets:');
    console.dir(assets);
    if (assets) {
      const { items } = this.state;
      this.setState({
        items: [...items, ...assets.wegList],
        totalHits: assets.totalHits,
        isFetchingInitial: false,
        searchOffset: searchOffset + limit,
      });

      this.setState({
        isFetching: false,
        isFetchingInitial: false,
      });
    }
    else if (searchString) {
      console.info('runSearch: !assets and searchString')
      searchDotCMS({
        section: 'WEG',
        searchString,
        limit,
        offset: searchOffset,
        authdViewLive,
      }).then(async (response) => {
        const { items } = this.state;
        const { contentlets, totalHits } = response;
        if (contentlets) {
          const newContentlets = await transformWEGlist({contentlets: contentlets}, searchOffset, totalHits, isOnline)
          console.log("newContentlets");
          console.dir(newContentlets);
          this.setState({
            items: [...items, ...newContentlets.wegList],
            totalHits,
            isFetchingInitial: false,
            searchOffset: searchOffset + limit,
          });
        }
        this.setState({
          isFetching: false,
          isFetchingInitial: false,
        });
      });
    }
  };

  handleHeaderOptionSelected = (option) => {
    const { value } = option;
    this.setState({ selectedHeaderOption: value });
  };

  handleExport = (searchString) => {
    searchDotCMS({
      section: 'WEG',
      searchString,
      limit: 0,
      offset: 0,
    }).then((response) => {
      if (response.contentlets) {
        exportAssetsCSV(response.contentlets);
      }
    });
  };

  /**
   * Render the WEG cards
   */
  renderItems() {
    const { items, selectedHeaderOption } = this.state;
    console.info('search results items:');
    console.dir(items);
    if (selectedHeaderOption === 'csv') {
      const { searchString } = this.props;
      return (
        <div className="csv-export">
          <Button
            title="Click Here to Download"
            click={() => this.handleExport(searchString)}
            className="csv-export-button"
          />
        </div>
      );
    }
    const itemList = items.map((item) => {
      console.info("WEGSearchResults renderItems map > item:")
      console.dir(item);
      const data = { ...item };

      data.id = item.name;
      if (item.images) {
        const parsedImgs = JSON.parse(item.images);
        data.image = parsedImgs.length ? `${BASE_URL}dotcms/${parsedImgs[0].url}` : '';
        console.log("WegSearchResults items.map data.image set to:");
        console.dir(data.image)
        const startUrl = BASE_URL.startsWith("https://") ? "https://" : ""
        data.image =  startUrl + data.image.split("https://").slice(-1)[0].replace('//', '/')
      } else {
        data.image = '';
      }

      const categories = [];
      if (data.domain && data.domain.length > 0) {
        data.domain.forEach((cat) => {
          const catValue = cat[Object.keys(cat)[0]];
          categories.push(catValue);
        });
      }
      data.categories = categories;
      let className = 'medium';
      if (selectedHeaderOption === 'list') {
        className = 'list';
      }
      return (
        <WEGAssetCard key={data.inode} className={className} data={item} />
      );
    });
    if (itemList.length) return itemList;
    return <p className="no-results">No Results Found</p>;
  }

  render() {
    const { totalHits, isFetchingInitial, selectedHeaderOption } = this.state;
    const { hide, className } = this.props;
    const hideClassName = hide ? `hide ${className}` : className;
    const loadingRender = isFetchingInitial ? <Loading internal /> : null;
    const headerOptions = [
      { displayName: 'Grid', value: 'grid' },
      { displayName: 'List', value: 'list' },
      { displayName: 'CSV Export', value: 'csv' },
    ];

    return (
      <div className={`weg-search-results-wrapper ${hideClassName}`}>
        <SearchResultsHeader
          title="Assets"
          numberOfHits={totalHits}
          options={headerOptions}
          onOptionSelected={this.handleHeaderOptionSelected}
          selectedOption={selectedHeaderOption}
        />
        <Scrollbars onUpdate={this.handleScrollUpdate}>
          <div className="weg-search-results">
            {loadingRender}
            {this.renderItems()}
          </div>
        </Scrollbars>
      </div>
    );
  }
}

// Define the data type for each property passed to the component
WEGSearchResults.propTypes = {
  /**
   * The string to search
   */
  searchString: PropTypes.string.isRequired,
  /**
   * should this component be hidden?
   */
  hide: PropTypes.bool,
  /**
   * A custom class name for this component
   */
  className: PropTypes.string,
};

WEGSearchResults.defaultProps = {
  hide: false,
  className: '',
};

export default WEGSearchResults;
