// "IndexedDBImage" probably isn't the best name for this

import { getImage } from 'dataAccess';
import { getOnlineStatus } from 'dataAccess';
import React, { useEffect, useState } from 'react';
import placeholderImage from '/src/ui/Image/images/placeholder.png';

export const IndexedDBImage = ({ src, alt, style }) => {
  const [imageSrc, setImageSrc] = useState(null);
  const PLACEHOLDER_SRC = placeholderImage;
  const API_BASE_URL = import.meta.env.VITE_ODIN_API_BASE_URL;

  useEffect(() => {
    // console.log("IndexedDBImage useEffect src:", src);
    // const images = import.meta.glob('/src/ui/Image/images/*', { eager: true });
    // console.log("Images mount images:")
    // console.dir(images);
    // console.dir(src);
    let isMounted = true;
    loadImage(isMounted);

    return () => {
      isMounted = false;
    };
  }, [src]);

  const loadImage = async (isMounted) => {
    // console.info('IndexedDBImage loadImage, src:', src);
    // console.info('IndexedDBImage loadImage, isMounted:', isMounted);
    const online = await getOnlineStatus();
    // console.info('IndexedDBImage loadImage, online:', online);

    if (!online && src.startsWith(`${API_BASE_URL}dotcms`)) {
      console.info('IndexedDBImage loadImage !online, API_BASE_URL:', API_BASE_URL);
      // console.info('IndexedDBImage loadImage !online, src:', src);
      const imageBlob = await getImage(src.replace(`${API_BASE_URL}dotcms`, ''));
      // console.log("imageBlob:");
      // console.dir(imageBlob);
      if (imageBlob && isMounted) {
        const imageUrl = URL.createObjectURL(imageBlob);
        setImageSrc(imageUrl);
      } else {
        console.warn("could not find this image:", src);
        console.info("searched for:", src.replace(`${API_BASE_URL}dotcms`, ''));
        setImageSrc(PLACEHOLDER_SRC);
      }
    } else {
      setImageSrc(src);
    }
  };

const renderPlaceholder = () => {
  return <img src={PLACEHOLDER_SRC}/>;
}

  return imageSrc ? (
    <img src={imageSrc} alt={alt} style={style}/>
  ) : renderPlaceholder();
};
