// Dependencies
import React from 'react';
import PropTypes from 'prop-types';

// Utils
import { getUID } from 'Utils/UID';

// UI
import Icon from 'UI/Icon';
import Image from 'UI/Image';
import Tooltip from 'UI/Tooltip';

// Styles
import './Button.css';

/**
 * Custom button element
 */
const button = (props) => {
  const {
    children,
    className,
    click,
    hideTitle,
    hideTooltip,
    icon,
    iconClassname,
    id,
    image,
    style,
    title,
    type,
    tooltipPlace,
    disabled,
  } = props;
  let { tooltip } = props;
  // default the tooltip text to the title if one is not defined
  if (!tooltip) tooltip = title;
  let showTooltip = hideTitle && !hideTooltip;
  if (!showTooltip) showTooltip = tooltip !== title;
  let uid = id;
  if (!uid) uid = getUID('button');
  const tipID = getUID('tooltip');
  return (
    <button
      className={`btn ${className}`}
      onClick={click}
      type={type}
      style={style}
      data-tip
      id={uid}
      data-for={tipID}
      disabled={disabled}
    >
      {icon && <Icon file={icon} tooltip={tooltip} className={iconClassname} />}
      {image && <Image file={image} tooltip={tooltip} />}
      {!hideTitle && <span className="btn-title">{title}</span>}
      {children}
      {showTooltip && (
        <Tooltip id={tipID} parent={uid} place={tooltipPlace}>
          {tooltip}
        </Tooltip>
      )}
    </button>
  );
};

// Define the data type for each property passed to the component
button.propTypes = {
  /**
   * Title of the button. Renders after the icon and image, if any.
   * Rendered inside the button or as a tooltip based on `hideTitle` value.
   */
  title: PropTypes.string.isRequired,
  /** Method to call when the button is clicked */
  click: PropTypes.func.isRequired,
  /**
   * Additional elements to display within the button.
   * Defined as the elements inside the <Button> open and closing tags.
   * Renders after the icon, image, and title, if any.
   */
  children: PropTypes.node,
  /** Additional classes to apply to the button */
  className: PropTypes.string,
  /** Hide the title of the button. If true, title is rendered as a tooltip */
  hideTitle: PropTypes.bool,
  /** Hide the tooltip of the button. */
  hideTooltip: PropTypes.bool,
  /** Icon to display inside the button. Renders first. */
  icon: PropTypes.string,
  /** Set the class name of Icon */
  iconClassname: PropTypes.string,
  /** Unique `id` of the button */
  id: PropTypes.string,
  /** Image to display inside the button. Renders after the icon, if any. */
  image: PropTypes.string,
  /** Inline styles to apply to the button */
  style: PropTypes.object,
  /** Tooltip text to apply to the button. Defaults to the title value */
  tooltip: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
  /** Type of button to display */
  type: PropTypes.oneOf(['button', 'reset', 'submit']),
  /**  Where the tooltip should appear */
  tooltipPlace: PropTypes.oneOf(['top', 'right', 'bottom', 'left']),
  /** Disable the button */
  disabled: PropTypes.bool,
};

// Set default values for optional properties
button.defaultProps = {
  children: null,
  className: '',
  hideTitle: false,
  hideTooltip: false,
  icon: null,
  iconClassname: '',
  id: '',
  image: null,
  style: {},
  tooltip: null,
  type: 'button',
  tooltipPlace: 'bottom',
  disabled: false,
};

export default button;
