// Dependencies
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

// UI
import Button from 'UI/Button';
import Icon from 'UI/Icon';

// Components

// Styles
import './Tiers.css';

/**
 * Render tier icons
 */
class Tiers extends Component {
  state = {
    tiers: ['Tier1', 'Tier2', 'Tier3', 'Tier4'],
  };

  render() {
    const { tiers } = this.state;
    const { click, isButton, list, label } = this.props;

    return (
      <div className="tiers">
        {label}
        {tiers.map((tier) => {
          let tierClass = 'tier';
          if (list.indexOf(tier) > -1) {
            tierClass += ' active';
          }
          const uniqueKey = tier.toLowerCase();
          if (isButton) {
            return (
              <Button
                key={uniqueKey}
                title={tier}
                click={() => click(tier)}
                icon={tier.toLowerCase()}
                className={tierClass}
                hideTitle
              />
            );
          }
          return (
            <Icon
              className={tierClass}
              key={uniqueKey}
              file={tier.toLowerCase()}
            />
          );
        })}
      </div>
    );
  }
}

// Define data type of each property passed to the component
Tiers.propTypes = {
  click: PropTypes.func,
  isButton: PropTypes.bool,
  list: PropTypes.array,
  label: PropTypes.string,
};

// Set default values for optional properties
Tiers.defaultProps = {
  click: (tier) => console.log(`${tier} Button Clicked`),
  isButton: false,
  list: [],
  label: '',
};

export default withRouter(Tiers);
