import React, { useContext } from 'react';
import { UpdateContext } from 'Context';
import './UpdateNotification.css';

const UpdateNotification = () => {
  const { needRefresh, refreshPage, updateDismissed,  handleDismiss } = useContext(UpdateContext);

  if (!needRefresh || updateDismissed) return null;

  return (
    <div className="update-notification">
      <div className="update-modal">
        <h2>Update Available</h2>
        <p>A new version of this app is available. Would you like to update?</p>
        <button onClick={refreshPage}>Yes, Update</button>
        <button onClick={handleDismiss}>Later</button>
      </div>
    </div>
  );
};

export default UpdateNotification;
