import React from 'react';

import Layout from 'UI/Layout';
import Scrollbars from 'UI/Scrollbars';

import { getUID } from 'Utils/UID';

import './TermsOfUse.css';

import content from './TermsOfUse.json';

/** Displays the ODIN Terms of Use page */
const termsOfUse = () => (
  <Layout className="tou-page">
    <h1 className="page-header">Terms of Use</h1>
    <Scrollbars className="page-content">
      {content.map((section) => (
        <div key={getUID()}>
          <h2 className="tou-header">{section.title}</h2>
          <hr />
          {section.body.map((paragraph) => (
            <p key={getUID()}>{paragraph}</p>
          ))}
        </div>
      ))}
    </Scrollbars>
  </Layout>
);

export default termsOfUse;
