/* eslint-disable */
import React, {
  useState,
  createContext,
  useContext,
  useEffect,
  useRef,
} from 'react';
import { AuthdViewLiveContext } from './AuthdViewLiveContext';

export const AuthdViewLiveProvider = ({ children }) => {
  let viewLive = true;
  const viewLiveSetting = localStorage.getItem('authdViewLive');
  if (viewLiveSetting && viewLiveSetting == 'false') {
    viewLive = false;
  }

  const [authdViewLive, setAuthdViewLive] = useState(viewLive);
  const mountedRef = useRef();

  useEffect(() => {
    if (mountedRef.current) {
      localStorage.setItem('authdViewLive', authdViewLive);
      setTimeout(() => window.location.reload(), 500);
    } else {
      mountedRef.current = true;
    }
  }, [authdViewLive]);

  return (
    <AuthdViewLiveContext.Provider value={{ authdViewLive, setAuthdViewLive }}>
      {children}
    </AuthdViewLiveContext.Provider>
  );
};

export default AuthdViewLiveProvider;

export function useAuthdViewLive() {
  const context = useContext(AuthdViewLiveContext);
  if (context == undefined) {
    throw new Error('authd view context must be used within a provider');
  }
  return context;
}
