// Dependencies
import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import MilSymbol from 'milsymbol';

// Utils

// UI
import Modal from 'UI/Modal';
import Image from 'UI/Image';

// Components
import Tiers from 'Components/Tiers';
import AssetDetail from 'Components/AssetDetail';
import { getTierFromDateOfIntro, transformAsset } from 'Utils/Transforms/DotCMS';
import { IndexedDBImage } from 'Components/IndexedDBImage'
import { getOnlineStatus } from 'dataAccess';

// Styles
import './WEGAssetCard.css';

// const BASE_URL = import.meta.env.VITE_ODIN_API_BASE_URL;

/**
 * Render a WEG Asset Card with overview that opens a modal with details
 */
const wegAssetCard = (props) => {
  const { data, className } = props;
  const { title, image, dateOfIntroduction, categories } = data;
  const [isOnline, setIsOnline] = useState(false);

  let calledOnce = false;

  const config = {
    node: {
      width: 210,
      height: 85,
      corner: 5,
      padding: 5,
      lines: 1.5,
      selected: 4,
      icon: 0.75,
      toggle: 20,
      margin: {
        t: 45,
        r: 10,
        b: 15,
        l: 10,
      },
    },
    svg: {},
    grid: {},
    colors: {
      icon: {
        bg: '#fff',
        fill: '#000',
      },
      text: {
        bg: '#9c9c95',
        fill: '#fff',
      },
      lines: '#2d2d2d',
      toggle: {
        bg: '#3e3e3e',
        fill: '#fff',
      },
      export: {
        bg: 'transparent',
        fill: '#fff',
      },
      selected: '#e6ba08',
    },
    dot: {
      radius: 4,
      offset: 10,
    },
    symbolButton: {
      width: 20,
      height: 20,
      padding: 5,
      margin: 5,
      selected: 'U',
      list: [
        {
          name: 'Unknown',
          value: 'U',
          icon: 'SUGP-----------',
        },
        {
          name: 'Neutral',
          value: 'N',
          icon: 'SNGP-----------',
        },
        {
          name: 'Friend',
          value: 'F',
          icon: 'SFGP-----------',
        },
        {
          name: 'Hostile',
          value: 'H',
          icon: 'SHGP-----------',
        },
      ],
    },
    transform: null,
    exportUnit: null,
    nodeCenter: null,
    rollUp: null,
    selected: null,
    isIE: false,
  };

  useEffect(() => {
    if (!calledOnce) getOnlineStatus().then(
      (online) => isOnline !== online && setIsOnline(online)
    );
    calledOnce = true;
  }, [])

  const openModalHandler = () => {
    const { inode, id } = data;
    console.info("WEGAssetCard openModalHandler data");
    console.dir(data);
    console.dir(data.images);
    // const newData = typeof data.images === "string" ? transformAsset({contentlets: [data]}) : data;
    console.log("test transformed asset:");
    // console.dir(newData);
    let children = <AssetDetail  assetName={data.title} id={id} />;
    if (!id) {
      children = <AssetDetail  assetName={data.title} id={inode} />;
    }
    return {
      section: 'WEG',
      title: data.title,
      children,
      modalStyle: {
        width: '83%',
        height: '83%',
      },
    };
  };

  const getTiers = () => [getTierFromDateOfIntro(dateOfIntroduction)];

  /**
   * If there is no image associated with the asset, render a placeholder.
   */
  const renderImage = () => {
    // console.log("WegAssetCard.jsx renderImage image:");
    // console.dir(image);
    if (image) {
      return <IndexedDBImage src={image} alt={title} online={isOnline} />;
    }
    return <Image title="placeholder" file="placeholder.png" />;
  };

  return (
    <div className={`weg-card ${className}`}>
      {/* <img src={new MilSymbol.Symbol("S*GPUCDMM--D**G").toDataURL()}/> */}
      <Modal
        open={openModalHandler}
        // eslint-disable-next-line react/jsx-boolean-value
        showClose={true}
        identifier={data.id}
      >
        <div className="top">
          <div className="image">{renderImage()}</div>
          <Tiers label="Tier: " list={getTiers()} />
        </div>
        <div className="colorbar" />
        <div className="footer">
          <div className="title">{title}</div>
          <div className="categories">{categories.join(', ')}</div>
        </div>
      </Modal>
    </div>
  );
};

wegAssetCard.propTypes = {
  /**
   * Data as received from filterWEG
   */
  data: PropTypes.object.isRequired,
  /**
   * Custom class name for this component
   */
  className: PropTypes.string,
};

wegAssetCard.defaultProps = {
  className: '',
};

export default wegAssetCard;
