import { useState, useEffect } from 'react';
import { addMisc, getMiscByKey } from 'dataAccess'

export function usePWAStatus() {
  const [isInstalled, setIsInstalled] = useState(false);
  const [isPWA, setIsPWA] = useState(false);
  const [isSupported, setIsSupported] = useState(false);
  const [isChecking, setIsChecking] = useState(true);

  const saveInstallStatus = async () => {
    getMiscByKey('installed').then(installed => {
      console.log("getMiscByKey res");
      console.dir(installed);
      const isInstalled = installed ? installed.value : false;
      if (!isInstalled) {
        addMisc("installed", false)
      }
      setIsInstalled(isInstalled)
    })
  }

  const checkPWAStatus = async () => {
    saveInstallStatus();
    let installed = false;
    let supported = false;

    const isStandalone =
      window.matchMedia('(display-mode: standalone)').matches ||
      window.navigator.standalone === true;

    installed = isStandalone;
    setIsPWA(isStandalone);

    console.log("usaPwaStatus running as PWA: ", installed);

    // Check if getInstalledRelatedApps is supported
    if ('getInstalledRelatedApps' in navigator) {
      supported = true;
      try {
        const relatedApps = await navigator.getInstalledRelatedApps();
        const pwaApp = relatedApps.find(
          (app) =>
            app.platform === 'webapp' &&
            app.url === window.location.origin + '/manifest.webmanifest'
        );
        if (pwaApp) {
          installed = true;
        }
      } catch (error) {
        console.error('Error fetching related apps:', error);
      }
    }

    // Check local storage for installation flag
    // if (localStorage.getItem('pwaInstalled') === 'yes') {
    //   installed = true;
    // }

    return {supported, installed}
  };

  useEffect(() => {
    let isMounted = true;



    checkPWAStatus().then((res) => {
      if (isMounted) {
        const { supported, installed } = res;
        console.dir({ supported, installed })
        setIsSupported(supported);
        setIsInstalled(installed);
        setIsChecking(false);
      }
    });

    const handleAppInstalled = () => {
      console.log('App was installed');
      // localStorage.setItem('pwaInstalled', 'yes');
      addMisc("installed", true);
      setIsInstalled(true);
    };

    const handleDisplayModeChange = () => {
      const isStandalone =
        window.matchMedia('(display-mode: standalone)').matches ||
        window.navigator.standalone === true;
      setIsPWA(isStandalone);
      if (isStandalone) {
        setIsInstalled(true);
      }
    };

    window.addEventListener('appinstalled', handleAppInstalled);
    window
      .matchMedia('(display-mode: standalone)')
      .addEventListener('change', handleDisplayModeChange);

    return () => {
      isMounted = false;
      window.removeEventListener('appinstalled', handleAppInstalled);
      window
        .matchMedia('(display-mode: standalone)')
        .removeEventListener('change', handleDisplayModeChange);
    };
  }, []);

  function getPWADisplayMode() {
    if (document.referrer.startsWith('android-app://'))
      return 'twa';
    if (window.matchMedia('(display-mode: browser)').matches)
      return 'browser';
    if (window.matchMedia('(display-mode: standalone)').matches)
      return 'standalone';
    if (window.matchMedia('(display-mode: minimal-ui)').matches)
      return 'minimal-ui';
    if (window.matchMedia('(display-mode: fullscreen)').matches)
      return 'fullscreen';
    if (window.matchMedia('(display-mode: window-controls-overlay)').matches)
      return 'window-controls-overlay';

    return 'unknown';
  }

  return { isInstalled, isPWA, isSupported, isChecking, checkPWAStatus, getPWADisplayMode };
}
