// Dependencies
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import ReactScroll from 'react-custom-scrollbars-2';

// Assets
import './Scrollbars.css';

/**
 * Custom Scrollbars wrapper
 */
class Scrollbars extends Component {
  componentDidUpdate(prevProps) {
    const { children: oldChildren } = prevProps;
    const { children: newChildren, resetTop } = this.props;
    if (oldChildren !== newChildren) {
      if (resetTop) this.scrollbar.scrollToTop();
    }
  }

  /**
   * Scroll event handler `handleScroll( even, values )`
   * - `event`: Native scroll event
   * - `values`: (object) The current scrolling position
   * -- `values.top`: (number) scrollTop progress, from 0 to 1
   * -- `values.left`: (number) scrollLeft progress, from 0 to 1
   * -- `values.scrollTop`: (number) Native scrollTop
   * -- `values.scrollLeft`: (number) Native scrollLeft
   * -- `values.scrollHeight`: (number) Native scrollHeight
   * -- `values.scrollWidth`: (number) Native scrollWidth
   * -- `values.clientHeight`: (number) Native clientHeight
   * -- `values.clientWidth`: (number) Native clientWidth
   */
  handleScroll = (event) => {
    const { onScroll } = this.props;
    const values = this.scrollbar.getValues();
    if (onScroll) onScroll(event, values);
  };

  /**
   * Called when the component is updated `handleUpdate( values )`
   * - `values`: (object) The current scrolling position
   * -- `values.top`: (number) scrollTop progress, from 0 to 1
   * -- `values.left`: (number) scrollLeft progress, from 0 to 1
   * -- `values.scrollTop`: (number) Native scrollTop
   * -- `values.scrollLeft`: (number) Native scrollLeft
   * -- `values.scrollHeight`: (number) Native scrollHeight
   * -- `values.scrollWidth`: (number) Native scrollWidth
   * -- `values.clientHeight`: (number) Native clientHeight
   * -- `values.clientWidth`: (number) Native clientWidth
   */
  handleUpdate = (values) => {
    const { onUpdate } = this.props;
    const newValues = { ...values };
    if (onUpdate) onUpdate(newValues);
  };

  /**
   * Called when scrolling has started `handleScrollStart( values )`
   * - `values`: (object) The current scrolling position
   * -- `values.top`: (number) scrollTop progress, from 0 to 1
   * -- `values.left`: (number) scrollLeft progress, from 0 to 1
   * -- `values.scrollTop`: (number) Native scrollTop
   * -- `values.scrollLeft`: (number) Native scrollLeft
   * -- `values.scrollHeight`: (number) Native scrollHeight
   * -- `values.scrollWidth`: (number) Native scrollWidth
   * -- `values.clientHeight`: (number) Native clientHeight
   * -- `values.clientWidth`: (number) Native clientWidth
   */
  handleScrollStart = (values) => {
    const { onScrollStart } = this.props;
    if (onScrollStart) onScrollStart(values);
  };

  /**
   * Called when scrolling has stopped `handleScrollStop( values )`
   * - `values`: (object) The current scrolling position
   * -- `values.top`: (number) scrollTop progress, from 0 to 1
   * -- `values.left`: (number) scrollLeft progress, from 0 to 1
   * -- `values.scrollTop`: (number) Native scrollTop
   * -- `values.scrollLeft`: (number) Native scrollLeft
   * -- `values.scrollHeight`: (number) Native scrollHeight
   * -- `values.scrollWidth`: (number) Native scrollWidth
   * -- `values.clientHeight`: (number) Native clientHeight
   * -- `values.clientWidth`: (number) Native clientWidth
   */
  handleScrollStop = (values) => {
    const { onScrollStop } = this.props;
    if (onScrollStop) onScrollStop(values);
  };

  render() {
    const { className, children, styles } = this.props;
    return (
      <div className="scrollbar-wrapper">
        <ReactScroll
          ref={(node) => {
            this.scrollbar = node;
          }}
          className={`scrollbar-content ${className} scroll-bar`}
          styles={styles}
          onScroll={this.handleScroll}
          onUpdate={this.handleUpdate}
          onScrollStart={this.handleScrollStart}
          onScrollStop={this.handleScrollStop}
          renderView={(props) => <div {...props} className="scrollbar-inner" />}
        >
          {children}
        </ReactScroll>
      </div>
    );
  }
}

// Define the data type of the properties passed to the component
Scrollbars.propTypes = {
  /**
   * The content to display inside the scrollbar container
   * Defined as the elements inside the <Scrollbars> open and closing tags.
   */
  children: PropTypes.node.isRequired,
  /** Additional classes to apply to the scrollbar container */
  className: PropTypes.string,
  /**
   * Additional processes for the Scroll event handler
   * Passes `event` & `values` object: `onScroll( event, values )`. See handleScroll.
   */
  onScroll: PropTypes.func,
  /**
   * Additional processes for when the scrolling has started.
   * Passes `values` object: `onScrollStart( values )`. See handleScrollStart.
   */
  onScrollStart: PropTypes.func,
  /**
   * Additional processes for when the scrolling has stopped
   * Passes `values` object: `onScrollStop( values )`. See handleScrollStop.
   */
  onScrollStop: PropTypes.func,
  /**
   * Additional processes for when the scrollbar component is updates
   * Passes `values` object: `onUpdate( values )`. See handleUpdate.
   */
  onUpdate: PropTypes.func,
  /** Inline styles to apply to the scrollbar container */
  styles: PropTypes.object,
  /** Flag to tell the component that the content is new and to scroll to top */
  resetTop: PropTypes.bool,
};

// Set default values for optional properties
Scrollbars.defaultProps = {
  className: '',
  onScroll: null,
  onScrollStart: null,
  onScrollStop: null,
  onUpdate: null,
  styles: null,
  resetTop: false,
};

export default Scrollbars;
