import axios from 'axios';

import { getWEGSubNav } from 'API';

import { transformSubnav } from 'Utils/Transforms/DotCMS';

const BASE_URL = import.meta.env.VITE_ODIN_API_BASE_URL;
export const API_URL = `${BASE_URL}dotcms/api/content/`;
const GRAPHQL_URL = `${BASE_URL}dotcms/api/v1/graphql`;

export const hitGraphQL = (query) => {
  return axios.post(GRAPHQL_URL, query).then(
    (response) => {
      return response.data;
    },
    (error) => error
  );
};

/**
 * Retrieve the subnavigation structure for the selected section of the application
 */
export const getSubnav = (_section) => {
  return getWEGSubNav().then((response) => {
    console.log("getWEGSubnav response:");
    console.dir(response);
    response.data.reference = 'Worldwide Equipment Guide';
    response.reference = 'Worldwide Equipment Guide';
    return transformSubnav(response.data, 'WEG');
  });
};

export async function fetchLocalData(file) {
  try {
    const response = await fetch('/data/' + file);
    console.dir(response);

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const data = await response.json();
    return data;
  } catch (error) {
    console.error('Failed to fetch local data:', error);
  }
}

/**
 * Basic comparison function for sorting subnavs
 */
export const compareIndex = (a, b) => a.index - b.index;

export const isIdentifier = (name) => /^[a-zA-Z0-9]{32}$/.test(name);

export const getContentById = (id, live) => {
  const body = {
    limit: 0,
    offset: 0,
    sort: 'score',
    query: `+(identifier:"${id}")`,
  };
  const url = `${API_URL}_search`;

  if (live) body.query += '+live:true';

  return axios.post(url, body).then(
    (res) => {
      if (
        res.data.entity.jsonObjectView.contentlets &&
        res.data.entity.jsonObjectView.contentlets.length > 0
      ) {
        const contentlet = res.data.entity.jsonObjectView.contentlets.find(
          ({ identifier }) => identifier == id
        );
        if (contentlet) return contentlet;
      }
      return false;
    },
    (error) => error
  );
};

export const getContentByName = (params) => {
  const { section, article: name, authdViewLive } = params;
  const articleTitleURL = name.replace(/_/g, '%20');
  const articleTitle = name.replace(/_/g, ' ');
  const url = `${API_URL}_search`;
  const body = {
    limit: 0,
    offset: 0,
    sort: 'score',
    query: '',
  };
  switch (section) {
    case 'DATE':
      body.query = `+(contentType:DateArticle contentType:DateRegion contentType:DateOE)+title:"${articleTitleURL}"`;
      if (params.howTo) {
        body.query = `+(contentType:HowTo)+title:"${articleTitleURL}"`;
      }
      break;
    case 'VOA':
      body.query = `+(contentType:VOAArticle)+title:"${articleTitleURL}"`;
      break;
    case 'Home Page':
      body.query = `+(contentType:Announcement)+Announcement.title:${articleTitleURL}`;
      break;
    case 'TC':
      body.query = `+(inode:${articleTitle} contentType:TPArticle contentType:TPChapter)+title:"${articleTitleURL}"`;
      break;
    case 'WEG':
      body.query = `+(contentType:HowTo)+title:"${articleTitleURL}"`;
      break;
  }

  if (authdViewLive) {
    body.query += '+live:true';
  }

  body.query += ' +deleted:false';

  return axios.post(url, body).then(
    (res) => {
      if (
        res.data.entity.jsonObjectView.contentlets &&
        res.data.entity.jsonObjectView.contentlets.length > 0
      ) {
        const contentlet = res.data.entity.jsonObjectView.contentlets.find(
          ({ title }) => title.toLowerCase() == articleTitle.toLowerCase()
        );
        if (contentlet) return contentlet;
      }
      return false;
    },
    (error) => error
  );
};

const handleArticleTransform = (
  contentlet,
  params,
  searchSnippet,
  searchTerm
) => {
  const { section } = params;
  const transformData = {
    title: contentlet.title,
    categories: Object.values(contentlet.categories || {}),
    jsonContent: contentlet.content,
    toc: contentlet.toc ? JSON.parse(contentlet.toc) : null,
    infobox: contentlet.infobox ? JSON.parse(contentlet.infobox) : null,
    identifier: contentlet.identifier,
    tags: contentlet.tags,
    shapefile: contentlet?.shapefile,
    webmap: contentlet?.webmap,
  };

  transformData.urlParams = params;
  transformData.raw = params.raw;

  const returnData = transformArticle(transformData, searchSnippet, searchTerm);

  returnData.type = contentlet.contentType;
  if (section === 'VOA')
    returnData.videoinfo = contentlet.videoinfo
      ? JSON.parse(contentlet.videoinfo)
      : null;
  if (params.raw) return rawArticle(returnData);
  return returnData;
};

/**
 * Retrieve a specific article from DotCMS
 */
export const getArticle = (params, searchSnippet, searchTerm) => {
  if (params.article && isIdentifier(params.article)) {
    return getContentById(params.article, params.authdViewLive).then(
      (contentlet) => {
        return handleArticleTransform(
          contentlet,
          params,
          searchSnippet,
          searchTerm
        );
      }
    );
  } else {
    return getContentByName(params).then((contentlet) => {
      return handleArticleTransform(
        contentlet,
        params,
        searchSnippet,
        searchTerm
      );
    });
  }
};

export const getHowToCategoryVar = (catName) => {
  return axios.get(`${BASE_URL}dotcms/api/categories?parent=howto`).then((res) => {
    const { data: categories } = res;
    const [targetCat] = categories.filter((cat) => cat.name === catName);
    return targetCat.var;
  });
};

export const getSiteIdentifier = (ctc) => {
  return axios.get(`${BASE_URL}dotcms/api/sites/identifiers`).then((res) => {
    if (ctc) {
      return res.data.find((e) => e.name === 'ctc').identifier;
    } else {
      return res.data.find((e) => e.name === 'default').identifier;
    }
  }).catch(() => {});
};

export const getChildCategories = (parent) => {
  return axios
    .get(`${BASE_URL}dotcms/api/categories`, { params: { parent } })
    .then((res) => res.data);
};

export const getSummary = (sectionCat) => {
  return axios
    .post(`${BASE_URL}dotcms/api/content/_search/`, {
      query: `+categories:${sectionCat}`,
    })
    .then((res) => {
      const { entity } = res.data;
      return entity.jsonObjectView.contentlets[0];
    });
};

export const getResourceButtons = () => {
  return axios
    .post(`${BASE_URL}dotcms/api/content/_search/`, {
      query: `+contentType:ImageButton +categories:resource-button +live:true`,
    })
    .then((res) => {
      const { entity } = res.data;
      entity.jsonObjectView.contentlets.sort(
        (a, b) => a.sortindex - b.sortindex
      );
      return entity.jsonObjectView.contentlets;
    });
};

export const getOEEBanner = () => {
  return axios
    .post(`${BASE_URL}dotcms/api/content/_search/`, {
      query: `+contentType:ImageButton +categories:oe-banner +live:true`,
    })
    .then((res) => {
      const { entity } = res.data;
      return entity.jsonObjectView.contentlets[0];
    });
};
