import { setUrl } from 'Utils/Router';

const DATE_CONTENT_TYPES = ['DateArticle', 'DateOE', 'DateRegion'];
const TP_CONTENT_TYPES = ['TPArticle', 'TPChapter'];
const VOA_CONTENT_TYPES = ['VOAArticle'];
const HOWTO_CONTENT_TYPES = ['HowTo'];
const WEG_CONTENT_TYPES = ['WegCard'];

const getSectionFromContentType = (contentType) => {
  if (DATE_CONTENT_TYPES.includes(contentType)) return 'DATE';
  if (TP_CONTENT_TYPES.includes(contentType)) return 'TC';
  if (VOA_CONTENT_TYPES.includes(contentType)) return 'VOA';
  if (HOWTO_CONTENT_TYPES.includes(contentType)) return 'How-To';
  if (WEG_CONTENT_TYPES.includes(contentType)) return 'WEG';
  return null;
};

export const getPathFromIdentifierAndContentType = ({
  identifier,
  contentType,
}) => {
  const section = getSectionFromContentType(contentType);
  return `/${section === 'WEG' ? 'WEG/Asset' : section}/${identifier}`;
};

export const getHowToSectionFromCategories = (categories) => {
  const howToSections = ['WEG', 'DATE'];
  let section = '';
  if (categories) {
    categories.forEach((category) => {
      const name = Object.values(category)[0];
      if (howToSections.includes(name)) {
        section = name;
      }
    });
  }
  return section;
};

export const getSearchResultPath = (searchSection, result) => {
  const { contentType } = result;
  let section = searchSection;
  if (
    section === 'All' ||
    section === 'TC|DATE|VOA|WEG_Resource' ||
    section === 'Region' ||
    section === 'WEG_Resource'
  ) {
    section = getSectionFromContentType(contentType);
  }

  let params = {};
  switch (section) {
    case 'DATE':
      params = {
        org: null,
        article: result.title,
      };
      break;
    case 'WEG':
      params = {
        type: 'Asset',
        page: result.identifier,
      };
      break;
    case 'TC':
      params = {
        tc: null,
        article: result.title,
      };
      break;
    case 'VOA':
      params = {
        article: result.title,
      };
      break;
    case 'How-To':
      params = {
        section: getHowToSectionFromCategories(result.categories),
        article: result.title,
      };
      break;
    default:
      break;
  }
  return setUrl(section, params);
};
