import { API_URL, getChildCategories, getSiteIdentifier, getSubnav as getSubnavFromDotCMS } from 'API';
import axios from 'axios';
import { openDB } from 'idb';
import { initializeSearchIndex } from './fuse';
import { getOnlineStatus } from './networkStatus';
import { flattenSubnav } from './util';
// import { requestPersistentStorage } from './fileSystem';

export const DB_VERSION = 3;
const BASE_URL = import.meta.env.VITE_ODIN_API_BASE_URL;
export const IMAGE_STORE = 'images';
export const DB_NAME = 'WEG';
const ASSET_STORE_NAME = 'assets';
const SUBNAV_STORE_NAME = 'subnav';
const COUNTRIES_STORE_NAME = 'countries';
const IMAGE_BUTTON_STORE_NAME = 'imageButton';
const MISC_STORE_NAME = 'misc';

export const initDB = async () => {
  console.log('initDB!!');
  return openDB(DB_NAME, DB_VERSION, {
    upgrade(db) {
      console.log('initDB openDB upgrade');
      console.dir(db.objectStoreNames);
      if (!db.objectStoreNames.contains(ASSET_STORE_NAME)) {
        db.createObjectStore(ASSET_STORE_NAME, { keyPath: 'identifier' });
      }
      if (!db.objectStoreNames.contains(SUBNAV_STORE_NAME)) {
        db.createObjectStore(SUBNAV_STORE_NAME, { keyPath: 'identifier' });
      }
      if (!db.objectStoreNames.contains(IMAGE_BUTTON_STORE_NAME)) {
        db.createObjectStore(IMAGE_BUTTON_STORE_NAME, {
          keyPath: 'identifier',
        });
      }
      if (!db.objectStoreNames.contains(COUNTRIES_STORE_NAME)) {
        db.createObjectStore(COUNTRIES_STORE_NAME, {
          keyPath: 'var',
        });
      }
      if (!db.objectStoreNames.contains(IMAGE_STORE)) {
        db.createObjectStore(IMAGE_STORE, { keyPath: 'url' });
      }
      if (!db.objectStoreNames.contains(MISC_STORE_NAME)) {
        db.createObjectStore(MISC_STORE_NAME, { keyPath: 'key' });
      }
    },
  });
};

export const initDBFuse = async () => {
  await initDB();
  console.log('initDB finished.');
  await initializeSearchIndex();
  console.log('initializeSearchIndex finished.');
};

export const initLocalDB = async (setTotalAssets, incrementAssetsCompleted, abortSignal, maxSize = Infinity) => {
  const addMessage = () => {};
  const addImportantMessage = () => {};

  const isOnline = getOnlineStatus();
  await initDB();
  if (isOnline) {
    addMessage("Beginning IndexedDB initialization.")
    await Promise.all([
      fetchAndStoreWegSubnav(addMessage, addImportantMessage, abortSignal),
      fetchAndStoreCountries(addMessage, addImportantMessage, abortSignal),
      fetchAndStoreAllWegCards(addMessage, addImportantMessage, setTotalAssets, incrementAssetsCompleted, 100, maxSize, abortSignal),
      fetchAndStoreResourceButtons(addMessage, addImportantMessage, abortSignal),
    ]);
  } else {
    addImportantMessage("Unable to initialize IndexedDB: Could not connect to dotCMS.")
  }
  await initializeSearchIndex();
};

const fetchAndStoreCountries = async (addMessage, addImportantMessage, abortSignal) => {
  try {
    const response = await getChildCategories('origin');
    addMessage("Retrieved sublinks.")
    await addCountries(response);
  } catch (error) {
    addImportantMessage(`Error fetching resource buttons: ${error}`, "error");
  }
};

const fetchAndStoreResourceButtons = async (addMessage, addImportantMessage, abortSignal) => {
  try {
    const response = await axios.post(`${BASE_URL}dotcms/api/content/_search/`, {
      query: `+contentType:ImageButton +categories:resource-button +live:true`,
    });
    const { entity } = response.data;
    const buttons = entity.jsonObjectView.contentlets;
    addMessage("Retrieved Resource Buttons.")
    await addImageButtons(buttons);
  } catch (error) {
    addImportantMessage(`Error fetching resource buttons: ${error}`, "error");
  }
};

const fetchAndStoreWegSubnav = async (addMessage, addImportantMessage, abortSignal) => {
  try {
    const siteId = await getSiteIdentifier(false);
    const subNav = await getSubnavFromDotCMS(siteId);
    await addSubnav(subNav, 'wegSubnav');
    addMessage("Retrieved WEG Subnav.")
    const flatSubnav = flattenSubnav(subNav.topCats);
    await addSubnav(flatSubnav, 'flatWegSubnav');
    addMessage("Stored WEG Subnav.")
  } catch (error) {
    addImportantMessage(`Error fetching WEG Subnav: ${error}`, "error");
  }
};

const fetchAndStoreAllWegCards = async (
  addMessage,
  addImportantMessage,
  setTotalAssets,
  incrementAssetsCompleted,
  batchSize = 100,
  maxSize = Infinity,
  abortSignal
) => {
  try {
    const startTime = Date.now();
    const siteId = await getSiteIdentifier(false);
    const total = await getTotalWegCards();
    addMessage(`Will retrieve ${total} WEG Assets.`)
    setTotalAssets(total);
    const totalBatches = Math.ceil(Math.min(total, maxSize) / batchSize);

    for (let batch = 0; batch < totalBatches; batch++) {
      if (abortSignal.aborted)
        continue;
      const offset = batch * batchSize;
      const assets = await fetchWegCardsBatch(siteId, offset, batchSize, abortSignal);
      await addAssets(assets, incrementAssetsCompleted);

      addMessage(`Fetched and stored batch ${batch + 1} of ${totalBatches}`);
    }
    addImportantMessage('All WEG assets have been fetched and stored.', 'success');
    addMessage('(optional) Click "Import Images" to download their associated WEG images.');
    addMisc('lastAssetImport', startTime);
  } catch (error) {
    if (!abortSignal.aborted)
      addImportantMessage(`Error fetching and storing WegCards: ${error.message}`, "error");
  }
};

export const addCountries = async (countries) => {
  const db = await initDB();
  const tx = db.transaction(COUNTRIES_STORE_NAME, 'readwrite');
  const store = tx.objectStore(COUNTRIES_STORE_NAME);
  countries.forEach((country) => {
    store.put(country);
  });
  await tx.done;
};

export const addAssets = async (assets, incrementAssetsCompleted) => {
  const db = await initDB();
  const tx = db.transaction(ASSET_STORE_NAME, 'readwrite');
  const store = tx.objectStore(ASSET_STORE_NAME);
  assets.forEach((asset) => {
    store.put(asset);
    incrementAssetsCompleted()
  });
  await tx.done;
};

export const addImageButtons = async (assets) => {
  const db = await initDB();
  const tx = db.transaction(IMAGE_BUTTON_STORE_NAME, 'readwrite');
  const store = tx.objectStore(IMAGE_BUTTON_STORE_NAME);
  assets.forEach((asset) => {
    store.put(asset);
  });
  await tx.done;
};

