export const addImage = async (url, imageBlob) => {
  try {
    const db = await initDB();
    await db.put(IMAGE_STORE, { url, imageBlob });
    return true;
  } catch (error) {
    if (error.name === 'QuotaExceededError') {
      console.error('Storage quota exceeded.');
      return false;
      // TODO: Handle quota exceeded
    } else {
      throw error;
    }
  }
};

export const addMisc = async (key, value) => {
  try {
    const db = await initDB();
    await db.put(MISC_STORE_NAME, {"key": key, "value": value});
    return true;
  } catch (error) {
    if (error.name === 'QuotaExceededError') {
      console.error('Storage quota exceeded.');
      return false;
      // TODO: Handle quota exceeded
    } else {
      throw error;
    }
  }
};

