export const resetLocalDB = async (incrementAssetsCompleted) => {
  const addMessage = () => {}
  await clearAssets();
  incrementAssetsCompleted();
  addMessage("Cleared all WEG assets.")
  await clearSubnav();
  addMessage("Cleared WEG subnav.")
  incrementAssetsCompleted();
  await clearResourceButtons();
  addMessage("Cleared WEG resource buttons.")
  incrementAssetsCompleted();
  await clearCountries();
  addMessage("Cleared all sublinks.")
  incrementAssetsCompleted();
  await clearImages();
  addMessage("Cleared all WEG asset images.")
  incrementAssetsCompleted();
  await clearMisc();
  addMessage("Cleared miscellaneous storage.")
  addMessage('All local data has been deleted.', 'success');
  incrementAssetsCompleted();
};
