window.global = window;

import axios from 'axios';
import React, { useEffect, useState } from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import { CookiesProvider } from 'react-cookie';
import { getSiteIdentifier } from 'API';
import Download from 'Containers/Download';
import Error from 'Containers/Error';
import Privacy from 'Containers/Privacy';
import SearchResults from 'Containers/SearchResults';
import TermsOfUse from 'Containers/TermsOfUse';
import WEG from 'Containers/WEG';
import { AuthdViewLiveProvider, UpdateProvider } from 'Context';
import { initDBFuse } from 'dataAccess';
import './index.css';
import { usePWAStatus } from './context';

const BASE_URL = import.meta.env.VITE_ODIN_API_BASE_URL;
const SEARCH_URL = `${BASE_URL}dotcms/api/content/_search`;
const ES_SEARCH_URL = `${BASE_URL}dotcms/api/es/search`;

const App = () => {
  const [ siteIdSet, setSiteIdSet ] = useState( false );
  const [ ready, setReady ] = useState(false);
  const { getPWADisplayMode } = usePWAStatus();

  useEffect(() => {
    console.dir(import.meta.env);
    const initializeApp = async () => {
      try {
        await initDBFuse();
        setReady(true);
      } catch (error) {
        console.error('Failed to initialize DB and Fuse:', error);
        setReady(true);
      }
    };

    if ('serviceWorker' in navigator) {
      navigator.serviceWorker.ready.then((registration) => {
        if (registration.active) {
          console.log('serviceWorker: App is running as a PWA!');
        } else {
          console.log('serviceWorker: App is NOT running as a PWA!');
        }
      });
    }

    const manifestLink = document.querySelector('link[rel="manifest"]');
    if (manifestLink) {
      console.log('App is running as a PWA');
    } else {
      console.log('App is not running as a PWA');
    }
    console.dir(manifestLink);

    initializeApp();

    window.addEventListener('DOMContentLoaded', () => {
      // Replace "standalone" with the display mode used in your manifest
      window.matchMedia('(display-mode: standalone)')
        .addEventListener('change', () => {
          // Log display mode change to analytics
          console.log('DISPLAY_MODE_CHANGED', getPWADisplayMode());
        });
    });

    getSiteIdentifier(false).then((site) => {
      axios.interceptors.request.use((config) => {
        if (config.url.includes(SEARCH_URL)) {
          config.data.query += ` +conhost:${site}`;
        }
        if (config.url.includes(ES_SEARCH_URL)) {
          config.data.query.query_string.query += ` +conhost:${site}`;
        }
        return config;
      });
      setSiteIdSet( true );
    }).catch((error) => {
      console.error('Failed to get site identifier:', error);
      setSiteIdSet(true);
    });
    // if ('serviceWorker' in navigator) {
    //   window.addEventListener('load', () => {
    //     const swPath = `${import.meta.env.BASE_URL}sw.js`;
    //     navigator.serviceWorker
    //       .register(swPath)
    //       .then((registration) => {
    //         console.log('SW registered: ', registration);
    //       })
    //       .catch((registrationError) => {
    //         console.log('SW registration failed: ', registrationError);
    //       });
    //   });
    // }

  }, []);

  return (
    <CookiesProvider>
      <UpdateProvider>
        <AuthdViewLiveProvider>
          <BrowserRouter basename={import.meta.env.BASE_URL}>
            {siteIdSet && ready ? (
              <Switch>
                <Route path="/" exact component={WEG} />
                <Route path="/WEG/:type?/:page?" component={WEG} />
                <Route
                  path="/Search/:section?/:searchString?/:filter?"
                  component={SearchResults}
                />
                <Route path="/Download" component={Download} />
                <Route path="/Privacy" component={Privacy} />
                <Route path="/Terms_Of_Use" component={TermsOfUse} />
                <Route path="/Error" component={Error} />
                <Route component={Error} />
              </Switch>
            ) : null}
          </BrowserRouter>
        </AuthdViewLiveProvider>
      </UpdateProvider>
    </CookiesProvider>
  );
}

ReactDOM.render(<App />, document.getElementById('root'));
