// Dependencies
import React from 'react';
import PropTypes from 'prop-types';

/**
 * Convert the content into a button that opens a modal
 */
const modalTrigger = (props) => {
  const { children, open } = props;
  return (
    <button type="button" className="modal-button" onClick={open}>
      {children}
    </button>
  );
};

// Define data types for the properties passed to the component
modalTrigger.propTypes = {
  /**
   * The content to render inside the button
   * Defined as the elements inside the <ModalTrigger> open and closing tags.
   */
  children: PropTypes.node.isRequired,
  /** Method to open and render the modal */
  open: PropTypes.func.isRequired,
};

// Set default values for optional properties
modalTrigger.defaultProps = {};

export default modalTrigger;
