import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import { getUID } from 'Utils/UID';

import packageJson from './../../../package.json';
import './Footer.css';

const footer = (props) => {
  const { section, results, lastModified, refresh } = props;
  let leftColContents = null;

  const handleOpenInstructions = () => {
    console.log('handleOpenInstructions called');
    const event = new CustomEvent('openInstallModal');
    window.dispatchEvent(event);
  };

  const centerLinks = [
    { title: 'Privacy', link: '/Privacy' },
    { title: 'Terms of Use', link: '/Terms_Of_Use' },
    { title: 'Home', link: '/WEG' },
    // {
    //   title: 'Accessibility/Section 508',
    //   link: 'https://dodcio.defense.gov/DoDSection508/Std_Stmt.aspx',
    // },
    // { title: 'FOIA', link: 'https://www.rmda.army.mil/foia/RMDA-FOIA-Division.html' },
    // { title: 'No Fear Act', link: 'https://www.opm.gov/equal-employment-opportunity/no-fear-act/' },
  ];

  // additional elements based on the current section
  switch (section) {
    // WEG and Search Results should display the number of results returned
    case 'WEG':
    case 'Search':
      if (results) {
        leftColContents = <p>{`${results} Results`}</p>;
      }
      break;

    default:
      break;
  }

  return (
    <footer>
      {
        <div
          className={`left-col${leftColContents ? ' left-col-populated' : ''}`}
        >
          {leftColContents}
        </div>
      }
      <div className="center-footer-links">
        <p className="btn footer-link instructions-link" onClick={handleOpenInstructions}>
          WEG Application
        </p>
        <div className="seperator vertical instructions-link" />
        {centerLinks.map((item, index) => (
          <Fragment key={getUID()}>
            {index ? <div className="seperator vertical" /> : null}
            {item.link.indexOf('http') > -1 ? (
              <a
                className='btn footer-link'
                href={item.link}
                rel="noopener noreferrer"
                target="_blank"
                onClick={item.onClick}
              >
                {item.title}
              </a>
            ) : (
              <Link
                className="btn footer-link noindex"
                onClick={item.onClick}
                to={item.link}
              >
                {item.title}
              </Link>
            )}
          </Fragment>
        ))}
      </div>
      <div className="right-col">
        <p className="version">v{packageJson.version}</p>
      </div>
    </footer>
  );
};

// Define the data type for each of property passed to the component
footer.propTypes = {
  /** The section currently being displayed */
  section: PropTypes.string,
  /** The number of results being displayed */
  results: PropTypes.number,
  /** The date the data was last modified */
  lastModified: PropTypes.instanceOf(Date),
  /** Actions to take when the refresh button is selected */
  refresh: PropTypes.func,
};

footer.defaultProps = {
  section: '',
  results: 0,
  lastModified: new Date(),
  refresh: null,
};

export default footer;
