const fetchWegCardsFromNetwork = async (offset, limit) => {
  const response = await axios.post(`${API_URL}_search`, {
    limit,
    offset,
    query: '+contentType:WegCard +live:true +deleted:false',
    sort: 'score',
  });

  const { entity } = response.data;
  return entity.jsonObjectView.contentlets;
};

export const getWegCardById = async (id) => {
  const online = await getOnlineStatus().then((online) => online);

  if (online) {
    const data = await fetchWegCardByIdFromNetwork(id);

    await addAssets([data]);

    return data;
  } else {
    const data = await getAssetById(id);
    return data;
  }
};

const fetchWegCardByIdFromNetwork = async (id) => {
  const response = await axios.post(`${API_URL}_search`, {
    query: `+identifier:"${id}" +live:true +deleted:false`,
    limit: 1,
  });

  const { entity } = response.data;
  return entity.jsonObjectView.contentlets[0];
};

export const searchWegCards = async (searchString, offset = 0, limit = 10) => {
  if (!getFuse()) {
    await initializeSearchIndex();
  }

  const fuse = getFuse();

  const results = fuse.search(searchString);

  const contentlets = results
    .slice(offset, offset + limit)
    .map((result) => result.item);

  return {
    contentlets,
    totalHits: results.length,
  };
};

// Periodically sync data? (would need to only updated modified cards/images/subnav)
export const synchronizeData = async () => {
  const online = await getOnlineStatus().then((online) => online);

  if (online) {
    let offset = 0;
    const limit = 100;
    let totalFetched = 0;

    do {
      const data = await fetchWegCardsFromNetwork(offset, limit);
      if (data.length === 0) break;

      await addAssets(data);

      totalFetched += data.length;
      offset += limit;
    } while (true);

    console.log(`Synchronized ${totalFetched} WegCards.`);
  }
};

