export const getSubnav = async (identifier="wegSubnav") => {
  const online = await getOnlineStatus().then((online) => online);
  console.info('dataAccess getSubnav, online:', online);

  if (online) {
    return await getSubnavFromDotCMS();
  } else {
    const subnav = await getSubnavLocal(identifier);
    console.info(`dataAccess getSubnav, ${identifier}:`, subnav);
    return await getSubnavLocal(identifier);
  }
}

