export const getAssetByInode = async (identifier) => {
  const online = await getOnlineStatus().then((online) => online);
  console.info('dataAccess getAssetByInode, online:', online);
  console.info('dataAccess getAssetByInode, identifier:', identifier);

  if (online) {
    return await getAssetByInodeFromDotCMS(identifier, '','identifier', false);
  } else {
    return await getAssetById(identifier).then(asset => transformAsset({contentlets: [asset]}));;
  }
}
