// Dependencies
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import CustomScrollbars from 'react-custom-scrollbars-2';

// Utils
import { getUID } from 'Utils/UID';

// UI
import Icon from 'UI/Icon';
import Button from 'UI/Button';

// Styles
import './Dropdown.css';

import '../../../node_modules/flag-icon-css/css/flag-icon.css';

/**
 * Custom dropdown element
 */
class Dropdown extends Component {
  ENUM_SELECTED_TYPE = {
    COUNTRY: 'Country',
    PROLIFERATION_COUNTRY: 'Proliferation Country',
    DOMAIN: 'Domain',
    CATEGORY: 'Category',
  };

  constructor(props) {
    super(props);
    this.wrapperRef = React.createRef();
    this.handleClickOutside = this.handleClickOutside.bind(this);
    this.selectedValue = null;
  }

  state = {
    showContent: false,
  };

  componentWillMount() {
    document.addEventListener('mousedown', this.handleClickOutside);
  }

  componentWillUnmount() {
    document.removeEventListener('mousedown', this.handleClickOutside);
  }

  handleToggleContent = () => {
    const { showContent } = this.state;
    this.setState({ showContent: !showContent });
  };

  handleSelection = (selected) => {
    const { setSelection } = this.props;
    this.handleToggleContent();
    // determine the the type
    const type = this.determineType(selected);
    setSelection({ selected, type });
  };

  handleClearSelection = (event) => {
    const { removeSelection } = this.props;
    event.stopPropagation();
    removeSelection();
  };

  determineType = (selected) => {
    if (selected.icon && !selected.icon.startsWith('/'))
      return this.ENUM_SELECTED_TYPE.DOMAIN;
    if (selected.icon && selected.icon.startsWith('/'))
      return this.ENUM_SELECTED_TYPE.CATEGORY;
    if (selected.iso_code) return this.ENUM_SELECTED_TYPE.COUNTRY;
    if (selected.countryName)
      return this.ENUM_SELECTED_TYPE.PROLIFERATION_COUNTRY;
    return null;
  };

  handleClickOutside(event) {
    if (this.wrapperRef && !this.wrapperRef.current.contains(event.target)) {
      this.setState({ showContent: false });
    }
  }

  render() {
    const {
      className,
      hideTitle,
      hideTooltip,
      icon,
      title,
      selectionItems,
      selectedData,
      useDropdownIcons,
    } = this.props;

    const { showContent } = this.state;

    let { tooltip } = this.props;
    if (!tooltip) tooltip = title;

    const renderSelectionItems = selectionItems
      ? selectionItems.map((selection) => (
          <div
            role="dialog"
            className={
              selectedData && selection === selectedData.selected
                ? 'selection-selected'
                : 'selection'
            }
            key={selection.id || selection.countryID || getUID(selection.name)}
            onClick={() => this.handleSelection(selection)}
            onKeyDown={null}
          >
            {selection.iso_code && !selection.iso_code.startsWith('_') && (
              <span className={`flag-icon flag-icon-${selection.iso_code}`} />
            )}
            {selection.iso_code && selection.iso_code.startsWith('_') && (
              <Icon file={selection.name.replace(/ /g, '_')} />
            )}
            {useDropdownIcons &&
              selection.icon &&
              !selection.icon.startsWith('/') && <Icon file={selection.icon} />}
            {selection.icon && selection.icon.startsWith('/') && (
              <img src={selection.icon} alt={selection.name} />
            )}
            {selection.proliferationCountryISO && (
              <span
                className={`flag-icon flag-icon-${selection.proliferationCountryISO}`}
              />
            )}
            <div className="cat-name">
              {selection.name || selection.countryName}
            </div>
          </div>
        ))
      : null;

    const renderSelected = selectedData ? (
      <div className="select">
        {selectedData.type === this.ENUM_SELECTED_TYPE.COUNTRY &&
          !selectedData.selected.iso_code.startsWith('_') && (
            <span
              className={`flag-icon flag-icon-${selectedData.selected.iso_code}`}
            />
          )}
        {selectedData.type === this.ENUM_SELECTED_TYPE.COUNTRY &&
          selectedData.selected.iso_code.startsWith('_') && (
            <Icon file={selectedData.selected.name.replace(/ /g, '_')} />
          )}
        {selectedData.type ===
          this.ENUM_SELECTED_TYPE.PROLIFERATION_COUNTRY && (
          <div className="name">{selectedData.selected.countryName}</div>
        )}
        {selectedData.type === this.ENUM_SELECTED_TYPE.DOMAIN &&
          useDropdownIcons && <Icon file={selectedData.selected.icon} />}
        {selectedData.type === this.ENUM_SELECTED_TYPE.CATEGORY &&
          selectedData.selected &&
          useDropdownIcons && <Icon file={selectedData.selected.icon} />}
        <div className="name">{selectedData.selected.name}</div>
        <div
          role="dialog"
          className="close"
          onClick={this.handleClearSelection}
          onKeyDown={null}
        >
          <Icon file="close" />
        </div>
      </div>
    ) : (
      <></>
    );

    return (
      <div
        className={
          selectedData
            ? `dropdown-container ${className}-selected`
            : `dropdown-container ${className}`
        }
        ref={this.wrapperRef}
      >
        <Button
          className="drop-button"
          title={!selectedData ? title : ''}
          click={this.handleToggleContent}
          hideTitle={hideTitle}
          tooltip={tooltip}
          hideTooltip={hideTooltip}
          icon={icon}
        >
          {!selectedData && <Icon className="rotate45" file="arrow" />}
          {selectedData && (
            <>
              <div className="arrow-right" />
              {renderSelected}
            </>
          )}
        </Button>
        {showContent && (
          <div className="dropdown-content">
            <CustomScrollbars
              style={{ width: 'max-content' }}
              className="customScrollbars"
              autoHeight
              autoHeightMax="75vh"
            >
              {renderSelectionItems}
            </CustomScrollbars>
          </div>
        )}
      </div>
    );
  }
}

Dropdown.propTypes = {
  setSelection: PropTypes.func,
  removeSelection: PropTypes.func,
  className: PropTypes.string,
  hideTitle: PropTypes.bool,
  hideTooltip: PropTypes.bool,
  icon: PropTypes.string,
  title: PropTypes.string,
  selectionItems: PropTypes.array,
  selectedData: PropTypes.object,
  tooltip: PropTypes.string,
  useDropdownIcons: PropTypes.bool,
};

Dropdown.defaultProps = {
  setSelection: () => {},
  removeSelection: () => {},
  className: '',
  hideTitle: false,
  hideTooltip: false,
  icon: '',
  title: '',
  selectionItems: [],
  selectedData: {},
  tooltip: '',
  useDropdownIcons: false,
};

export default Dropdown;
