// Dependencies
import React from 'react';
import { withRouter } from 'react-router-dom';
import RouterPropTypes from 'react-router-prop-types';

// Utils

// UI
import Layout from 'UI/Layout';
import Button from 'UI/Button';

// Components

// Styles
import './Error.css';

/**
 * Fetch, format, and display a Mediawiki Error
 */
const error = (props) => {
  const { history } = props;
  return (
    <Layout>
      <div className="error-page">
        <h2>This Page is Under Construction</h2>
        <Button title="Go to Previous Page" click={() => history.goBack()} />
        <Button title="Return to Home Page" click={() => history.push('/')} />
      </div>
    </Layout>
  );
};

// Define the data type for each property passed to the component
error.propTypes = {
  /**
   * Library that lets you easily manage session history anywhere JavaScript runs
   * Provided by react-router-dom and the withRouter metho
   */
  history: RouterPropTypes.history.isRequired,
};

// Set default values for optional properties
error.defaultProps = {};

export default withRouter(error);
