import ImportModal from 'Components/ImportModal';
import throttle from 'lodash.throttle';
import React, { useEffect, useRef, useState } from 'react';
import { isChrome, isMobileDevice } from 'dataAccess';
import { usePWAStatus } from 'Context';

// Ideas:
// - Cancel Import button
// - Clear IndexedDB Button
// - Persistant Storage button/checkbox (defaults to yes)

function ImportManager({ assetsCompleted, setAssetsCompleted, totalAssets, setTotalAssets, showImportModal, showClearModal, close, isFullScreen }) {
  const [messages, setMessages] = useState([]);
  const [importantMessages, setImportantMessages] = useState([]);
  const messageQueue = useRef([]);
  const [importing, setImporting] = useState(false);
  const [startTime, setStartTime] = useState(null);
  const [estimatedTimeRemaining, setEstimatedTimeRemaining] = useState(null);
  const [mangerMode, setManagerMode] = useState(null);
  const { isPWA, isInstalled, checkPWAStatus } = usePWAStatus();

  const setGreetingMessages = () => {
    setImportantMessages([]);
    setMessages([]);
    if (!isChrome()) {
      addImportantMessage('For the best experience, use the ODIN WEG in Chrome as a Progressive Web Application.', 'warn');
    }
    addMessage('Click "Import Assets" to begin.');
  }

  useEffect(() => {
    if (showImportModal) {
      setManagerMode('import');
    } else if (showClearModal) {
      setManagerMode('clear');
    } else {
      setManagerMode(null);
    }
  }, [showImportModal, showClearModal]);


  useEffect(() => {


    setGreetingMessages()

    const mediaQuery = window.matchMedia('(display-mode: standalone)');

    const handleChange = () => {
      checkPWAStatus().then(() => isPWA && setGreetingMessages());
      // const isApp = mediaQuery.matches || window.navigator.standalone === true;

    };

    mediaQuery.addEventListener('change', handleChange);

    return () => {
      mediaQuery.removeEventListener('change', handleChange);
    };
  }, [])

  const flushMessages = useRef(
    throttle(() => {
      if (messageQueue.current.length === 0) {
        return;
      }

      setMessages((prevMessages) => {
        const newMessages = [...prevMessages, ...messageQueue.current];
        messageQueue.current = [];
        if (newMessages.length > 999) {
          return newMessages.slice(newMessages.length - 999);
        }
        return newMessages;
      });
    }, 250)
  ).current;

  const addMessage = (text, type = 'info', immediate = false) => {
    if (immediate) {
      setMessages((prevMessages) => [...prevMessages, { text, type }]);
    } else {
      messageQueue.current.push({ text, type });
      flushMessages();
    }
  };

  const addImportantMessage = (text, type) => {
    addMessage(text, type);
    setImportantMessages((prevMessages) => [...prevMessages, { text, type }]);
  }

  useEffect(() => {
    return () => {
      flushMessages.cancel();
    };
  }, [flushMessages]);

  useEffect(() => {
    console.log("assetsCompleted: ", assetsCompleted);
  }, [assetsCompleted]);

  return (
    <div>
      {mangerMode && (
        <ImportModal
        mangerMode={mangerMode}
        close={close}
        isFullScreen={isFullScreen}
        messages={messages}
        addMessage={addMessage}
        importantMessages={importantMessages}
        addImportantMessage={addImportantMessage}
        flushMessages={flushMessages}
        assetsCompleted={assetsCompleted}
        setAssetsCompleted={setAssetsCompleted}
        totalAssets={totalAssets}
        setTotalAssets={setTotalAssets}
        importing={importing}
        setImporting={setImporting}
        startTime={startTime}
        setStartTime={setStartTime}
        estimatedTimeRemaining={estimatedTimeRemaining}
        setEstimatedTimeRemaining={setEstimatedTimeRemaining}
      />
      )}
    </div>
  );
}

export default ImportManager;
