// Dependencies
import React, { PureComponent } from 'react';
import SVG from 'react-inlinesvg';
import PropTypes from 'prop-types';

// Styles
import './Icon.css';

/**
 * Dynamically loads an inline `svg` icon
 */
class Icon extends PureComponent {
  state = {
    iconFile: null,
  };

  isMounted = false;

  componentDidMount() {
    this.getIcon();
    this.isMounted = true;
  }

  componentDidUpdate(prevProps) {
    const { file: newFile } = this.props;
    const { file: oldFile } = prevProps;
    if (newFile !== oldFile) {
      this.getIcon();
    }
  }

  componentWillUnmount() {
    this.setState({ iconFile: null });
    this.isMounted = false;
  }

  getIcon = () => {
    const { file } = this.props;
    const iconFiles = import.meta.glob('/src/ui/Icon/icons/*', { eager: true });
    const svgPath = `/src/ui/Icon/icons/${file}.svg`;
    if (iconFiles[svgPath]) {
      this.setState({ iconFile: iconFiles[svgPath].default });
    } else {
      const pngPath = `/src/ui/Icon/icons/${file}.png`;
      if (iconFiles[pngPath]) {
        this.setState({ iconFile: iconFiles[pngPath].default });
      }
    }
  };

  render() {
    const { iconFile } = this.state;
    const { className, style } = this.props;
    if (
      iconFile &&
      (iconFile.endsWith('.png') || iconFile.startsWith('data:image/png'))
    ) {
      return (
        <div className={`icon ${className}`} style={style}>
          <img src={iconFile} alt={className} />
        </div>
      );
    }
    return (
      <div className={`icon ${className}`} style={style}>
        {iconFile && <SVG cacheRequests src={iconFile} />}
      </div>
    );
  }
}

// Define the data type for each property passed to the component
Icon.propTypes = {
  /** The icon to render */
  file: PropTypes.string.isRequired,
  /** Additional classes to apply to the icon */
  className: PropTypes.string,
  /** Inline styles to apply to the icon wrapper */
  style: PropTypes.object,
};

// Set default values for optional properties
Icon.defaultProps = {
  className: '',
  style: {},
};

export default Icon;
