import React, { Component, Fragment, createContext } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import RouterPropTypes from 'react-router-prop-types';
import { withCookies, Cookies } from 'react-cookie';
import RandomBG from 'Utils/RandomBG';
import { getSection } from 'Utils/Router';
import Footer from 'UI/Footer';
import Button from 'UI/Button';
import DisclaimerModal from 'Components/DisclaimerModal';
import MessageBanner from 'Components/MessageBanner';
import UpdateNotification from 'Components/UpdateNotification';
import './Layout.css';

export const FullScreenContext = createContext();

/**
 * Main layout wrapper with header, footer, and navigation
 */
class Layout extends Component {
  /* eslint-disable */
  state = {
    bgImage: {},
    showAlert: true,
    browserAlert: true,
    governmentAlert: true,
    showMessageBanner: false,
    deploymentText: '',
  };
  /* eslint-enable */

  componentDidMount() {
    this.updateBg();

    const isIE = false || !!document.documentMode;
    const isEdge = !isIE && !!window.StyleMedia;

    const { cookies } = this.props;
    const showAlert = !cookies.get('accept-cookies');
    let browserAlert = isIE || isEdge;
    if (browserAlert) browserAlert = !cookies.get('browser-alert');
    const governmentAlert = !cookies.get('acknowledgeGovernment');
    this.setState({ showAlert, browserAlert, governmentAlert });
  }

  componentDidUpdate(prevProps) {
    const { location: newRoute } = this.props;
    const { location: oldRoute } = prevProps;
    const newSection = getSection(newRoute);
    const oldSection = getSection(oldRoute);
    if (newSection !== oldSection) {
      this.updateBg();
    }
  }

  /**
   * Update the background image when the section changes
   */
  updateBg = () => {
    const { location, history } = this.props;
    const section = getSection(location);
    const VALID_SECTIONS = [
      'WEG',
      'DATE',
      'TC',
      'FS',
      'VOA',
      'Search',
      'How-To',
      'Error',
      'FS-Search',
      'Privacy',
      'Terms Of Use',
      'Download',
      'DATEWORLD',
      'Region',
      'ExternalLinks',
    ];
    if (VALID_SECTIONS.indexOf(section) < 0 && location.pathname !== '/') {
      history.replace('/Error');
    }
    const bgImage = RandomBG();
    this.setState({ bgImage });
  };

  /**
   * Accept the use of the cookies
   */
  acceptCookies = () => {
    this.setState({
      showAlert: false,
    });
    const { cookies } = this.props;
    cookies.set('accept-cookies', true);
  };

  browserAlert = () => {
    this.setState({
      browserAlert: false,
    });
    const { cookies } = this.props;
    cookies.set('browser-alert', true);
  };

  acknowledgeGovernment = () => {
    this.setState({
      governmentAlert: false,
    });
    const { cookies } = this.props;
    cookies.set('acknowledgeGovernment', true);
  };

  render() {
    const {
      bgImage,
      showAlert,
      browserAlert,
      governmentAlert,
      showMessageBanner,
      deploymentText,
    } = this.state;
    const {
      children,
      className,
      footerOptions,
      section,
      isFullScreen,
      regionCodeName,
    } = this.props;

    /* eslint-disable */
    return (
      <FullScreenContext.Provider value={isFullScreen}>
        <Fragment>
          <UpdateNotification />

          <div className="bgImage" style={bgImage} />
          <div className="wrapper">
            {showMessageBanner && (
              <MessageBanner
                deploymentText={deploymentText}
                handleDismiss={() =>
                  this.setState({ showMessageBanner: false })
                }
              />
            )}
            <div className={`main ${className}`}>{children}</div>
            {!isFullScreen && <Footer section={section} {...footerOptions} />}
          </div>
          {governmentAlert && (
            <DisclaimerModal
              message={
                'YOU ARE ACCESSING A U.S. GOVERNMENT (USG) INFORMATION SYSTEM (IS). ' +
                'ALL CONTENT PROVIDED ON THIS SITE IS DISTRIBUTION: A, APPROVED FOR PUBLIC RELEASE.'
              }
              onConfirm={this.acknowledgeGovernment}
            />
          )}
          {browserAlert && (
            <div className="browser-alert">
              <p>
                ODIN is best experienced in
                <a
                  href="https://www.google.com/chrome/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Google Chrome
                </a>
                or
                <a
                  href="https://www.mozilla.org/en-US/firefox/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Mozilla Firefox.
                </a>
                Not all functionality can be guaranteed in Internet Explorer or
                Edge.
              </p>
              <Button
                title="Close"
                icon="close"
                hideTitle
                className="browser-alert-icon"
                click={this.browserAlert}
              />
            </div>
          )}
          {showAlert && (
            <div className="cookie-alert">
              <Button
                title="Accept"
                icon="close"
                hideTitle
                className="accept-cookie-icon"
                click={this.acceptCookies}
                hideTooltip
              />
              <p>
                This website uses cookies to track how visitors use our website
                so that we can improve the performance of our site and provide
                you with a better user experience. By continuing to browse this
                website, you are agreeing to our use of cookies.
              </p>
              <Button
                title="Accept"
                className="accept-cookie"
                click={this.acceptCookies}
              />
            </div>
          )}
        </Fragment>
      </FullScreenContext.Provider>
    );
  }
}

// Define the data type for each property passed to the component
Layout.propTypes = {
  /**
   * The content to be displayed
   * Defined as the elements inside the <Layout> open and closing tags.
   */
  children: PropTypes.node,
  /**
   * Represents where the app is now to determine when navigation happens.
   * Provided by react-router-dom and the withRouter method
   */
  location: RouterPropTypes.location.isRequired,
  /**
   * Library that lets you easily manage session history anywhere JavaScript runs
   * Provided by react-router-dom and the withRouter metho
   */
  history: RouterPropTypes.history.isRequired,
  /** Additional class name to apply to the `main` tag */
  className: PropTypes.string,
  /** Object of data to send to the footer section */
  footerOptions: PropTypes.object,
  /** Get the cookies saved for this site */
  cookies: PropTypes.instanceOf(Cookies).isRequired,
};

// Set default values for optional properties
Layout.defaultProps = {
  children: null,
  className: '',
  footerOptions: {},
};

export default withCookies(withRouter(Layout));
