/**
 * Convert the passed data into a useable structure for the subnavigation
 */
export const transformSubnav = (data, section) => {
  console.dir("transformSubnav data:");
  console.dir(data);
  const { links: retrievedLinks, reference } = data; // links doesn't exist?
  console.log("retrievedLinks:");
  console.dir(retrievedLinks);
  linkMap = {};
  imageMap = {};
  let links = [];
  let topCats = [];

  if (section === 'WEG') {
    topCats = data.content.children.domain.children;
    if (Object.keys(topCats).includes('ctc') && !ctc) delete topCats.ctc;
    Object.keys(topCats).forEach((topKey) => {
      if ((!ctc && topKey === 'ctc') || (ctc && topKey !== 'ctc')) return;
      const topCat = topCats[topKey];

      // transform subdomains (firstCats)
      Object.keys(topCat.children).forEach((firstKey) => {
        const firstCat = topCat.children[firstKey];
        const link = {};
        const subLinks = [];
        link.domain = topCat.name;
        link.domainVariable = topCat.variable;
        link.icon = `${firstCat.name.split(' ').join('_')}`;
        link.name = firstCat.name;
        link.variable = firstCat.variable;
        links.push(link);

        // transform sub-sub-domains (secondCats)
        Object.keys(firstCat.children).forEach((secondKey) => {
          const secondCat = firstCat.children[secondKey];
          const subLink = {};
          const subSubLinks = [];
          subLink.domain = topCat.name;
          subLink.domainVariable = topCat.variable;
          subLink.icon = `${secondCat.name.split(' ').join('_')}`;
          subLink.name = secondCat.name;
          subLink.variable = secondCat.variable;
          subLinks.push(subLink);

          Object.keys(secondCat.children).forEach((thirdKey) => {
            const thirdCat = secondCat.children[thirdKey];
            const subSubLink = {};
            subSubLink.domain = topCat.name;
            subSubLink.domainVariable = topCat.variable;
            subSubLink.icon = `${thirdCat.name.split(' ').join('_')}`;
            subSubLink.name = thirdCat.name;
            subSubLink.variable = thirdCat.variable;
            subSubLinks.push(subSubLink);
          });
          subLink.subLinks = subSubLinks;
        });
        link.subLinks = subLinks;
      });
    });
  } else {
    retrievedLinks.forEach((obj) => {
      const url = `dotcms/${obj.icon}`.replace('//', '/');
      imageMap[obj.name] = `${BASE_URL}${url}`;
    });

    links = retrievedLinks
      .filter((el) => !el.archived)
      .map((item) => setSubnav(item));
    if (section === 'TC') addExternalTPLinks(links);
  }
  console.info("topCats:");
  console.dir(topCats);
  return {
    links,
    linkMap,
    reference,
    data,
    topCats,
  };
};

const normalizeDate = (date) => {
  date.setMonth(0);
  date.setDate(1);
  date.setHours(0);
  date.setMinutes(0);
  date.setSeconds(0);
};

export const getTierFromDateOfIntro = (dateOfIntro) => {
  if (!dateOfIntro) return null;
  const now = new Date();
  const currentYear = now.getFullYear();
  const year8 = new Date();
  const year29 = new Date();
  const year50 = new Date();
  year8.setFullYear(currentYear - 8);
  year29.setFullYear(currentYear - 29);
  year50.setFullYear(currentYear - 50);
  normalizeDate(year8);
  normalizeDate(year29);
  normalizeDate(year50);

  let tier;
  const date = new Date(dateOfIntro.substring(0, 4));
  if (date <= year50) {
    tier = 'Tier4';
  } else if (date <= year29) {
    tier = 'Tier3';
  } else if (date <= year8) {
    tier = 'Tier2';
  } else {
    tier = 'Tier1';
  }
  return tier;
};

export const getTierArrayFromDateOfIntro = (dateOfIntro) => {
  const tier = getTierFromDateOfIntro(dateOfIntro);
  let tier1 = false;
  let tier2 = false;
  let tier3 = false;
  let tier4 = false;
  switch (tier) {
    case 'Tier1':
      tier1 = true;
      break;
    case 'Tier2':
      tier2 = true;
      break;
    case 'Tier3':
      tier3 = true;
      break;
    case 'Tier4':
      tier4 = true;
      break;
    default:
      break;
  }
  return [tier1, tier2, tier3, tier4];
};

/**
 * Convert the WEG asset list into a useable structure
 */
export const transformWEGlist = (data, offset, totalHits = 0, online=true) => {
  console.info("transformWEGlist data");
  console.dir(data);
  console.info("transformWEGlist totalHits");
  console.dir(totalHits);
  let wegList = [];
  if (!online || (data && data.contentlets.length > 0)) {
    const { contentlets: wegs } = data;
    wegList = wegs.map((asset) => {
      const {
        dateOfIntroduction,
        identifier: id,
        images,
        title,
        domain,
      } = asset;
      const categories = [];
      const color = '#184c96';
      if (domain && domain.length > 0) {
        domain.forEach((cat) => {
          const key = Object.keys(cat)[0];
          categories.push(cat[key]);
        });
      }

      let image = null;
      if (images) {
        const parsedImgs = JSON.parse(images);
        if (parsedImgs && parsedImgs.length > 0) {
          const tempImage = `dotcms/${parsedImgs[0].url}`.replace('//', '/');
          image = `${BASE_URL}${tempImage}`;
        }
      }

      const transformed = {
        id,
        title,
        image,
        categories,
        dateOfIntroduction,
        color,
      };
      return transformed;
    });
  }
  console.info("transformWEGlist will return");
  console.dir({ wegList, totalHits });
  return { wegList, totalHits };
};

/**
 * Convert the WEG asset details into a useable structure
 */
export const transformAsset = (data, name) => {
  console.info('transformAsset data:');
  console.dir(data);
  let matchedAsset = null;
  data.contentlets.forEach((asset) => {
    if (asset.identifier === name) matchedAsset = asset;
    else if (asset.title === name) {
      matchedAsset = asset;
    }
  });
  if (!matchedAsset) matchedAsset = data.contentlets[0];
  const {
    identifier: id,
    title,
    notes,
    proliferation: rawProliferation,
    dateOfIntroduction,
    origin: rawOrigin,
    sections,
    domain,
  } = matchedAsset;

  // Get country of Origin
  let countryOfOrigin;
  if (rawOrigin) {
    countryOfOrigin = Object.values(rawOrigin[0])[0];
  }

  // Extract proliferation string
  let proliferation;
  if (rawProliferation) {
    proliferation = extractProliferation(rawProliferation);
  }

  let images = [];
  if (matchedAsset.images) {
    images = JSON.parse(matchedAsset.images);
    images.forEach((image) => {
      image.url = `${BASE_URL}dotcms/${image.url}`;
    });
  }

  return {
    id,
    title,
    notes,
    dateOfIntroduction,
    countryOfOrigin,
    proliferation,
    sections,
    images,
    domain,
  };
};

/**
 * Private method to loop through the nested list of links and transform for rendering
 */
function setSubnav(item) {
  const newItem = { ...item };
  newItem.id = getUID('subnav');
  if (imageMap[item.icon]) {
    newItem.icon = imageMap[item.icon];
  }
  if (!item.page) {
    newItem.page = item.name;
  }
  if (item.subLinks && item.subLinks.length > 0) {
    let children = [...item.subLinks];
    if (item.subLinks[0] && item.subLinks[0].name === 'Chapters') {
      children = [...item.subLinks[0].subLinks];
    }
    const subLinks = children.map((subItem) => {
      const child = { ...subItem };
      child.parent = newItem.name;
      return setSubnav(child);
    });
    newItem.subLinks = subLinks;
  }
  let propName = newItem.name;
  if (newItem.page) propName = newItem.page;
  linkMap[propName] = newItem;
  return newItem;
}

function extractProliferation(rawProliferation) {
  let proliferationString = '';
  if (rawProliferation.length === 1) {
    proliferationString = Object.values(rawProliferation[0])[0];
  }
  if (rawProliferation.length > 1) {
    const proliferationCountries = [];
    // eslint-disable-next-line no-plusplus
    rawProliferation.forEach((proliferationObject) => {
      proliferationCountries.push(Object.values(proliferationObject)[0]);
    });
    proliferationString = proliferationCountries.join(', ');
  }

  return proliferationString;
}
