import React from 'react';
import PropTypes from 'prop-types';

import Button from 'UI/Button';

import './SearchResultsHeader.css';

/**
 * A header for the WEGSearchResults and ArticleSearchResults components
 */
const SearchResultsHeader = (props) => {
  const { title, numberOfHits, options, selectedOption, onOptionSelected } =
    props;

  let optionsRender = null;
  if (options && options.length > 0) {
    optionsRender = options.map((option) => {
      const className = selectedOption === option.value ? 'selected' : '';
      return (
        <Button
          key={option.value}
          className={className}
          title={option.displayName}
          click={() => onOptionSelected(option)}
        />
      );
    });
  }
  const hitsString = `${numberOfHits} results`;
  return (
    <div className="search-results-header">
      <h2>{title}</h2>
      {optionsRender}
      <p>{hitsString}</p>
    </div>
  );
};

SearchResultsHeader.propTypes = {
  /**
   * The title of the section
   */
  title: PropTypes.string.isRequired,
  /**
   * The number of search results
   */
  numberOfHits: PropTypes.number.isRequired,
  options: PropTypes.array,
  selectedOption: PropTypes.string,
  onOptionSelected: PropTypes.func,
};

SearchResultsHeader.defaultProps = {
  options: [],
  selectedOption: '',
  onOptionSelected: () => {},
};

export default SearchResultsHeader;
