export const selectImageDirectory = async () => {
  const dirHandle = await window.showDirectoryPicker();

  window.localStorage.setItem('imageDirHandle', dirHandle);
};

export const downloadAndSaveImage = async (dirHandle, imageUrl, imageName) => {
  const response = await fetch(imageUrl);
  const blob = await response.blob();

  const fileHandle = await dirHandle.getFileHandle(imageName, { create: true });
  const writable = await fileHandle.createWritable();

  await writable.write(blob);
  await writable.close();
};

export const requestPersistentStorage = async (addImportantMessage) => {
  if (navigator.storage && navigator.storage.persist) {
    const isPersisted = await navigator.storage.persisted();
    if (!isPersisted) {
      const granted = await navigator.storage.persist();
      console.log(`Storage persistence granted: ${granted}`);
      if (granted) {
        addImportantMessage('Persistent storage has been granted!', "success");
      } else {
        addImportantMessage('Persistent storage request was denied.', "error");
      }
    } else {
      console.log("Persistent storage already granted!");
      addImportantMessage('Persistent storage is already granted.', "warn");
    }
  } else {
    console.log("Persistent storage API not supported.");
    addImportantMessage('Persistent storage is not supported in your browser.', "error");
  }
};

export const storeDirectoryHandle = async (dirHandle) => {
  const db = await openDB('DirectoryHandleDB', 1, {
    upgrade(db) {
      db.createObjectStore('handles');
    },
  });
  await db.put('handles', dirHandle, 'imageDir');
};

export const retrieveDirectoryHandle = async () => {
  const db = await openDB('DirectoryHandleDB', 1);
  const dirHandle = await db.get('handles', 'imageDir');
  return dirHandle;
};

export const selectAndStoreImageDirectory = async () => {
  const dirHandle = await window.showDirectoryPicker();
  await requestPersistentStorage();
  await storeDirectoryHandle(dirHandle);
};

export const isFileSystemAccessSupported = () => {
  if ('showDirectoryPicker' in window)
    return "Your browser does not support the File System Access API.";
};

export const getStorageEstimate = async () => {
  return navigator.storage.estimate();
}

export const isPWA = () => {
  const isStandalone = window.matchMedia('(display-mode: standalone)').matches;

  const isIOSStandalone = window.navigator.standalone === true;

  // For older browsers that might not support matchMedia
  const isChromeStandalone = window.matchMedia('(display-mode: fullscreen)').matches;

  return isStandalone || isIOSStandalone || isChromeStandalone;
}

export const isChrome = () => {
  const userAgent = navigator.userAgent;
  const vendor = navigator.vendor;

  const isChromium = window.chrome;
  const isChrome = /Chrome/.test(userAgent) && /Google Inc/.test(vendor);

  const isEdge = /Edg/.test(userAgent);
  const isOpera = /OPR/.test(userAgent);

  return isChromium && isChrome && !isEdge && !isOpera;
}

export const isMobileDevice = () => {
  const ua = navigator.userAgent || navigator.vendor || window.opera;
  return /android|iphone|ipad|ipod|blackberry|iemobile|opera mini/i.test(ua.toLowerCase());
}

// TODO: Show an Install button by default
// If intalled, instead show Import Modal
// Install button can be under/above the WEG search bar home page thing
export const showInstallPromotion = () => {
  const installButton = document.getElementById('installButton');
  installButton.style.display = 'block';

  installButton.addEventListener('click', async () => {
    installButton.style.display = 'none';
    if (deferredPrompt) {
      deferredPrompt.prompt();
      const { outcome } = await deferredPrompt.userChoice;
      if (outcome === 'accepted') {
        console.log('User accepted the install prompt');
      } else {
        console.log('User dismissed the install prompt');
      }
      deferredPrompt = null;
    }
  });
}
