// Dependencies
import React, { Component } from 'react';
import PropTypes from 'prop-types';

// Utils

// UI
import Button from 'UI/Button';
import { FullScreenContext } from 'UI/Layout';

// Components
import Share from 'Components/Share';

// Styles
import './SectionHeader.css';

/**
 * Render a button and options to allow the user to SectionHeader content
 */
class SectionHeader extends Component {
  static contextType = FullScreenContext;

  state = {
    shareRef: {
      isOpen: false,
      node: null,
    },
  };

  componentWillMount() {
    document.addEventListener('click', this.clickOutside, false);
  }

  componentWillUnmount() {
    document.removeEventListener('click', this.clickOutside, false);
  }

  /**
   * Set a reference to the pop up containers for use with the `clickOutside` event
   */
  setRef = (node, refName) => {
    const refs = { ...this.state };
    const { node: prevNode } = refs[refName];
    if (!prevNode) {
      this.setState({
        [refName]: {
          isOpen: false,
          node,
        },
      });
    }
  };

  /**
   * Open the selected pop up
   */
  togglePopup = (popup) => {
    const refs = { ...this.state };
    if (refs[popup]) {
      refs[popup].isOpen = !refs[popup].isOpen;
    }
    Object.keys(refs).forEach((refName) => {
      if (refName !== popup && refs[refName].node) {
        refs[refName].isOpen = false;
      }
    });
    this.setState(refs);
  };

  /**
   * Close the currently opened pop up when the user clicks outside of it
   */
  clickOutside = (event) => {
    const refs = { ...this.state };
    let refName = null;
    Object.keys(refs).forEach((ref) => {
      if (refs[ref].isOpen) {
        refName = ref;
      }
    });
    if (refName) {
      const { node } = refs[refName];
      if (node && node.contains(event.target)) return;
      this.setState({
        [refName]: {
          isOpen: false,
          node,
        },
      });
    }
  };

  render() {
    const {
      title,
      modal,
      identifier,
      modalClose,
      section,
      showShare,
      showClose,
      additionalButtons,
      isFullScreen,
    } = this.props;
    const { shareRef } = this.state;
    let isOpen = null;
    if (modal) {
      isOpen = (
        <Button
          className="close-modal"
          title="Close Modal"
          click={modalClose}
          icon="Back"
          hideTitle
        />
      );
    }

    return (
      <div
        className="section-header"
        role="presentation"
        onClick={this.clickOutside}
      >
        {isFullScreen && isOpen}
        <h1>{title}</h1>
        {section.toLowerCase() === 'date' ? (
          <GeospatialButton identifier={identifier} />
        ) : null}
        {showShare ? (
          <Share
            nodeRef={(node) => this.setRef(node, 'shareRef')}
            refName="shareRef"
            togglePopup={() => this.togglePopup('shareRef')}
            isOpen={shareRef.isOpen}
            section={section}
            itemTitle={title}
          />
        ) : null}
        {additionalButtons}
        {showClose ? (
          <Button
            title="Close Modal"
            click={modalClose}
            icon="close"
            hideTitle
          />
        ) : null}
      </div>
    );
  }
}

// Define the data type for each property passed to the component
SectionHeader.propTypes = {
  /** Title to display */
  title: PropTypes.string,
  identifier: PropTypes.string,
  /** The title is being displayed in a modal */
  modal: PropTypes.bool,
  /** The action to take when the modal close button is clicked */
  modalClose: PropTypes.func,
  /** the section that we are located in */
  section: PropTypes.string,
  /** Controls whether or not to show the share button */
  showShare: PropTypes.bool,
  /** Additional components to render on the right side, if any */
  additionalButtons: PropTypes.object,
  showClose: PropTypes.bool,
  isFullScreen: PropTypes.bool,
};

// Set default values for optional properties
SectionHeader.defaultProps = {
  title: '',
  identifier: null,
  modal: false,
  modalClose: () => console.log('Close Modal'),
  section: '',
  showShare: true,
  showFeedback: true,
  additionalButtons: null,
  showClose: false,
  isFullScreen: false,
};

export default SectionHeader;
