import React, {
  useState,
  useEffect
} from 'react';
import Button from 'UI/Button';
import { beginImageImport } from 'dataAccess';
import { getMiscByKey, initLocalDB, getOnlineStatus, addMisc } from 'dataAccess';
import './ImportModal.css';
import { resetLocalDB } from 'dataAccess';

// todo: z-index fix (filters overlaying)
// todo: add clear data button

export default function ImportModal({
  mangerMode,
  close,
  assetsCompleted,
  setAssetsCompleted,
  totalAssets,
  setTotalAssets,
  fetchedAssets,
  importing,
  setImporting,
  startTime
}) {
  const [isOnline, setIsOnline] = useState(false);
  const [assetImagesLimit, setAssetImagesLimit] = useState(1);
  const [storageEstimate, setStorageEstimate] = useState(0);
  const [storageUsed, setStorageUsed] = useState(0);
  const [controller, setController] = useState(new AbortController());
  const [completed, setCompleted] = useState(false);
  const [currentStatus, setCurrentStatus] = useState('');
  const [modalDetails, setModalDetails] = useState({});

  const incrementAssetsCompleted = () => {
    console.info('incrementAssetsCompleted!');
    setAssetsCompleted((prevCount) => prevCount + 1);
  };

  useEffect(() => {
    if (mangerMode === 'clear') {
      setModalDetails({
        title: 'Clear Data Storage',
        className: 'progress-bar-delete',
        iconStyle: 'invert-color',
        icon: 'erase_icon.png'
      });
    } else {
      setModalDetails({
        title: 'Import/Update WEG Data',
        className: 'progress-bar-import',
        iconStyle: '',
        icon: 'refresh_icon.png'
      });
    }
    setStorageDetails();
    getOnlineStatus().then((online) => setIsOnline(online));
  }, []);

  const setStorageDetails = async () => {
    navigator.storage.estimate().then((estimate) => {
      setStorageEstimate(estimate.quota);
      setStorageUsed(estimate.usage);
    });
  };

  useEffect(() => {
    let intervalId;

    if (importing) {
      intervalId = setInterval(() => {
        setStorageDetails();
        if (!importing) {
          clearInterval(intervalId);
        }
      }, 1000);
    }

    return () => clearInterval(intervalId);
  }, [importing]);

  const signalImport = (ongoing) => {
    console.info('signaling import; ongoing:', ongoing);
    const event = new CustomEvent('importStatusChanged');
    event.ongoing = ongoing;
    console.info('signaling import; event:', event);
    window.dispatchEvent(event);
  };

  const startAssetImport = async () => {
    const abortSignal = controller.signal;
    setAssetsCompleted(0);
    console.info('Beginning asset import...');
    try {
      await initLocalDB(setTotalAssets, incrementAssetsCompleted, abortSignal);
    } catch (error) {
      console.error(`Failed to import assets: ${error.message}`);
    } finally {
      setController(new AbortController());
    }
  };

  const startImageImport = async () => {
    const abortSignal = controller.signal;

    setAssetsCompleted(0);
    console.info('Beginning image import...');
    try {
      await beginImageImport(
        setTotalAssets,
        incrementAssetsCompleted,
        assetImagesLimit,
        abortSignal
      );
      console.log('Images imported successfully.');
      addMisc('lastImageImport', startTime);
    } catch (error) {
      console.error(`Failed to import images: ${error.message}`);
    } finally {
      if (!abortSignal.aborted) {
        console.info('Finished importing images.');
      }
      setController(new AbortController());
    }
  };

  const handleStart = async () => {
    setImporting(true);
    signalImport(true);
    if (mangerMode === 'clear') {
      setAssetsCompleted(0);
      setTotalAssets(6);
      setCurrentStatus('Clearing Local Data');
      await resetLocalDB(incrementAssetsCompleted);
      setCurrentStatus('Finished clearing local data');
      setImporting(false);
      setCompleted(true);
      setStorageDetails();
    } else {
      setCurrentStatus('Importing WEG Asset Information');
      await startAssetImport();
      setCurrentStatus('Importing WEG Asset Images');
      await startImageImport();
      setCurrentStatus('Finished importing');
      setImporting(false);
      setCompleted(true);
      setStorageDetails();
      signalImport(false);
    }
  };

  const handleCancel = () => {
    addImportantMessage('Cancelling import in progress.', 'error');
    controller.abort();
  }

  function formatBytes(bytes, decimals = 2) {
    if (bytes === 0) return '0 Bytes';

    const k = 1024;
    const dm = decimals < 0 ? 0 : decimals;

    const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB'];

    const i = Math.floor(Math.log(bytes) / Math.log(k));

    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
  }

  const renderImportButton = () => {
    if (completed)
      return (
        <button className="close" onClick={close}>
          CLOSE
        </button>
      );
    if (totalAssets > 0 && assetsCompleted / totalAssets === 1)
      return (
        <button className="start" onClick={handleStart}>
          RESTART
        </button>
      );
    if (!importing && !completed)
      return (
        <button className="start" onClick={handleStart}>
          START
        </button>
      );
    if (importing)
      return (
        <button className="cancel" onClick={handleCancel}>
          CANCEL
        </button>
      );
  };

  return (
    <div className="import-modal">
      <div className="import-modal-header-container">
        <div className="import-modal-header">
          <img className={modalDetails.iconStyle} src={modalDetails.icon} />
          <h3>{modalDetails.title}</h3>
        </div>
        <Button
          className="close-modal"
          title="Close Modal"
          click={close}
          icon="close"
          hideTitle
        />
      </div>

      <div className="import-modal-content">
        <div className="status-section">
          <div className="status-section-text">
            <h4>Status:</h4>
            <p>{currentStatus}</p>
            {importing && totalAssets > 0 && (
              <p>{`(${assetsCompleted} / ${totalAssets})`}</p>
            )}
          </div>
          <div className="progress-bar-container">
            <progress
              className={`progress-bar ${modalDetails.className}`}
              value={assetsCompleted}
              max={totalAssets}
            />
            <h3 className="progress-text">
              {Math.floor((assetsCompleted / totalAssets) * 100)}% Complete
            </h3>
          </div>
        </div>

        <div className="bottom-row">
          <div className="storage-info">
            <p>
              <strong>Storage Used:</strong> {formatBytes(storageUsed)}
              {` (${(100 * (storageUsed / storageEstimate)).toFixed(3)}%)`}
            </p>
            <p>
              <strong>Storage Available:</strong> {formatBytes(storageEstimate)}
            </p>
          </div>

          <div className="button-container">{renderImportButton()}</div>
        </div>
      </div>
    </div>
  );
}
