// Dependencies
import React from 'react';
import ReactDOM from 'react-dom';
// import SVG from 'react-inlinesvg';
import PropTypes from 'prop-types';

// UI

// Styles
import './Loading.css';

// Assets

/**
 * Display Loading Animation
 */
const loading = (props) => {
  const { internal } = props;
  let loadClass = 'loading';
  if (internal) {
    loadClass += ' internal';
  }
  const loadContent = (
    <div className={loadClass}>
      <div className="spinner" />
    </div>
  );

  if (internal) return loadContent;
  return ReactDOM.createPortal(loadContent, document.body);
};

// Define the data type for each property passed to the component
loading.propTypes = {
  /** Render the loading animation within another container */
  internal: PropTypes.bool,
};

// Set default values for optional properties
loading.defaultProps = {
  internal: false,
};

export default loading;
