export const parseQuery = (query) => {
  console.info('parseQuery query:');
  console.dir(query);

  const filters = {
    dateRanges: [],
    categories: [],
  };

  query.forEach((entry) => {
    console.info('parseQuery entry:');
    console.dir(entry);
    const dateRangeMatch = entry.match(
      /\+WegCard\.dateOfIntroduction:\(\s*(.*?)\s*\)/
    );
    if (dateRangeMatch) {
      console.info('dateRangeMatch:');
      console.dir(dateRangeMatch);
      const dateRangeExpr = dateRangeMatch[1]; // The content inside the parentheses

      // Regex to match each date range within the expression
      const dateRangeRegex =
        /\[\s*(\d{4}-\d{2}-\d{2})\s+TO\s+(\d{4}-\d{2}-\d{2})\s*\]/g;
      let match;
      while ((match = dateRangeRegex.exec(dateRangeExpr)) !== null) {
        console.info('while match:');
        console.dir(match);
        filters.dateRanges.push({
          from: match[1],
          to: match[2],
        });
      }
    }

    const categoryRegex = /\+categories:([^\s]+)/g;
    let match;
    while ((match = categoryRegex.exec(entry)) !== null) {
      filters.categories.push(match[1]);
    }
  });

  console.info('parseQuery returning:');
  console.dir(filters);
  return filters;
};

export const flattenSubnav = (subnav) => {
  const categoryMap = {};

  const traverse = (node, parentKey = null) => {
    for (const key in node) {
      const category = node[key];
      categoryMap[key] = {
        name: category.name,
        key: key,
        variable: category.variable,
        parent: parentKey,
        children: Object.keys(category.children || {}),
      };

      if (category.children && Object.keys(category.children).length > 0) {
        traverse(category.children, key);
      }
    }
  };

  traverse(subnav);
  return categoryMap;
};
