/* eslint-disable */
import {
  ELEMENT_PARAGRAPH,
  ELEMENT_H1,
  ELEMENT_H2,
  ELEMENT_H3,
  ELEMENT_H4,
  ELEMENT_H5,
  ELEMENT_H6,
  ELEMENT_IMAGE,
  createPlugins,
  createReactPlugin,
  createPlateUI,
  createHistoryPlugin,
  createParagraphPlugin,
  createHeadingPlugin,
  createBoldPlugin,
  createItalicPlugin,
  createUnderlinePlugin,
  createListPlugin,
  createLinkPlugin,
  createImagePlugin,
  createTablePlugin,
  createHorizontalRulePlugin,
  createAlignPlugin,
  createSelectOnBackspacePlugin,
  createPlateEditor,
  withPlate,
  serializeHtml,
  createDeserializeDocxPlugin,
} from '@udecode/plate';
import { parse } from 'node-html-parser';
import DOMPurify from "dompurify";
import React from 'react';

export const getEditorPack = () => {
  const plugins = createPlugins(
      [
          createParagraphPlugin(),
          createHeadingPlugin(),
          createBoldPlugin(),
          createItalicPlugin(),
          createUnderlinePlugin(),
          createListPlugin(),
          createLinkPlugin(linkPlugin),
          createImagePlugin(imagePlugin),
          createHorizontalRulePlugin(),
          createTablePlugin(),
          createAlignPlugin(alignPlugin),
          createDeserializeDocxPlugin(),
          createSelectOnBackspacePlugin(backspacePlugin),
      ],
      {
          components: createPlateUI({})
      }
  );
  const editor = createPlateEditor({ plugins });
  return { editor, plugins };
}

const urlRegEx = /^(((https|http)?:\/\/)|(\/)|(..\/))(\w+:{0,1}\w*@)?(\S+)(:[0-9]+)?(\/|\/([\w#!:.?+=&%@!\-\/]))?/;
const mailRegEx = /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/;
const telRegEx = /^1-\d{3}-\d{3}-\d{4}$/;

const isHref = ( str ) => {
return urlRegEx.test(str) || mailRegEx.test(str) || telRegEx.test(str);
}

const serializeLink = ( props ) => {
const link = props.element.url;
const text = props.children;
let href = link;
if (mailRegEx.test(link)) {
  href = 'mailto:' + href;
} else if (telRegEx.test(link)) {
  href = href.replace(/-/g, '');
  href = 'tel:+' + href;
}
return (
  <a href={href}>{text}</a>
);
}

const linkPlugin = {
options: { isUrl: isHref },
serializeHtml: props => serializeLink(props),
};

const imagePlugin = {
serializeHtml: props => serializeImageFromProps(props),
};

const alignPlugin = {
  inject: {
      props: {
          validTypes: [
              ELEMENT_PARAGRAPH,
              ELEMENT_H1,
              ELEMENT_H2,
              ELEMENT_H3,
              ELEMENT_H4,
              ELEMENT_H5,
              ELEMENT_H6,
          ],
      },
  }
}

const backspacePlugin = {
  options: { query: { allow: [ELEMENT_IMAGE] } },
}

const serializeImageFromProps = props => {
  const { element } = props;
  let altText = "";
  if (element.caption) {
      if (element.caption[0]?.children?.length)
          altText = element.caption[0].children[0].text;
      else if (element.caption.length)
          altText = element.caption[0].text;
  }
  const image = <img src={element.url} alt={altText} className="thumbimage" />
  return (
      <figure>
          {image}
          <figcaption>{altText}</figcaption>
      </figure>
  )
}

export const getFirstHeadingIndex = (nodes) => {
  let index = 0;
  for (let i = 0; i < nodes.length; i++) {
      if (nodes[i].matches('h1') || nodes[i].matches('h2')) {
          index = i;
          break;
      }
  }
  return index;
};

const transformPtoDL = (body, p) => {
  const dd = document.createElement('dd');
  const dl = document.createElement('dl');
  dd.innerHTML = p.innerHTML;
  dl.appendChild(dd);
  p.parentNode.replaceChild(dl, p);
};

const addAttributesToChapterSubheading = (el) => {
  const nextEl = el.nextElementSibling;
  if (
      el.innerText.startsWith('Chapter') &&
      nextEl.innerText.startsWith('Main article:')
  ) {
      const div = document.createElement('div');
      div.setAttribute('role', 'note');
      div.classList.add('hatnote');
      div.innerHTML = nextEl.innerHTML;
      nextEl.replaceWith(div);
  }
};

const addAttributesToAnchor = (el) => {
  el.setAttribute('rel', 'nofollow');
  el.className += ' external free';
};

const addChildToHeadline = (el) => {
  const span = document.createElement('span');
  span.className += 'mw-headline';
  span.innerHTML = el.innerHTML;

  el.innerHTML = '';
  el.appendChild(span);
};


const transformElems = (body, selector, transform) => {
  const elements = body.querySelectorAll(selector);
  elements.forEach(element => transform(element));
}

const getImageWidthMap = (nodes, imgMap={}) => {
  nodes.forEach(node => {
      const { type, children } = node;
      if ( type === 'img' ) {
          const { url, width } = node;
          imgMap[url] = width;
      }
      if ( children ) getImageWidthMap(children, imgMap);
  });
  return imgMap;
}

const transformImgs = (body, nodes) => {
  const imgMap = getImageWidthMap(nodes);
  const elements = body.querySelectorAll('img');
  elements.forEach(elem => {
      const { src } = elem;
      const srcKey = src.slice(src.indexOf('/dotcms'));
      const width = imgMap[srcKey] ? imgMap[srcKey] : 100;
      elem.width = width;
  });
}

const transformHTML = (html, origNodes) => {
  const cleanHTML = DOMPurify.sanitize(html);
  const { body } = new DOMParser().parseFromString(cleanHTML, 'text/html');

  transformElems(body, 'h1, h2, h3, h4, h5, h6', addChildToHeadline);
  transformElems(body, 'h2', addAttributesToChapterSubheading);
  transformElems(body, 'a', addAttributesToAnchor);
  transformImgs(body, origNodes);

  const nodes = body.children;
  const firstHeadingIndex = getFirstHeadingIndex(nodes);
  if (nodes[0]?.matches('dl') && firstHeadingIndex > 0) {
      for (let i = 0; i < firstHeadingIndex; i++) {
          const el = nodes[i];
          if (el.tagName === 'p') transformPtoDL(body, el);
      }
  }

  const result = document.createElement('div');
  result.className += 'mw-parser-output';
  result.innerHTML = body.innerHTML;

  return result.outerHTML;
};

const serializeToHTML = (editor, plugins, nodes) => {
  const html = serializeHtml(editor, { nodes });
  return transformHTML(html, nodes);
};

const changeHeadingIDs = (html) => {
  const nodes = html.childNodes[0].childNodes;
  nodes.forEach((node) => {
      const tag = node.rawTagName;
      if (tag === 'h2' || tag === 'h3' || tag === 'h4' || tag === 'h5') {
          const childNode = node.childNodes[0];
          childNode.setAttribute('id', idText);
      }
  });
  return html;
};

export const extractHtml = (editor, plugins, content) => {
  const nodes = JSON.parse(content);
  const serialized = serializeToHTML(editor, plugins, nodes);
  const parsed = parse(serialized);
  return parsed.toString();
}

const replaceLinksWithPlaceholder = (doc) => {
Array.from(doc.getElementsByClassName('image')).forEach((el) => {
  if (el.nodeName === 'A') {
    const placeholder = document.createElement('div');
    placeholder.innerHTML = el.innerHTML;
    placeholder.classList.add('PLACEHOLDER_DIV');
    el.replaceWith(placeholder);
  }
});
};

const makeImagesClickable = (doc) => {
  Array.from(doc.querySelectorAll('.PLACEHOLDER_DIV, .slate-ImageElement-img')).forEach((el) => {
    const link = document.createElement('a');
    if (el.classList.contains('PLACEHOLDER_DIV')) {
      const image = el.getElementsByTagName('img')[0];
      link.setAttribute('href', image.src);
    } else {
      link.setAttribute('href', el.src);
    }
    link.classList.add('image');
    el.parentNode.replaceChild(link, el);
    link.appendChild(el);
  });
};

export const prepareContentForEditor = (content) => {
  let doc = new DOMParser().parseFromString(content, 'text/html');
  replaceLinksWithPlaceholder(doc);
  return doc.body.innerHTML;
}

export const processContentFromEditor = (editor, plugins) => {
  const newContent = extractHtml(editor, plugins);
  let doc = new DOMParser().parseFromString(newContent, 'text/html');
  makeImagesClickable(doc);
  return doc.body.innerHTML;
}
