// Dependencies
import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import RouterPropTypes from 'react-router-prop-types';

// Utils
import { getParams } from 'Utils/Router';

// UI
import Layout from 'UI/Layout';
import Button from 'UI/Button';

// Components
import WEGSearchResults from 'Components/WEGSearchResults';

// Styles
import './SearchResults.css';

/**
 * Fetch, format, and display a Mediawiki SearchResults
 */
class SearchResults extends Component {
  state = {
    showWEGResults: true,
    showArticleResults: false,
  };

  render() {
    console.info("SearchResults render")
    const { showWEGResults, showArticleResults } = this.state;
    const { location } = this.props;
    const { section, searchString, regionCodeName } = getParams(location);

    const sectionHasWEG =
      !section ||
      section === 'All' ||
      section === 'WEG' ||
      section === 'Search';

    const sectionHasArticle =
      !section ||
      section === 'All' ||
      section === 'TC' ||
      section === 'DATE' ||
      section === 'WEG' ||
      section === 'Search' ||
      section === 'VOA' ||
      section === 'Region';

    const wegClassName =
      sectionHasArticle && showArticleResults ? 'partial-width' : 'full-width';
    const wegRender = sectionHasWEG ? (
      <WEGSearchResults
        hide={!showWEGResults}
        searchString={searchString}
        className={wegClassName}
      />
    ) : null;

    let tabRender = null;
    if (sectionHasWEG && sectionHasArticle) {
      if (showWEGResults && showArticleResults) {
        // both sections are being shown
        tabRender = (
          <Fragment>
            <Button
              className="tab flip-x"
              icon="search-chevron"
              click={() => this.setState({ showWEGResults: false })}
              title="Hide Assets"
              hideTitle
              hideTooltip
            />
            <Button
              className="tab"
              icon="search-chevron"
              click={() => this.setState({ showArticleResults: false })}
              title="Hide Articles"
              hideTitle
              hideTooltip
            />
          </Fragment>
        );
      } else if (showWEGResults) {
        // only weg assets are being shown
        tabRender = (
          <Button
            className="tab flip-x"
            icon="search-chevron"
            click={() => this.setState({ showArticleResults: true })}
            title="Show Articles"
            hideTitle
            hideTooltip
          />
        );
      } else if (showArticleResults) {
        // only articles are being shown
        tabRender = (
          <Button
            className="tab"
            icon="search-chevron"
            click={() => this.setState({ showWEGResults: true })}
            title="Show Assets"
            hideTitle
            hideTooltip
          />
        );
      }
    }

    let articleSection = section;
    if (!section || section === 'All' || section === 'Search') {
      articleSection = 'TC|DATE|VOA|WEG_Resource';
    } else if (section === 'WEG') {
      articleSection = 'WEG_Resource';
    }

    let filterRender = null;
    const allResultsClass =
      showWEGResults && showArticleResults ? 'active' : '';
    const assetsClass = showWEGResults && !showArticleResults ? 'active' : '';
    const articlesClass = !showWEGResults && showArticleResults ? 'active' : '';

    if (sectionHasWEG && sectionHasArticle) {
      filterRender = (
        <div className="sr-filters">
          <Button
            className={allResultsClass}
            title="All Results"
            icon="subnav-book"
            click={() =>
              this.setState({ showWEGResults: true, showArticleResults: true })
            }
          />
          <Button
            className={assetsClass}
            title="Assets"
            icon="weg"
            click={() =>
              this.setState({ showWEGResults: true, showArticleResults: false })
            }
          />
          <Button
            className={articlesClass}
            title="Articles"
            icon="subnav-chapter"
            click={() =>
              this.setState({ showWEGResults: false, showArticleResults: true })
            }
          />
        </div>
      );
    }

    return (
      <Layout section="Search" regionCodeName={regionCodeName}>
        {filterRender}
        <h2 className="search-term-header">
          {`Search results for "${searchString}":`}
        </h2>
        <div className="search-results">
          {wegRender}
          {tabRender}
        </div>
      </Layout>
    );
  }
}

// Define the data type for each property passed to the component
SearchResults.propTypes = {
  /**
   * Represents where the app is now to determine when navigation happens.
   * Provided by react-router-dom and the withRouter method
   */
  location: RouterPropTypes.location.isRequired,
};

// Set default values for optional properties
SearchResults.defaultProps = {};

export default withRouter(SearchResults);
