let isOnline = false;
let lastCheckTime = 0;
const BASE_URL = import.meta.env.VITE_ODIN_API_BASE_URL;
const CHECK_INTERVAL = 1000 * 30 * 999; // 30 seconds
const HEALTH_CHECK_URL = `${BASE_URL}dotcms/api/sites/identifiers`;

export const performHealthCheck = async () => {
  try {
    const response = navigator.onLine
      ? await fetch(HEALTH_CHECK_URL, { method: 'HEAD' }).catch(() => false)
      : false;
    if (response.ok) {
      isOnline = true;
    } else {
      isOnline = false;
    }
  } catch (error) {
    isOnline = false;
  } finally {
    lastCheckTime = Date.now();
  }
};

export const getOnlineStatus = async () => {
  console.info("getOnlineStatus called!")
  const now = Date.now();
  if (now - lastCheckTime > CHECK_INTERVAL) {
    console.info("getOnlineStatus performHealthCheck!")
    performHealthCheck();
  }
  console.info("getOnlineStatus returning:", isOnline)
  return isOnline;
};

setInterval(performHealthCheck, CHECK_INTERVAL);

performHealthCheck();

export const toggleOnline = () => {
  console.info('toggling online status. Is currently:', isOnline);
  isOnline = !isOnline;
};
