import React from 'react';
import PropTypes from 'prop-types';
import './messageBanner.css';
import { useAuthdViewLive } from 'Context';

const MessageBanner = ({ deploymentText, handleDismiss }) => {
  const { authdViewLive, setAuthdViewLive } = useAuthdViewLive();

  let messageText = 'Content and searches will load draft content.';
  let actionText = 'Load as live content';
  if (authdViewLive === true) {
    messageText = 'Content and searches will load live content.';
    actionText = 'Load as draft content';
  }

  const handleAction = () => {
    setAuthdViewLive(!authdViewLive);
  };

  return (
    <div className="message-banner">
      <span className="deployment">{deploymentText}</span>
      <span className="message">{messageText}</span>
      <button className="action-button" onClick={handleAction}>
        {actionText}
      </button>
      <button className="dismiss-button" onClick={handleDismiss}>
        X
      </button>
    </div>
  );
};

MessageBanner.propTypes = {
  deploymentText: PropTypes.string.isRequired,
  handleDismiss: PropTypes.func.isRequired,
};

export default MessageBanner;
