/* eslint-disable */
import axios from 'axios';
import { parse } from 'papaparse';
import { API_URL } from 'API';

const BASE_URL = import.meta.env.VITE_ODIN_API_BASE_URL;
const RFI_URL = '/WEGAPI/rfi';

/**
 * Parse a list of weg assets into an exportable csv format
 */
export const exportAssetsCSV = (assets) => {
  const fields = ['name', 'disname', 'disstring', 'dateOfIntroduction'];
  const opts = { fields };

  try {
    const csv = parse(assets, opts);
    const blob = new Blob([csv], { type: 'text/csv' });
    const url = window.URL.createObjectURL(blob);
    window.location.assign(url);
  } catch (err) {
    console.log(err);
  }
};

export const getContent = (id) => {
  return axios.get(`${API_URL}/id/${id}`).then(
    (response) => {
      return response.data.contentlets[0];
    },
    (error) => error
  );
};

export const submitRFIFeedback = (data) => {
  const config = {
    json: true,
    headers: {
      'Content-Type': 'application/json',
    },
  };
  return axios.post(RFI_URL, data, config).then(
    (response) => {
      return response;
    },
    (error) => error
  );
};

export const getNode = (query) => {
  const endpoint = `${BASE_URL}dotcms/api/content/_search`;
  return axios.post(endpoint, {
    limit: 0,
    offset: 0,
    query: query,
    sort: 'score',
  });
};

export const postExport = (data) => {
  return axios.post('/export/pdf', data, {
    json: true,
    headers: { 'Content-Type': 'application/json' },
  });
};
