import React from 'react';

import Layout from 'UI/Layout';
import Scrollbars from 'UI/Scrollbars';

import { getUID } from 'Utils/UID';

import './Privacy.css';

import content from './Privacy.json';

/** Displays the ODIN Privacy & Security Policy page */
const privacy = () => (
  <Layout className="privacy-page">
    <h1 className="page-header">Privacy &amp; Security Policy</h1>
    <Scrollbars className="page-content">
      {content.map((section) => (
        <div key={getUID()}>
          <h2 className="privacy-header">{section.title}</h2>
          <hr />
          {section.body.map((paragraph) => (
            <p key={getUID()}>{paragraph}</p>
          ))}
        </div>
      ))}
    </Scrollbars>
  </Layout>
);

export default privacy;
