// Dependencies
import React, { Component } from 'react';
import PropTypes from 'prop-types';

// UI
import Button from 'UI/Button';

// Styles
import './AssetTabs.css';

/**
 * Displays the tab view at the top of the asset detail window.
 */
class AssetTabs extends Component {
  state = {
    /** The index of the 'active' tab, whose details are shown */
    tabIndex: 0,
    /** The index of the first tab shown in the list */
    tabStart: 0,
    /** The offset as calculated by the widths of the tabs */
    tabOffset: 0,
    /** Stored component width; re-render if this changes */
    containerWidth: 0,
    /** Children of the container's total added widths */
    childrenWidth: 0,
  };

  container = null;

  componentDidMount() {
    window.addEventListener('resize', () => {
      this.updateWidths();
    });
  }

  componentDidUpdate() {
    this.updateWidths();
  }

  componentWillUnmount() {
    window.removeEventListener('resize', () => {
      this.updateWidths();
    });
  }

  /**
   * Show a tab in the tab view. Set class name depending on whether or not
   * it is the currently active tab.
   */
  renderTab = (tab, index) => {
    const { click } = this.props;
    const { tabIndex, tabOffset } = this.state;
    return (
      <div key={tab.name}>
        <Button
          title=""
          className={`tab${index === tabIndex ? ' active' : ''}`}
          click={() => {
            click(index);
            this.setState({ tabIndex: index });
          }}
          style={{
            transform: `translateX(${tabOffset}px)`,
          }}
        >
          {tab.name}
        </Button>
      </div>
    );
  };

  /**
   * Navigate through displayed tabs. Won't go past 0 or the full number of tabs.
   */
  navigateTabs = (direction) => {
    const { tabs } = this.props;
    let { tabStart } = this.state;
    tabStart += direction;
    if (tabStart > -1 && tabStart < tabs.length) {
      this.setState({
        tabStart,
      });
    }
  };

  /**
   * Update the widths on window resize
   */
  updateWidths = () => {
    if (!this.container) return;
    const { containerWidth, childrenWidth, tabStart, tabOffset } = this.state;
    if (containerWidth !== this.container.offsetWidth) {
      this.setState({ containerWidth: this.container.offsetWidth });
    }

    const children = Array.from(this.container.childNodes);
    const widths = children.reduce(
      (total, child) => total + child.offsetWidth,
      0
    );
    if (childrenWidth !== widths) {
      this.setState({ childrenWidth: widths });
    }

    const newOffset = -children
      .slice(0, tabStart)
      .reduce((total, child) => total + child.offsetWidth, 0);

    if (tabOffset !== newOffset) {
      this.setState({ tabOffset: newOffset });
    }
  };

  /**
   * Show a navigational tab, either left or right.
   */
  renderNavTab = (direction) => {
    const { tabStart, childrenWidth, tabOffset, containerWidth } = this.state;

    const { tabs } = this.props;

    const inc = direction > 0;
    const classNames = ['tab'];

    if (!inc) {
      classNames.push('flipped');
      if (tabStart === 0) classNames.push('inactive');
    } else {
      const innerWidth = childrenWidth + tabOffset;
      if (innerWidth < containerWidth || tabStart === tabs.length - 1)
        classNames.push('inactive');
    }

    return (
      <div className="nav-tab">
        <Button
          title=""
          icon="chevron"
          className={classNames.join(' ')}
          click={() => this.navigateTabs(direction)}
        />
      </div>
    );
  };

  render() {
    const { tabs } = this.props;

    if (!tabs) return null;

    return (
      <div className="asset-tabs">
        {this.renderNavTab(-1)}
        <div
          className="container"
          ref={(element) => {
            this.container = element;
          }}
        >
          {tabs.map(this.renderTab)}
        </div>
        {this.renderNavTab(1)}
      </div>
    );
  }
}

AssetTabs.propTypes = {
  /** A method to be called when a tab is clicked */
  click: PropTypes.func.isRequired,
  /** An array of tabs from AssetDetail component */
  tabs: PropTypes.array,
};

AssetTabs.defaultProps = {
  tabs: null,
};

export default AssetTabs;
