/* eslint-disable */
import axios from 'axios';

import { transformWEGlist, transformAsset } from 'Utils/Transforms/DotCMS';

import { API_URL } from 'API';

import { getAssetByInode } from 'dataAccess';

const BASE_URL = import.meta.env.VITE_ODIN_API_BASE_URL;

const normalizeDate = (date) => {
  date.setMonth(0);
  date.setDate(1);
  date.setHours(0);
  date.setMinutes(0);
  date.setSeconds(0);
};

const getDateRange = (tier) => {
  const now = new Date();
  const currentYear = now.getFullYear();
  const year7 = new Date();
  const year28 = new Date();
  const year49 = new Date();
  const year300 = new Date();

  year7.setFullYear(currentYear - 7);
  year28.setFullYear(currentYear - 28);
  year49.setFullYear(currentYear - 49);
  year300.setFullYear(currentYear - 300);
  normalizeDate(year7);
  normalizeDate(year28);
  normalizeDate(year49);

  const nowMillis = now
    .toISOString()
    .replace('Z', '')
    .split('.')[0]
    .split('T', 1)[0];
  const year7Millis = year7
    .toISOString()
    .replace('Z', '')
    .split('.')[0]
    .split('T', 1)[0];
  const year28Millis = year28
    .toISOString()
    .replace('Z', '')
    .split('.')[0]
    .split('T', 1)[0];
  const year49Millis = year49
    .toISOString()
    .replace('Z', '')
    .split('.')[0]
    .split('T', 1)[0];
  const year300Millis = year300
    .toISOString()
    .replace('Z', '')
    .split('.')[0]
    .split('T', 1)[0];

  if (tier == 'Tier1') {
    return `[ ${year7Millis} TO ${nowMillis} ]`;
  } else if (tier == 'Tier2') {
    return `[ ${year28Millis} TO ${year7Millis} ]`;
  } else if (tier == 'Tier3') {
    return `[ ${year49Millis} TO ${year28Millis} ]`;
  } else if (tier == 'Tier4') {
    return `[ ${year300Millis} TO ${year49Millis} ]`;
  }
};

const processTierFilters = (categories) => {
  const tiers = [];
  let lastTierIndex;
  categories.forEach((cat, i) => {
    if (cat.includes('Tier')) {
      tiers.push(getDateRange(cat));
      lastTierIndex = i;
    }
  });
  return { tiers, lastTierIndex };
};

const getWEGCategoriesQuery = (categories) => {
  const cleanCats = [];
  categories.forEach((cat) => {
    const validCat = getValidWEGCategory(cat);
    if (validCat) cleanCats.push(validCat);
  });
  return cleanCats.length
    ? `+categories:${cleanCats.join(' +categories:')}`
    : null;
};

const getWEGDateOfIntroQuery = (dateRanges) => {
  let tierParams = '';
  if (dateRanges.length > 0) {
    for (let i = 0; i < dateRanges.length; i++) {
      const tier = dateRanges[i];
      if (i === 0) {
        tierParams += tier;
      } else {
        tierParams += ` OR ${tier}`;
      }
    }
    return `+WegCard.dateOfIntroduction:(${tierParams})`;
  }
};

const getDateOEIdentifiers = (categories) => {
  const identifiers = [];
  categories.forEach((category) => {
    if (category.includes('OE ')) identifiers.push(category.slice(3));
  });
  return identifiers;
};

const getValidWEGCategory = (category) => {
  if (category.includes('ORIGIN ')) return category.slice(7);
  if (category.includes('PRO ')) return category.slice(4);
  if (category.includes('ENV ')) return category.slice(4);
  if (category.includes('DOM ')) return category.slice(4);
  return false;
};

export const getWEGSubNav = () => {
  const url = `${BASE_URL}dotcms/api/subnav/weg`;
  return axios.get(url).then(
    (res) => res,
    (err) => err
  );
};

export const createParamList = (params) => {
  console.info("createParamList, params:");
  console.dir(params);
  const { categories, authdViewLive } = params;
  const paramList = ['+contentType:WegCard'];
  if (authdViewLive) {
    paramList.push('+live:true');
  }
  paramList.push('+deleted:false');
  const { tiers, lastTierIndex } = processTierFilters(categories);
  const cats = [...categories.slice(lastTierIndex + 1)];
  const dateOfIntroQuery = getWEGDateOfIntroQuery(tiers);
  if (dateOfIntroQuery) paramList.push(dateOfIntroQuery);
  const categoriesQuery = getWEGCategoriesQuery(cats);
  if (categoriesQuery) paramList.push(categoriesQuery);
  console.info("createParamList, returning paramList:");
  console.dir(paramList);
  return paramList;
};

/**
 * Retrieve a list of WEG assets according to the selected categories
 */
export const filterWEG = (params, linkMap) => {
  const {
    categories,
    offset,
    limit,
    paramList: givenParamList,
    transform = true,
  } = params;

  console.info("filterWEG params:");
  console.dir(params);

  const { lastTierIndex } = processTierFilters(categories);
  const cats = [...categories.slice(lastTierIndex + 1)];
  const paramList = givenParamList ?? createParamList(params);
  let relatedQuery = '';
  const oeIds = getDateOEIdentifiers(cats);
  if (oeIds.length)
    relatedQuery = `WegCard.dateProliferation:${oeIds.join(',')}`;
  try {
    return axios
      .post(`${API_URL}_search${relatedQuery}`, {
        limit,
        offset,
        query: paramList.join(' ') + relatedQuery,
        sort: 'score',
      })
      .then((response) => {
        const { entity } = response.data;
        console.info("filterWEG axios relatedQuery response entity:");
          console.dir(entity);
        if (transform) {
          console.info("filterWEG transforming:");
          console.dir(params);
          return transformWEGlist(
            entity.jsonObjectView,
            offset,
            entity.resultsSize
          );
        } else {
          return entity.jsonObjectView;
        }
      })
      .catch(() => transformWEGlist());
  } catch (err) {
    console.log("Online but couldn't reach dotCMS");
  }

};

/**
 * Retrieve specific WEG asset information
 */
export const getAsset = (name, authdViewLive = true) => {
  console.info('weg.js getAsset name:');
  console.dir(name);
  const isIdentifier = /^[a-zA-Z0-9]{32}$/.test(name);
  if (isIdentifier) return getAssetByInode(name, '', 'id', authdViewLive);

  const searchName = name.replace(/\//g, '%2F');

  const paramList = [
    '+contentType:WegCard',
    `+title:${encodeURIComponent(searchName)}`,
  ];

  if (authdViewLive) {
    paramList.push('+live:true');
  }

  const url = `${API_URL}_search/`;
  return axios
    .post(url, {
      query: paramList.join(' '),
      depth: 3,
      sort: 'score',
    })
    .then(
      (response) => transformAsset(response.data.entity.jsonObjectView, name),
      (error) => error
    );
};

export const getAssetByInodeFromDotCMS = (
  inode,
  searchName = '',
  param = 'inode',
  authdViewLive = true
) => {
  if (!authdViewLive) {
    const url = `${API_URL}_search/`;
    return axios
      .post(url, {
        query: `+identifier:${inode}`,
        sort: 'score',
      })
      .then(
        (response) => {
          if (response.data.entity.jsonObjectView.contentlets.length >= 1) {
            return transformAsset(response.data.entity.jsonObjectView);
          }
        },
        (error) => error
      );
  } else {
    return axios.get(`${API_URL}${param}/${inode}`).then(
      (response) => transformAsset(response.data, searchName),
      (error) => error
    );
  }
};

const getCategoryUrl = (catKey) => {
  return `${BASE_URL}dotcms/api/categories?parent=${catKey}`;
};

export const getLandCategories = () => {
  const domUrl = getCategoryUrl('domain');
  return axios.get(domUrl).then(
    (res) => {
      for (const cat of res.data) {
        const { name, key } = cat;
        if (name.toLowerCase() === 'land') {
          const landUrl = getCategoryUrl(key);
          return axios.get(landUrl).then(
            (landRes) => landRes.data,
            (err) => err
          );
        }
      }
    },
    (err) => err
  );
};
