import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import { DebounceInput } from 'react-debounce-input';
import { withRouter } from 'react-router-dom';

import { searchByTitle } from 'API';

import { getSearchResultPath } from 'Utils/LinkGeneration';
import { setUrl } from 'Utils/Router';

import Button from 'UI/Button';
import { getOnlineStatus } from 'dataAccess';

import './SearchBox.css';

const { VITE_ctc } = import.meta.env;
let ctc = true;
if (VITE_ctc === 'false') ctc = false;

/**
 * Display Search Box and forward to appropriate location
 */
const SearchBox = ({ section, regionCodeName, history }) => {
  const rootNode = useRef(null);
  const isMounted = useRef(false);

  const [searchString, setSearchString] = useState('');
  const [autocompleteItems, setAutocompleteItems] = useState([]);
  const [isFocused, setIsFocused] = useState(false);

  // Track component mount status
  useEffect(() => {
    isMounted.current = true;
    return () => {
      isMounted.current = false;
    };
  }, []);

  useEffect(() => {
    const checkForOutsideClick = (event) => {
      if (rootNode.current && rootNode.current.contains(event.target)) {
        return;
      }
      setIsFocused(false);
    };

    document.addEventListener('click', checkForOutsideClick, false);
    return () => {
      document.removeEventListener('click', checkForOutsideClick, false);
    };
  }, []);

  // Clear out the search bar when switching sections
  useEffect(() => {
    setSearchString('');
    setAutocompleteItems([]);
    setIsFocused(false);
  }, [section]);

  const runSearch = () => {
    if (!searchString) return;
    const routeString = setUrl('Search', {
      section,
      regionCodeName,
      searchString,
    });
    history.push(routeString);
  };

  const handleKeyPress = (event) => {
    if (event.charCode === 13) runSearch();
  };

  const handleItemClick = (item) => {
    setSearchString('');
    setAutocompleteItems([]);
    const linkPath = getSearchResultPath(section, item);
    history.push(linkPath);
  };

  const searchPredict = (event) => {
    console.info('searchPredict event:');
    console.dir(event);
    let localSection = section;
    const searchStringValue = event.target.value;
    setSearchString(searchStringValue);

    if (searchStringValue && searchStringValue !== '') {
      if (localSection === 'home' || localSection === 'Search') {
        localSection = null;
      }
      const params = {
        searchString: searchStringValue,
        section: localSection,
        regionCodeName,
      };
      searchByTitle(params).then((response) => {
        getOnlineStatus().then((online) => {
          console.info('searchPredict response:');
          console.dir(response);
          if (isMounted.current) {
            if (!online) setAutocompleteItems(response.wegList);
            else setAutocompleteItems(response.contentlets);
          }
        });
      });
    } else {
      setAutocompleteItems([]);
    }
  };

  const renderAutocompleteItems = () => {
    if (isFocused && autocompleteItems && autocompleteItems.length > 0) {
      const items = autocompleteItems.map((item) => (
        <Button
          key={item.inode}
          title={item.title}
          click={() => handleItemClick(item)}
        />
      ));
      return <div className="autocomplete-items">{items}</div>;
    }
    return null;
  };

  let placeholder = '';

  if (section) placeholder = `Search ${section}`;
  if (section === 'All' || section === 'Search') {
    placeholder = 'Search ODIN';
  }
  if (section && section.toLowerCase() === 'weg') {
    placeholder = 'Search Worldwide Equipment Guide';
    if (ctc) placeholder = 'Search CTC Equipment Guide';
  }

  return (
    <div
      className="search-box"
      ref={rootNode}
    >
      <div className="search-box-content">
        <Button
          title={placeholder}
          icon="search"
          click={runSearch}
          className="search-button"
          hideTitle
        />
        <DebounceInput
          type="text"
          placeholder={placeholder}
          value={searchString}
          onChange={searchPredict}
          onKeyPress={handleKeyPress}
          debounceTimeout={200}
          onFocus={() => setIsFocused(true)}
        />
      </div>
      {renderAutocompleteItems()}
    </div>
  );
};

// Define the data type for the properties passed to the component
SearchBox.propTypes = {
  /** The section to search in */
  section: PropTypes.string,
  regionCodeName: PropTypes.string,
  history: PropTypes.object.isRequired,
};

// Set default values for optional properties
SearchBox.defaultProps = {
  section: null,
  regionCodeName: null,
};

export default withRouter(SearchBox);
