import React, { createContext, useState } from 'react';
import { useRegisterSW } from 'virtual:pwa-register/react';

// Optionally, import your build date or version
// You can define this in your vite.config.js

export const UpdateContext = createContext();

export const UpdateProvider = ({ children }) => {
  const [needRefresh, setNeedRefresh] = useState(false);
  const [offlineReady, setOfflineReady] = useState(false);
  const [updateDismissed, setUpdateDismissed] = useState(false);

  const buildDate = __BUILD_DATE__; // We'll define this shortly

  const { updateServiceWorker } = useRegisterSW({
    swUrl: `sw.js?version=${buildDate}`,
    onNeedRefresh() {
      console.log('onNeedRefresh called');
      setNeedRefresh(true);
    },
    onOfflineReady() {
      console.log('setOfflineReady called');
      setOfflineReady(true);
    },
    onRegistered(registration) {
      console.log('Service Worker registered:', registration);
    },
    onRegisterError(error) {
      console.error('Service Worker registration error:', error);
    },
  });

  const handleDismiss = () => {
    setUpdateDismissed(true);
    console.log('Update dismissed');
  };

  const refreshPage = () => {
    if (updateServiceWorker) {
      updateServiceWorker(true);
    }
    window.location.reload();
  };

  return (
    <UpdateContext.Provider
      value={{
        needRefresh,
        refreshPage,
        updateDismissed,
        handleDismiss,
        offlineReady,
      }}
    >
      {children}
    </UpdateContext.Provider>
  );
};
