// Dependencies
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

// UI
import Button from 'UI/Button';

// Utils
import { setUrl } from 'Utils/Router';

// Styles
import './Share.css';

/**
 * Render a button and options to allow the user to Share content
 */
class Share extends Component {
  state = {};

  /**
   * copy the url of the current page to the clipboard
   */
  copyToClipboard = () => {
    if (!this.textArea) {
      return;
    }
    this.textArea.select();
    document.execCommand('copy');
  };

  render() {
    const { isOpen, togglePopup, refName, nodeRef, section, itemTitle } =
      this.props;
    let url = window.location.href;
    if (section === 'WEG') {
      url = setUrl(section, { type: 'Asset', page: itemTitle });
      url = `${window.location.protocol}//${window.location.host}${url}`;
    }
    return (
      <div ref={nodeRef} className="share-page">
        <Button
          className={isOpen ? 'active' : ''}
          title="Share"
          icon="Share"
          click={() => togglePopup(refName)}
          hideTitle
          hideTooltip
        />
        {isOpen && (
          <div className="share-popup">
            <h3>Share URL:</h3>
            <input
              type="text"
              ref={(textArea) => {
                this.textArea = textArea;
              }}
              value={url}
              rows={1}
              readOnly
            />
            <Button
              className="copy"
              title="Copy to Clipboard"
              click={this.copyToClipboard}
              hideTooltip
            />
          </div>
        )}
      </div>
    );
  }
}

// Define the data type for each property passed to the component
Share.propTypes = {
  /**
   * Reference to the feedback node. Used to determine if anything
   * was clicked outside of the component
   */
  nodeRef: PropTypes.func.isRequired,
  /** Name of the reference used in the parent component */
  refName: PropTypes.string.isRequired,
  /** Close the modal from the outside */
  isOpen: PropTypes.bool,
  /** Passed function for opening the pop up */
  togglePopup: PropTypes.func,
  /** The section that this share component in */
  section: PropTypes.string,
  /** The title of the item to be shared */
  itemTitle: PropTypes.string,
};

// Set default values for optional properties
Share.defaultProps = {
  isOpen: false,
  togglePopup: (event) => console.log('Toggle the feedback popup:', event),
  section: '',
  itemTitle: '',
};

export default withRouter(Share);
