import React, {
  Fragment,
  useState,
  useEffect,
  useRef,
  useContext,
} from 'react';
import { withRouter } from 'react-router-dom';
import RouterPropTypes from 'react-router-prop-types';
import axios from 'axios';

import { createParamList, filterWEG, getAsset } from 'API';

import { getParams, setUrl } from 'Utils/Router';

import Layout from 'UI/Layout';
import Loading from 'UI/Loading';
import Scrollbars from 'UI/Scrollbars';
import Modal from 'UI/Modal';

import SplashPage from 'Components/SplashPage';
import WegSubNavigation from 'Components/WegSubNavigation';
import WEGAssetCard from 'Components/WEGAssetCard';
import AssetDetail from 'Components/AssetDetail';
import WEGOptions from 'Components/WEGOptions';
import { transformWEGlist } from 'Utils/Transforms/DotCMS';
import InstallModal from 'Components/InstallModal';
import MilSymbol from 'milsymbol';

import './WEG.css';

import { AuthdViewLiveContext } from 'Context';
import { addMisc, getAssets, getMiscByKey, getOnlineStatus, parseQuery, searchAssetsWithFilters } from 'dataAccess';
import ImportManager from 'Components//ImportManager';

const BASE_URL = import.meta.env.VITE_ODIN_API_BASE_URL;

/**
 * Fetch, format, and display a Mediawiki WEG
 */
function WEG(props) {
  const { location, history } = props;

  const { authdViewLive } = useContext(AuthdViewLiveContext);

  const cardWidths = [
    { name: 'small', width: 192 },
    { name: 'medium', width: 288 },
    { name: 'large', width: 384 },
  ];

  const [display, setDisplay] = useState(null);
  const [totalHits, setTotalHits] = useState(0);
  const [cards, setCards] = useState([]);
  const [fetching, setFetching] = useState(false);
  const [resultsPerRow, setResultsPerRow] = useState(4);
  const [resultsPerFetch, setResultsPerFetch] = useState(4);
  const [cardWidth, setCardWidth] = useState(cardWidths[2]);
  const [stopFetchingList, setStopFetchingList] = useState(false);
  const [isFullScreen, setIsFullScreen] = useState(false);
  const [linkMap, setLinkMap] = useState({});
  const [paramList, setParamList] = useState([]);
  const [listView, setListView] = useState(undefined);
  const [showImportModal, setShowImportModal] = useState(false);
  const [showClearModal, setShowClearModal] = useState(false);
  const [showInstallmodal, setShowInstallModal] = useState(false);
  const [showCancel, setShowCancel] = useState(false);
  const [prevImportModal, setPrevImportModal] = useState(null);
  const [assetsCompleted, setAssetsCompleted] = useState(0);
  const [totalAssets, setTotalAssets] = useState(-1);

  const wegListRef = useRef(null);
  const scrollbarRef = useRef(null);

  const fetchAtPercentage = 0.75;


  const cancelPrompt = (
    <div id="cancel-wrapper">
      <div id="cancel-modal">
        <h2>Install Instructions</h2>
        <p>
          To view information on WEG Application again, use the "WEG Application" link in the bottom of this page.
        </p>
        <button onClick={() => handlePromptExit()}>Okay</button>
      </div>
    </div>
  );

  const handleInstallModalClose = () => {
    console.log('handleInstallModalClose!');
    // getMiscByKey('showInstructions').then(res => {
    //   console.info('getMiscByKey showInstructions res:');
    //   console.dir(res);
    //   if (!res) {
    //     console.log('!res')
    //     setShowCancel(true);
    //   }
    //   else if (res && !res.value) {
    //     console.log('res && res.value');
    //   } else {
    //     console.log('else');
    //     setShowCancel(true);
    //   }
    // })
    setShowInstallModal(false);

  }

  const handlePromptExit = () => {
    // addMisc('showInstructions', false);
    setShowCancel(false);
  }

  const handleModalOpen = (currentImportModal) => {
    if (currentImportModal != prevImportModal) {
      setAssetsCompleted(0);
      setTotalAssets(-1);
    }
    setPrevImportModal(currentImportModal)
  }

  useEffect(() => {
    const handleResize = () => {
      updateWidths();
    };

    window.addEventListener('resize', handleResize);

    const openImportModal = () => {
      handleModalOpen('import');
      console.log('openImportModal in WEG.jsx');
      setShowImportModal(true);
    };
    window.addEventListener('openImportModal', openImportModal);

    const openClearModal = () => {
      console.log('openClearModal in WEG.jsx');
      handleModalOpen('clear');
      setShowClearModal(true);
    };
    window.addEventListener('openClearModal', openClearModal);

    const openInstallModal = () => {console.log('openInstallModal in WEG.jsx'); setShowInstallModal(true)};
    window.addEventListener('openInstallModal', openInstallModal);

    return () => {
      window.removeEventListener('resize', handleResize);
      window.removeEventListener('openImportModal', openImportModal);
      window.removeEventListener('openClearModal', openClearModal);
      window.removeEventListener('openInstallModal', openInstallModal);
    };
  }, []);

  useEffect(() => {

  }, [prevImportModal])

  useEffect(() => {
    fetchAssets(0, 20, true);

    if (location.search === '?fullscreen=true') {
      setIsFullScreen(true);
    }

    updateWidths();

    // getMiscByKey('showInstructions').then(res => {
    //   console.info('getMiscByKey showInstructions res:');
    //   console.dir(res);

    //   const showInstructions = res ? res.value : true;

    //   console.info('showInstructions:', showInstructions);

    //   setShowInstallModal(showInstructions)
    // })
  }, []);

  useEffect(() => {
    setStopFetchingList(false);
    fetchAssets(0, 20, true);
    updateWidths();
  }, [location.key]);


  /**
   * Get the list of assets to display as cards
   */
  const fetchAssets = async (offset, limit, newList) => {
    if (newList) {
      setDisplay(<Loading />);
      setFetching(true);
      setCards([]);
    }
    // console.info("fetchAssets location:");
    // console.dir(location);

    const categories = getParams(location);
    const toSearch = categories.page ? categories.page.split('&') : [];
    const pageName = toSearch.length ? toSearch[toSearch.length - 1] : '';
    // console.info("fetchAssets categories:");
    // console.dir(categories);
    // console.info("fetchAssets linkMap:");
    // console.dir(JSON.stringify(linkMap));
    // console.info("fetchAssets toSearch:");
    // console.dir(toSearch);
    // console.info("fetchAssets pageName:");
    // console.dir(pageName);

    const online = await getOnlineStatus().then((online) => online)
    console.info("fetchAssets online:", online);

    switch (categories.type) {
      case 'Asset':
        // console.log("fetchAssets switch case: Asset");
        if (online) {
          // Online: Fetch from API and store in IndexedDB
          try {
            const response = await getAsset(pageName, authdViewLive);
            // console.log("fetchAssets switch case: Asset ONLINE");
            autoModal(response);
          } catch (error) {
            console.error('Error fetching asset:', error);
            // Optionally, fallback to IndexedDB
          }
        } else {
          // Offline: Retrieve from IndexedDB
          // console.log("fetchAssets switch case: Asset OFFLINE");
          const assets = await getAssets();
          const asset = assets.find((item) => item.identifier === pageName);
          if (asset) {
            autoModal(asset);
          } else {
            console.warn('Asset not found in IndexedDB:', pageName);
            // Handle asset not found offline
          }
        }
        break;

      case 'List':
        // console.log("fetchAssets switch case: List");
        if (!stopFetchingList || newList) {
          const paramListResult = createParamList({
            categories: toSearch,
            offset,
            limit,
            authdViewLive,
          });
          if (online) {
            // Online: Fetch from API and store? in IndexedDB
            // console.log("fetchAssets switch case: List ONLINE");
            try {
              if ( !stopFetchingList || newList ) {
                const newParamList = createParamList({
                  categories: toSearch,
                  offset,
                  limit,
                  authdViewLive,
                });
                filterWEG({
                  categories: toSearch,
                  offset,
                  limit,
                  authdViewLive,
                }, linkMap ).then(
                  ( response ) => {
                    setDisplay(null);
                    setParamList(newParamList);
                    // console.info("calling finalizeWEG while online")
                    finalizeWEG( response );
                  },
                );
              }
            } catch (error) {
              console.error('Error fetching assets:', error);
              // Optionally, fallback to IndexedDB
            }
          } else {
            // Offline: Search using Fuse.js
            // console.log("fetchAssets switch case: List OFFLINE");
            // console.dir(paramListResult);
            const query = parseQuery(paramListResult);
            // console.log("query is:", JSON.stringify(query));
            const assets = await searchAssetsWithFilters(query, offset, limit);
            // console.info("manually calling transformWEGlist")
            const transformed = transformWEGlist({contentlets: assets.wegList}, offset, assets.totalHits, online)
            setDisplay(null);
            setParamList(paramListResult);
            // console.info("calling finalizeWEG while OFFLINE")
            finalizeWEG(transformed);
        }
        }
        break;

      default:
        console.log("fetchAssets switch case: default");
        {
          const params = ['+contentType:WegCard'];
          if (authdViewLive) {
            params.push('+live:true');
          }
          params.push('+deleted:false');
          setDisplay(<SplashPage section="WEG" />);
          setCards([]);
          setTotalHits(0);
          setParamList(params);
        }
        break;
    }
  };

  /**
   * Display a hidden modal button that automatically opens a modal,
   * displaying an asset.
   */
  const autoModal = (data) => {
    // console.info("WEG autoModal data:");
    // console.dir(data);
    const returnUrl = setUrl('WEG', {
      type: 'List',
      page: data.name,
    });

    const modalContent = (
      <Fragment>
        <div style={{ display: 'none' }}>
          <Modal
            open={() => ({
              section: 'WEG',
              title: data.title,
              children: (
                <AssetDetail
                  assetName={data.title}
                  data={data}
                  isFullScreen={isFullScreen}
                  id={data.identifier}
                />
              ),
              modalStyle: isFullScreen
                ? { width: '100%', height: '100%' }
                : { width: '83%', height: '83%' },
            })}
            autoOpen
            close={() => {
              history.push(returnUrl);
            }}
            identifier={data.id}
          >
            <div />
          </Modal>
        </div>
      </Fragment>
    );

    setDisplay(modalContent);
  };

  /**
   * Displays the cards of all assets returned by filterWEG.
   * TODO: NOW ASYNC!
   */
  const finalizeWEG = async (response) => {
    const online = await getOnlineStatus().then((online) => online);
    // console.log("finalizeWEG response:")
    // console.dir(response);
    const { wegList, totalHits } = response;
    // console.log("finalizeWEG wegList:")
    // console.dir(wegList);
    // console.log("finalizeWEG totalHits:")
    // console.dir(totalHits);
    if (totalHits === 0) {
      setTotalHits(totalHits);
      return;
    }
    setFetching(false);

    setCards((prevCards) => {
      const newCardArray = [...prevCards];
      let allDuplicates = true;

      for (const currentCard of wegList) {
        let cardExists = false;
        for (const newCard of newCardArray) {
          if (newCard.id === currentCard.id) {
            cardExists = true;
            break;
          }
        }
        if (!cardExists) {
          newCardArray.push(currentCard);
          allDuplicates = false;
        }
      }

      if (allDuplicates) {
        setStopFetchingList(true);
      }

      return newCardArray;
    });

    setTotalHits(totalHits);
  };

  /**
   * Called when the WEGOptions component updates
   */
  const onOptionsUpdate = (options) => {
    const { resultsPerRow: newResultsPerRow, resultsPerFetch: newResultsPerFetch, listView: newListView } = options;

    if (
      newResultsPerRow !== resultsPerRow ||
      newResultsPerFetch !== resultsPerFetch ||
      newListView !== listView
    ) {
      setResultsPerRow(newResultsPerRow);
      setResultsPerFetch(newResultsPerFetch);
      setListView(newListView);
    }
  };

  /**
   * Check if we are at the bottom of the scrollbar.
   * If so, attempt to fetch more data
   */
  const updateScroll = (values) => {
    const difference = totalHits - cards.length;
    if (!fetching && difference > 0) {
      // Fetch if we are at the bottom of the scrollbar
      const { scrollTop, scrollHeight, clientHeight } = values;
      const cutoffDistance = 50;
      // t will be greater than 1 if we are about to reach the bottom
      const t = (scrollTop + cutoffDistance) / (scrollHeight - clientHeight);
      if (t > 1) {
        setFetching(true);
        fetchAssets(cards.length, 20);
      }
    }
  };

  const updateWidths = () => {
    if (!wegListRef.current) return;

    const wIndex = cardWidths.indexOf(cardWidth);
    const wegListWidth = wegListRef.current.offsetWidth;

    if (wIndex > 0 && wegListWidth < cardWidth.width * resultsPerRow) {
      setCardWidth(cardWidths[wIndex - 1]);
    } else if (
      wIndex < cardWidths.length - 1 &&
      wegListWidth > cardWidths[wIndex + 1].width * resultsPerRow
    ) {
      setCardWidth(cardWidths[wIndex + 1]);
    }
  };

  const subNavigationHandler = (data) => {
    setLinkMap(data);
  };

  const handleCloseManager = () => {
    setShowImportModal(false);
    setShowClearModal(false);
  }

  // Footer options
  const footerOpts = {
    results: totalHits,
    section: 'WEG',
  };

  const symbol = (
    <img
      src={new MilSymbol.Symbol('S*APMHUM---F**N').toDataURL()}
      style={{
        backgroundColor: 'rgba(255,255,255,0.75)',
        width: `${110 - (10*resultsPerRow) }px`,
        height: `${110 - (10*resultsPerRow) }px`,
        borderRadius: '50%',
        position: 'absolute',
        zIndex: 1000,
        top: `1%`,
        left: `1%`
      }}
    />
  );

  return (
    <Layout footerOptions={footerOpts} isFullScreen={isFullScreen}>
      {showInstallmodal && (
        <InstallModal
          isFullScreen={isFullScreen}
          close={handleInstallModalClose}
        />
      )}

      {showCancel ? cancelPrompt : null}

      <div id="weg-page">
        <WegSubNavigation
          paramList={paramList}
          wegCallback={subNavigationHandler}
        />
        <div
          className={`weg-list${listView ? ' list-view' : ''}`}
          ref={wegListRef}
        >
          {' '}
          <img id="imageContainer" />
          <Scrollbars ref={scrollbarRef} onUpdate={updateScroll}>
            {display}
            {!display && !cards.length && (
              <div className="no-results">No Equipment Available</div>
            )}
            {!display &&
              cards.map((wegCard) => {
                return (
                  <div
                    key={wegCard.id}
                    className="weg-card-container"
                    style={{
                      position: 'relative',
                      width: `calc(90% / ${resultsPerRow})`,
                    }}
                  >
                    <WEGAssetCard
                      className={listView ? 'list' : cardWidth.name}
                      data={wegCard}
                    />
                    {/* {symbol} */}
                  </div>
                );
              })}

            <ImportManager
              assetsCompleted={assetsCompleted}
              setAssetsCompleted={setAssetsCompleted}
              totalAssets={totalAssets}
              setTotalAssets={setTotalAssets}
              showImportModal={showImportModal}
              showClearModal={showClearModal}
              close={handleCloseManager}
              isFullScreen={isFullScreen}
            />
            {!display && <WEGOptions onUpdate={onOptionsUpdate} />}
          </Scrollbars>
        </div>
      </div>
    </Layout>
  );
}

WEG.propTypes = {
  /**
   * Represents where the app is now to determine when navigation happens.
   * Provided by react-router-dom and the withRouter method
   */
  location: RouterPropTypes.location.isRequired,
  /**
   * Library that lets you easily manage session history anywhere JavaScript runs
   * Provided by react-router-dom and the withRouter method
   */
  history: RouterPropTypes.history.isRequired,
};

WEG.defaultProps = {};

export default withRouter(WEG);
