// Dependencies
import React from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';

// UI
import Scrollbars from 'UI/Scrollbars';

// Components
import SectionHeader from 'Components/SectionHeader';

/**
 * Render modal elements and append to the end of `document.body` in the DOM
 */
const modalContent = (props) => {
  const {
    close,
    content,
    onKeyDown,
    clickOutside,
    modalRef,
    showPrint,
    showShare,
    additionalButtons,
    showClose,
    isFullScreen,
    identifier,
  } = props;
  const { className, modalStyle, contentStyle, children, title, section } =
    content;

  return ReactDOM.createPortal(
    <div
      className="modal-overlay"
      aria-modal="true"
      tabIndex="-1"
      role="dialog"
      aria-label={title}
      onKeyDown={onKeyDown}
      onClick={clickOutside}
    >
      <div className={`modal ${className}`} style={modalStyle} ref={modalRef}>
        <SectionHeader
          title={title}
          modal
          modalClose={close}
          section={section}
          itemTitle={title}
          showPrint={showPrint}
          showShare={showShare}
          additionalButtons={additionalButtons}
          showClose={showClose}
          isFullScreen={isFullScreen}
          identifier={identifier}
        />

        <Scrollbars className="modal-content" style={contentStyle}>
          {children}
        </Scrollbars>
      </div>
    </div>,
    document.body
  );
};

// Define data type for properties passed to the component
modalContent.propTypes = {
  /** Passed method to close the modal */
  close: PropTypes.func.isRequired,
  /** The content to display in the modal */
  content: PropTypes.object.isRequired,
  /**
   * Reference to the feedback node. Used to determine if anything
   * was clicked outside of the component
   */
  modalRef: PropTypes.func.isRequired,
  /** Actions to take when a key is pressed while a modal is open */
  onKeyDown: PropTypes.func,
  /** Actions to take when the user clicks outside of the modal */
  clickOutside: PropTypes.func,
  /** Booleans for whether or not to show header buttons */
  showPrint: PropTypes.bool,
  showShare: PropTypes.bool,
  /** Additional components to render on the right side, if any */
  additionalButtons: PropTypes.object,
  showClose: PropTypes.bool,
  isFullScreen: PropTypes.bool,
  identifier: PropTypes.string,
};

// Set default values for optional properties
modalContent.defaultProps = {
  onKeyDown: (event) => console.log('key pressed:', event.keyCode),
  clickOutside: (event) => console.log('clicked outside modal:', event),
  showPrint: true,
  showShare: true,
  additionalButtons: null,
  showClose: true,
  isFullScreen: false,
  identifier: null,
};

export default modalContent;
