// Dependencies
import PropTypes from 'prop-types';
import React, { Component, Fragment } from 'react';

// UI
import Button from 'UI/Button';
import Scrollbars from 'UI/Scrollbars';

// Components
import AssetImages from 'Components/AssetImages';
import AssetTabs from 'Components/AssetTabs';

// API
import { IndexedDBImage } from 'Components/IndexedDBImage';
import { getTierFromDateOfIntro } from 'Utils/Transforms/DotCMS';
import { getAssetByInode, getOnlineStatus } from 'dataAccess';

// Utils
import { getUID } from 'Utils/UID';

// Styles
import './AssetDetail.css';

import { AuthdViewLiveContext } from 'Context';

const BASE_URL = import.meta.env.VITE_ODIN_API_BASE_URL;

/**
 * Format and display WEG Asset detail information
 */
class AssetDetail extends Component {
  static contextType = AuthdViewLiveContext;

  state = {
    tabs: null,
    activeTab: 0,
    images: [
      {
        name: '',
        url: '',
      },
    ],
    notes: null,
    tiers: [],
    activeImage: null,
    lastActiveImage: null,
    activeImageDivWidth: 0,
    isOnline: false,
  };

  activeImageDiv = null;

  componentDidMount() {
    const { isOnline } = this.state;

    getOnlineStatus().then(online =>  {
      if (isOnline !== online ) this.setState({isOnline: online})
    });
    window.addEventListener('resize', () => {
      this.updateWidths();
    });
    const { assetName, data, id } = this.props;
    console.info('AssetDetail mount data:')
    console.dir(data);
    console.info('AssetDetail mount props:')
    console.dir(this.props);
    console.info('AssetDetail mount state:')
    console.dir(this.state);
    // if (typeof data.images === "string") data.images = JSON.parse(data.images);
    /**
     * If we already got data as a prop, go ahead and display it. If not,
     * get it from the backend and then display it.
     */
    if (data) {
      console.log('We got data as a prop');
      this.displayAsset(data);
      return;
    }

    const { authdViewLive } = this.context;
    getAssetByInode(id, assetName, 'id', authdViewLive).then((response) => {
      this.displayAsset(response);
    });
  }

  componentDidUpdate() {
    const { isOnline } = this.state;
    console.info('AssetDetail componentDidUpdate props:')
    console.dir(this.props);
    console.info('AssetDetail componentDidUpdate state:')
    console.dir(this.state);

    this.updateWidths();
    getOnlineStatus().then(online =>  {
      if (isOnline !== online ) this.setState({isOnline: online})
    });
  }

  componentWillUnmount() {
    window.removeEventListener('resize', () => {
      this.updateWidths();
    });
  }

  /**
   * Display the called asset using data supplied.
   */
  displayAsset = (data) => {
    console.info('displayAsset, data:');
    console.dir(data);
    const tabs = [];
    // eslint-disable-next-line max-len, no-extra-boolean-cast
    const proliferationProperty = {
      name: 'Proliferation',
      value: !!data.proliferation ? data.proliferation : 'Not Entered',
    };
    console.info('proliferationProperty');
    console.dir(proliferationProperty);
    // eslint-disable-next-line max-len, no-extra-boolean-cast
    const originProperty = {
      name: 'Country Of Origin',
      value: !!data.countryOfOrigin ? data.countryOfOrigin : 'Not Entered',
    };
    // eslint-disable-next-line max-len, no-extra-boolean-cast
    const dateOfIntroProperty = {
      name: 'Date of Introduction',
      value: !!data.dateOfIntroduction
        ? data.dateOfIntroduction.substring(0, 4)
        : 'Not Entered',
    };

    let hasSystemTab = false;
    if (data.sections && data.sections.length) {
      const sections = JSON.parse(data.sections);
      sections.forEach((section) => {
        if (section.name === 'System') {
          hasSystemTab = true;
          if (!section.properties) {
            section.properties = [];
          }
          // eslint-disable-next-line max-len, no-extra-boolean-cast
          section.properties.unshift(proliferationProperty);
          // eslint-disable-next-line max-len, no-extra-boolean-cast
          section.properties.unshift(originProperty);
          // eslint-disable-next-line max-len, no-extra-boolean-cast
          section.properties.unshift(dateOfIntroProperty);
        }
        const newTab = {
          name: section.name,
          properties: section.properties,
          sections: section.sections,
        };

        tabs.push(newTab);
      });
    }
    if (!hasSystemTab) {
      tabs.unshift({
        name: 'System',
        properties: [
          dateOfIntroProperty,
          originProperty,
          proliferationProperty,
        ],
      });
    }

    if (data.variants && data.variants.length > 0) {
      const variantTab = {
        name: 'Variants',
        properties: data.variants.map((variant) => ({
          name: variant.name,
          value: variant.notes,
        })),
      };

      tabs.push(variantTab);
    }

    this.setState({
      tabs,
      notes: data.notes,
      tiers: [getTierFromDateOfIntro(data.dateOfIntroduction)],
      images: data.images.map((img) => ({
        name: img.name,
        url: (BASE_URL.startsWith("https://") ? "https://" : "") + img.url.split("https://").pop().replace(/\/\/+/g, '/')
      })),
    });
  };

  /**
   * Display the notes section of the asset in a scrollable view.
   */
  renderNotes = (notes) => (
    <div className="notes element">
      <div className="header">Notes</div>
      <Scrollbars>
        <div className="content">{notes}</div>
      </Scrollbars>
    </div>
  );

  /**
   * Display the details for an individual asset
   */
  renderDetails = (tab) => (
    <div className="details element">
      <Scrollbars>
        <div className="content">
          {tab.properties ? this.renderProperties(tab.properties) : null}
          {tab.sections ? this.renderSections(tab.sections) : null}
          <div className="flex-grid-row">
            <div className="grid-cell" />
          </div>
        </div>
      </Scrollbars>
    </div>
  );

  renderDvidsLinkIfExists = (propertyValue) => {
    const dvidsRoot = 'https://www.dvidshub.net';
    if (propertyValue.includes(dvidsRoot)) {
      const valueChunks = propertyValue.split(' ');
      const fragChildren = valueChunks.map((chunk) => {
        if (chunk.includes(dvidsRoot)) {
          return (
            <a href={chunk} target="_blank" rel="noopener noreferrer">
              {chunk}{' '}
            </a>
          );
        }
        return `${chunk} `;
      });
      return <Fragment>{fragChildren}</Fragment>;
    }
    return propertyValue;
  };

  /**
   * Render a set of properties from the data passed to props. This can be within
   * a section, or on its own.
   */
  renderProperties = (properties) => {
    const propList = [];
    properties.forEach((prop) => {
      if (prop.isVisible !== undefined && !prop.isVisible) return;
      propList.push(prop);
    });
    return (
      <Fragment>
        {propList.map((property) => {
          let { value } = property;
          if (property.units) {
            value += ` ${property.units}`;
          }
          return (
            <div className="flex-grid-row" key={getUID(property.name)}>
              <div className="grid-cell">{property.name}</div>
              <div className="grid-cell">
                {this.renderDvidsLinkIfExists(value)}
              </div>
            </div>
          );
        })}
      </Fragment>
    );
  };

  /**
   * Render a section from the data passed to props. A section is rendered
   * as a yellow header. If that section contains properties, render those too.
   */
  renderSections = (sections) => (
    <Fragment>
      {sections.map((section) => (
        <Fragment key={getUID(section.name)}>
          <div className="flex-grid-row">
            <div className="grid-cell header-container">
              <div className="header">{section.name}</div>
              <div className="spacer" />
            </div>
            <div className="grid-cell" />
          </div>
          {section.properties
            ? this.renderProperties(section.properties)
            : null}
        </Fragment>
      ))}
    </Fragment>
  );

  /**
   * If there is not a currently active image (user has not clicked on an image
   * on the left side) then display the tabs with details below. Otherwise,
   * display the image only with a 'Close' button.
   */
  renderRightPanel = () => {
    const { tabs, activeTab, activeImage, activeImageDivWidth } = this.state;

    const width = Math.max(activeImageDivWidth + 64, 364);

    return (
      <div
        className="right"
        style={{
          width: activeImage
            ? `calc(100% - ${width}px)`
            : 'calc(100% - 356px - 8px)',
        }}
      >
        <AssetTabs tabs={tabs} activeTab={activeTab} click={this.tabHandler} />
        {tabs && tabs.length ? this.renderDetails(tabs[activeTab]) : null}
      </div>
    );
  };

  /**
   * This method is called when a tab in the AssetTabs component is clicked.
   */
  tabHandler = (tabIndex) => {
    this.setState({ activeTab: tabIndex });
  };

  /**
   * This method is called when the currently shown image on the left is clicked.
   */
  clickImageHandler = (imageIndex) => {
    const { images } = this.state;
    this.setState({
      activeImage: images[imageIndex],
      lastActiveImage: images[imageIndex],
    });
  };

  closeImageHandler = () => {
    this.setState({ activeImage: null, lastActiveImage: null });
  };

  /**
   * This method is called when the window is resized, to update the width of
   * the asset detail window accordingly.
   */
  updateWidths() {
    if (!this.activeImageDiv) return;
    const { activeImageDivWidth } = this.state;
    if (activeImageDivWidth !== this.activeImageDiv.offsetWidth) {
      this.setState({ activeImageDivWidth: this.activeImageDiv.offsetWidth });
    }
  }

  render() {
    const { notes, images, tiers, activeImage, lastActiveImage, isOnline } = this.state;
    console.info("AssetDetail isOnline:", isOnline);
    console.info("AssetDetail isOnline:");

    console.dir
    const { isFullScreen } = this.props;

    const lastUrl = lastActiveImage ? lastActiveImage.url : '';
    const lastName = lastActiveImage ? lastActiveImage.name : '';

    return (
      <div className="asset-detail">
        <div className="left">
          <div className={`expanded-image${activeImage ? '' : ' inactive'}`}>
            <div
              className="active-image element"
              ref={(element) => {
                this.activeImageDiv = element;
              }}
            >
              {isFullScreen && (
                <Button
                  title=""
                  icon="close"
                  click={() => {
                    this.setState({ activeImage: null });
                  }}
                />
              )}
              <div className="inner-image">
                <IndexedDBImage
                  src={activeImage ? activeImage.url : lastUrl}
                  alt={activeImage ? activeImage.name : lastName}
                  online={isOnline}
                />
                <Button title="" icon="close" click={this.closeImageHandler} />
              </div>
            </div>
          </div>
          <AssetImages
            images={images}
            tiers={tiers}
            clickImage={this.clickImageHandler}
          />
          {this.renderNotes(notes)}
        </div>
        {this.renderRightPanel()}
      </div>
    );
  }
}

// Define data types for each property passed to the component
AssetDetail.propTypes = {
  /** Data from the 'getAsset' method in API */
  data: PropTypes.object,
  /** Used for the modal title */
  assetName: PropTypes.string.isRequired,
  id: PropTypes.string.isRequired,
  isFullScreen: PropTypes.bool,
};

// Set default values for optional properties
AssetDetail.defaultProps = {
  data: null,
  isFullScreen: false,
};

export default AssetDetail;
