// Dependencies
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { IndexedDBImage } from 'Components/IndexedDBImage'
import { getOnlineStatus } from 'dataAccess';

// Assets
import './Image.css';

/**
 * Dynamically loads image with optional caption
 */
class Image extends Component {
  state = {
    imageFile: null,
    isOnline: false,
    calledOnce: false
  };

  componentDidMount() {
    const { isOnline } = this.state;
    getOnlineStatus().then(online =>  {
      if (isOnline !== online ) this.setState({isOnline: online})
    });
    const { file } = this.props;
    const images = import.meta.glob('/src/ui/Image/images/*', { eager: true });
    // console.log("Images mount images:")
    // console.dir(images);
    const path = `/src/ui/Image/images/${file}`;
    if (images[path]) {
      this.setState({ imageFile: images[path].default });
    }
  }

  componentDidUpdate() {
    const { isOnline, calledOnce } = this.state;
    if (calledOnce) getOnlineStatus().then(online =>  {
      if (isOnline !== online ) this.setState({isOnline: online})
        this.setState({calledOnce: true});
    });

  }

  render() {
    const { imageFile, isOnline } = this.state;
    const { border, caption, captionStyle, className, imgStyle, style, title } =
    this.props;
    // console.log("Image.jsx render imageFile:");
    // console.dir(imageFile);
    let captionText = null;
    if (caption)
      captionText = (
        <p className="caption" style={captionStyle}>
          {caption}
        </p>
      );
    return (
      <div
        className={`image ${className} ${border ? 'border' : ''}`}
        style={style}
      >
        <IndexedDBImage src={imageFile} alt={title} online={isOnline} style={imgStyle} />
        {captionText}
      </div>
    );
  }
}

// Define the data type for each property passed to the component
Image.propTypes = {
  /** Name of the image file to display */
  file: PropTypes.string.isRequired,
  /** `alt` attribute text */
  title: PropTypes.string.isRequired,
  /** Display a 1px border all around the image container in the current text color */
  border: PropTypes.bool,
  /** Text to display below the image */
  caption: PropTypes.node,
  /** Inline styles to apply to the caption */
  captionStyle: PropTypes.object,
  /** Additional classes to apply to the image */
  className: PropTypes.string,
  /** Inline styles to apply to the image tag */
  imgStyle: PropTypes.object,
  /** Inline styles to apply to the image wrapper */
  style: PropTypes.object,
};

// Set default values for optional properties
Image.defaultProps = {
  border: false,
  caption: '',
  captionStyle: {},
  className: '',
  imgStyle: {},
  style: {},
};

export default Image;
