/* eslint-disable */
import axios from 'axios';

import { API_URL } from 'API';
import { getOnlineStatus, searchAssetsWithFilters } from 'dataAccess';

const BASE_URL = import.meta.env.VITE_ODIN_API_BASE_URL;
const ES_SEARCH_URL = `${BASE_URL}dotcms/api/es/search`;

const replaceLuceneChars = (term) => {
  let retVal = term;
  retVal = retVal.replace(/\(/g, ' ');
  retVal = retVal.replace(/\)/g, ' ');
  retVal = retVal.replace(/\:/g, ' ');
  return retVal;
};

const getLuceneSearchString = (term) => {
  const trimmed = replaceLuceneChars(term).trim();
  const chunks = trimmed.split(' ');
  const wildChunks = chunks
    .map((chunk) => {
      return `*${chunk}*`;
    })
    .filter((el) => el != '**');
  return `(${wildChunks.join(' ')})`;
};

const getRegionTagName = (regionCodeName) => {
  return `date__${regionCodeName
    .toLowerCase()
    .replace(/[^\w-]/g, '_')
    .replace(/\s/g, '_')}`;
};

/**
 * Retrieve search results based on the passed search terms
 */
export const searchDotCMS = (params) => {
  console.info('searchDotCMS params:');
  console.dir(params)
  const { section, searchString, offset, limit, authdViewLive } = params;
  const luceneSearch = getLuceneSearchString(searchString);
  let contentType = '';
  let search = '';

  if (!section) {
    paramList.push('-contentType:FileAsset');
    contentType = '-contentType:FileAsset';
    search = `title:${luceneSearch}`;
  } else {
    switch (section) {
      case 'WEG':
        contentType = '+contentType:WegCard';
        search = `+(WegCard.name:${luceneSearch}^100 WegCard.notes:${luceneSearch} WegCard.sections:${luceneSearch})`;
        break;
      case 'TC':
        contentType = '+(contentType:TPArticle contentType:TPChapter)';
        search = `+(title:${luceneSearch}^100 TPArticle.plaintext:${luceneSearch} TPChapter.plaintext:${luceneSearch})`;
        break;
      case 'VOA':
        contentType = '+contentType:VOAArticle';
        search = `+(title:${luceneSearch}^100 VOAArticle.plaintext:${luceneSearch})`;
        break;
      case 'DATE':
        contentType =
          '+(contentType:DateArticle contentType:DateOE contentType:DateRegion)';
        search = `+(title:${luceneSearch}^100 DateArticle.plaintext:${luceneSearch} DateRegion.plaintext:${luceneSearch} DateOE.plaintext:${luceneSearch})`;
        break;
    }
  }

  if (authdViewLive) {
    search += '+live:true';
  }

  console.info('searchDotCMS axios body:');
  console.dir({
    limit,
    offset,
    query: `${contentType} ${search} +deleted:false`,
    sort: 'score',
  })

  return axios
    .post(`${API_URL}_search`, {
      limit,
      offset,
      query: `${contentType} ${search} +deleted:false`,
      sort: 'score',
    })
    .then((response) => {
      const { entity } = response.data;
      console.info('searchDotCMS axios response:');
      console.dir({
        contentlets: entity.jsonObjectView.contentlets,
        totalHits: entity.resultsSize,
      })

      return {
        contentlets: entity.jsonObjectView.contentlets,
        totalHits: entity.resultsSize,
      };
    })
    .catch(() => {});
};

/**
 * Search by title
 */
export const searchByTitle = async (params) => {
  const online = await getOnlineStatus({searchTerm: params});
  if (!online) return searchAssetsWithFilters(params);
  console.info('searchByTitle params:');
  console.dir(params)
  const { searchString } = params;
  let contentTypes = [];
  const luceneSearchString = getLuceneSearchString(searchString);
  let searchQuery = '';
  contentTypes = ['WegCard'];

  const contentTypeQuery = `+(${contentTypes
    .map((contentType) => {
      return `contentType:${contentType}`;
    })
    .join(' ')})`;

  const titleQuery = `+(${contentTypes
    .map((contentType) => {
      const titleField = contentType === 'WegCard' ? 'name' : 'title';
      return `${contentType}.${titleField}:${luceneSearchString}`;
    })
    .join(' ')})`;

  const paramList = [contentTypeQuery, titleQuery];
  if (searchQuery !== '') paramList.push(searchQuery);

  const url = `${API_URL}_search/`;
  const body = {
    limit: 0,
    offset: 0,
    query: `${paramList.join(' ')} +deleted:false`,
    sort: 'score',
  };
  return axios.post(url, body).then(
    (response) => response.data.entity.jsonObjectView,
    (error) => error
  );
};

/*
  RESPONSE is the response from /api/es/search
*/
const mergeSnippetsIntoContentlets = (response) => {
  const contentlets = response.contentlets;
  const esHits = response.esresponse[0].hits.hits;
  for (let i = 0; i < contentlets.length; i++) {
    const contentlet = contentlets[i];
    const hit = esHits[i];
    if (hit.highlight) {
      const contentType = contentlet.contentType.toLowerCase();
      const snippet = hit.highlight[`${contentType}.plaintext`][0];
      contentlet['snippet'] = snippet;
    }
  }
  return {
    contentlets: contentlets,
    totalHits: response.esresponse[0].hits.total.value,
  };
};

export const esSearchDotCMS = async (params) => {
  let { section, searchString, regionCodeName, limit, offset, authdViewLive } =
    params;
  if (section == 'TC|DATE|VOA|WEG_Resource') {
    section = 'ALL';
  }
  let contentType = '';
  let search = '';
  let highlightFields = {};

  searchString = getLuceneSearchString(searchString);

  switch (section) {
    case 'ALL':
      ({ contentType, search } = esSearchAllTypes(
        searchString,
        highlightFields
      ));
      break;
    case 'WEG_Resource':
      ({ contentType, search, highlightFields } =
        await esSearchWegResource(searchString));
      break;
    case 'WEG':
      ({ contentType, search } = esSearchWeg(searchString));
      break;
    case 'DATE':
      ({ contentType, search, highlightFields } = esSearchDate(searchString));
      break;
    case 'Region':
      ({ contentType, search, highlightFields } = esSearchRegion(
        regionCodeName,
        searchString
      ));
      break;
    case 'VOA':
      ({ contentType, search, highlightFields } = esSearchVOA(searchString));
      break;
    case 'TC':
      ({ contentType, search, highlightFields } = esSearchTC(searchString));
      break;
    case 'How-To':
      ({ contentType, search, highlightFields } = esSearchHowTo(searchString));
      break;
  }

  let authdViewLiveParam = '';
  if (authdViewLive) {
    authdViewLiveParam = '+live:true';
  }

  const query = {
    query: {
      query_string: {
        query: `${contentType} ${search} ${authdViewLiveParam} +deleted:false`,
      },
    },
    highlight: {
      pre_tags: ['<span class="searchmatch">'],
      post_tags: ['</span>'],
      encoder: 'html',
      fragment_size: 200,
      no_match_size: 200,
      number_of_fragments: 1,
      fields: highlightFields,
    },
    sort: ['_score'],
    size: limit,
    from: offset,
  };

  const config = {
    headers: {
      'Content-Type': 'application/json',
    },
  };
  return axios.post(ES_SEARCH_URL, query, config).then(
    (response) => mergeSnippetsIntoContentlets(response.data),
    (error) => error
  );
};

const esSearchHowTo = (searchString) => {
  let contentType = '+(contentType:HowTo)';
  let search = `+(HowTo.title:${searchString} OR HowTo.plaintext:${searchString})`;
  let highlightFields = {
    'HowTo.plaintext': {},
  };
  return { contentType, search, highlightFields };
};

const esSearchTC = (searchString) => {
  let contentType = `+(contentType: TPArticle OR contentType: TPChapter)`;
  let search = `+(TPArticle.title:${searchString}^100 OR TPArticle.plaintext:${searchString}^2 OR TPChapter.title:${searchString}^100 OR TPChapter.plaintext:${searchString}^2)`;
  let highlightFields = {
    'TPArticle.plaintext': {},
    'TPChapter.plaintext': {},
  };
  return { contentType, search, highlightFields };
};

const esSearchVOA = (searchString) => {
  let contentType = `+(contentType:VOAArticle)`;
  let search = `+(VOAArticle.title:${searchString}^100 OR VOAArticle.plaintext:${searchString}^2)`;
  let highlightFields = {
    'VOAArticle.plaintext': {},
  };
  return { contentType, search, highlightFields };
};

const esSearchRegion = (regionCodeName, searchString) => {
  let contentType = '+(contentType:DateOE contentType:DateArticle)';
  const regionTag = getRegionTagName(regionCodeName);
  let search = `+(DateArticle.tags:${regionTag} DateOE.tags:${regionTag}) +(DateArticle.title:${searchString}^100 DateOE.title:${searchString}^100 DateArticle.plaintext:${searchString}^2 DateOE.plaintext:${searchString}^2)`;
  let highlightFields = {
    'DateArticle.plaintext': {},
    'DateOE.plaintext': {},
  };
  return { contentType, search, highlightFields };
};

const esSearchDate = (searchString) => {
  let contentType =
    '+(contentType:DateArticle contentType:DateOE contentType:DateRegion)';
  let search = `+(DateArticle.title:${searchString}^100 DateOE.title:${searchString}^100 DateRegion.title:${searchString}^100 DateArticle.plaintext:${searchString}^2 DateOE.plaintext:${searchString}^2 DateRegion.plaintext:${searchString}^2)`;
  let highlightFields = {
    'DateArticle.plaintext': {},
    'DateOE.plaintext': {},
    'DateRegion.plaintext': {},
  };
  return { contentType, search, highlightFields };
};

const esSearchWeg = (searchString) => {
  let contentType = '+(contentType:WegCard)';
  let search = `+(WegCard.name:${searchString}^100 WegCard.notes:${searchString}^2 WegCard.sections:${searchString})`;
  return { contentType, search };
};

const esSearchWegResource = async (searchString) => {
  const wegCatVar = await getHowToCategoryVar('WEG');
  let contentType = '+(contentType:HowTo)';
  let search = `+(+categories:${wegCatVar} HowTo.title:${searchString}^100 HowTo.plaintext:${searchString}^2)`;
  let highlightFields = {
    'HowTo.plaintext': {},
  };
  return { contentType, search, highlightFields };
};

const esSearchAllTypes = (searchString, highlightFields) => {
  const contentTypes = [
    'DateArticle',
    'DateRegion',
    'DateOE',
    'HowTo',
    'TPArticle',
    'TPChapter',
    'VOAArticle',
  ];
  let contentType = '+(';
  let search = '+(';
  contentTypes.forEach((type) => {
    contentType += `contentType:${type} `;
    search += `${type}.title:${searchString}^100 ${type}.plaintext:${searchString}^2 `;
    highlightFields[`${type}.plaintext`] = {};
  });
  contentType = `${contentType.trim()})`;
  search = `${search.trim()})`;
  return { contentType, search };
};
