async function enqueueAbortableFetch(arg, signal) {
  if (signal.aborted) {
    throw new Error('Aborted');
  }

  const result = await fetch(arg);

  if (signal.aborted) {
    throw new Error('Aborted');
  }

  return result;
}

async function compressImage(file, name, addImportantMessage, assetName) {
  const options = {
    maxSizeMB: 0.05, //  50KB
    maxWidthOrHeight: 800,
    useWebWorker: true,
    fileType: 'image/webp',
  };

  try {
    const compressedFile = await imageCompression(file, options);
    return compressedFile;
  } catch (error) {
    addImportantMessage(`${assetName}: Error compressing ${name}: ${error.message}`, 'error')
  }
}

export const getImage = async (id) => {
  const db = await initDB();
  const record = await db.get(IMAGE_STORE, id);
  if (!record) console.error("COULD NOT FIND THIS ID:", id);
  console.info("getImage id:", id);
  return record ? record.imageBlob : null;
};

export const hasImage = async (url, addImportantMessage, assetName) => {
  try {
    const db = await initDB();
    const key = await db.getKey(IMAGE_STORE, url);
    return key !== undefined;
  } catch (error) {
    addImportantMessage(`${assetName}: Error checking image in IndexedDB for URL ${url}: ${error.message}`, "error");
    return false;
  }
};


async function downloadAndStoreImagesInBatches(assets, addMessage, addImportantMessage, incrementAssetsCompleted, assetImagesLimit, abortSignal) {
  const queue = new PQueue({
    concurrency: 5, // Adjust as needed
    interval: 250, // Time window in milliseconds
    intervalCap: 5, // Number of downloads per interval
  });

  const promises = assets.map(asset => {
    return processAsset(asset, addMessage, addImportantMessage, incrementAssetsCompleted, assetImagesLimit, queue, abortSignal);
  });

  await Promise.all(promises);
}



const processAsset = async (asset, addMessage, addImportantMessage, incrementAssetsCompleted, assetImagesLimit, queue, abortSignal) => {
  if (abortSignal.aborted) {
    queue.clear();
    throw new Error('Aborted');
  }
  addMessage(`Processing ${asset.name}`);
  console.log("processImages processAsset");
  console.dir(asset.name);
  const images = JSON.parse(asset.images);

  const imagesToProcess = images.slice(0, assetImagesLimit);

  await processImages(imagesToProcess, addMessage, addImportantMessage, asset.name, queue, abortSignal);
  incrementAssetsCompleted();
  if (abortSignal.aborted) {
    queue.clear();
    throw new Error('Aborted');
  }
  addMessage(`Finished processing ${asset.name}`);
};





async function processImages(images, addMessage, addImportantMessage, assetName, queue, abortSignal) {
  const promises = images.map((image) => {
    return downloadAndCompressImageWithRetry(image.url, addMessage, addImportantMessage, assetName, 3, queue, abortSignal);
  });

  await Promise.all(promises);
}


function encodeImageUrl(imageUrl) {
  // Step 1: Replace slashes '/' with '%2F'
  let tempUrl = imageUrl.replace(/\//g, '%2F');

  // Step 2: Replace spaces ' ' with '%20'
  tempUrl = tempUrl.replace(/ /g, '%20');

  // Step 3: Do NOT encode '%' signs
  // Leave percent signs '%' as they are

  return tempUrl;
}


async function downloadAndCompressImageWithRetry(imageUrl, addMessage, addImportantMessage, assetName, retries = 3, queue, abortSignal) {
  if (abortSignal.aborted) {
    queue.clear();
    throw new Error('Aborted');
  }
  if (await hasImage(imageUrl, addImportantMessage, assetName)) {
    addMessage(`${assetName}: Skipping ${imageUrl}, as it already exists in IndexedDB.`);
    return;
  }
  if (!imageUrl || imageUrl === '') {
    addImportantMessage(`${assetName}: Skipping an image, as it points to nothing.`, 'warn');
    return;
  }
  for (let attempt = 1; attempt <= retries; attempt++) {
    if (abortSignal.aborted) {
      queue.clear();
      throw new Error('Aborted');
    }
    try {
      addMessage(`Attempting to download ${imageUrl} (Attempt ${attempt})`);
      console.log('Original imageUrl:', imageUrl);


      // const fileShareUrl = await getImageUrlFromLookupTable(decodeURI(imageUrl).replace(/\.[^/.]+$/, ""), lookupTable);

      // Use the revised encoding function
      // const encodedImageUrl = encodeImageUrl(imageUrl);

      // console.log('Encoded Image URL:', encodedImageUrl);

      // const fileShareUrl = `${IMG_URL}${encodedImageUrl}`;
      // /dA/0090fd034c694e805e0568b25932a5b3/fileAsset/WZ551_APC_%28NN%29d110.jpg'
      const idMatch = imageUrl.match(identifierRegex);
      const imageId = idMatch && `${BASE_URL}${idMatch[0].slice(1)}`;
      console.log('downloadAndCompressImageWithRetry fileShareUrl:', imageId);

      const response = await queue.add(() => enqueueAbortableFetch(imageId, abortSignal));
      console.log("downloadAndCompressImageWithRetry proxiedImageUrl response:");
      console.dir(response);

      if (!response.ok) {
        throw new Error(`Network response was not ok: ${response.statusText}`);
      }

      const contentType = response.headers.get('content-type');

      let blob = await response.blob();

      if (!contentType || !contentType.startsWith('image/')) {
        blob = new Blob([blob], { type: "image/jpeg" });
        addImportantMessage(`${assetName}: Could not find contentType for ${imageUrl}. Assuming it is of type "image/jpeg".`, "warn");
      }

      if (abortSignal.aborted) {
        queue.clear();
        throw new Error('Aborted');
      }
      const compressedBlob = await compressImage(blob, imageUrl, addImportantMessage, assetName);
      if (abortSignal.aborted) {
        queue.clear();
        throw new Error('Aborted');
      }
      await addImage(imageUrl, compressedBlob);
      addMessage(`Downloaded, compressed, and stored ${imageUrl}`);
      return compressedBlob;
    } catch (error) {
      if (abortSignal.aborted) {
        queue.clear();
        throw new Error('Aborted');
      }
      addImportantMessage(`${assetName}: Attempt ${attempt} failed for ${imageUrl}: ${error.message}`, 'error');
      if (attempt === retries) addImportantMessage(`${assetName}: Could not fetch ${imageUrl}: ${error.message}`, 'error');
      await new Promise((resolve) => setTimeout(resolve, 1000 * attempt));
    }
  }
}


export const beginImageImport = async (setTotalAssets, incrementAssetsCompleted, assetImagesLimit, abortSignal) => {
  const addMessage = () => {};
  const addImportantMessage = () => {};

  const assets = await getAssets();
  setTotalAssets(assets.length);
  addMessage(`${assets.length} assets retrieved from IndexedDB.`, 'success', true);
  // addMessage("Building a lookup table...");
  // const lookupTable = await buildImageLookupTable();
  // console.log("lookupTable");
  // console.dir(lookupTable)
  await downloadAndStoreImagesInBatches(assets, addMessage, addImportantMessage, incrementAssetsCompleted, assetImagesLimit, abortSignal);
}

export const removeFirstXImages = async (x, addMessage) => {
  console.dir(x);
  console.dir(addMessage);
  try {
    const db = await initDB();

    const tx = db.transaction(IMAGE_STORE, 'readwrite');
    const store = tx.objectStore(IMAGE_STORE);

    const keys = await store.getAllKeys();

    if (keys.length === 0) {
      addMessage('No images found in IndexedDB to remove.');
      return false;
    }

    const numToRemove = Math.min(x, keys.length);

    for (let i = 0; i < numToRemove; i++) {
      await store.delete(keys[i]);
      addMessage(`Removed image with URL: ${keys[i]}`);
    }

    await tx.done;

    addMessage(`Successfully removed ${numToRemove} image(s) from IndexedDB.`);
    return true;
  } catch (error) {
    addMessage(`Error removing images from IndexedDB: ${error}`, "error");
    return false;
  }
};

