// Dependencies
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import RouterPropTypes from 'react-router-prop-types';
import PropTypes from 'prop-types';

// API
import { getSubnav } from 'dataAccess';

// Utils
import { getParams, getUrl, setUrl } from 'Utils/Router';

// UI
import Icon from 'UI/Icon';
import Dropdown from 'UI/Dropdown';

// Components
import Tiers from 'Components/Tiers';

// Styles
import './WegSubNavigation.css';

// Proliferation country flag data
import ISOData from './proliferationCountryISO.json';

let ctc = false;

const prolifCountryISO = ISOData;

function findCountryCode(countryName) {
  const country = prolifCountryISO.find((c) => c.name === countryName);
  return country ? country.code : 'Country not found';
}

/**
 * Fetch and render the subnavigation bar for the current URL page
 */
class WegSubNavigation extends Component {
  constructor(props) {
    super(props);
    this.mainContainer = null;
    this.selectedContainer = null;
    this.unselectedContainer = null;
    this.utilsRef = React.createRef();
    this.ctc = false;
    this.catDropdownTitle = 'Category';
  }

  state = {
    additionalBtns: [],
    utilBtns: [],
    unselected: [],
    topList: [],
    linkMap: {},
    routeParams: {},
    printRef: {
      isOpen: false,
      node: null,
    },
    mainWidth: 0,
    selectedWidth: 0,
    unselectedWidth: 0,
    countries: null,
    country: null,
    proliferationCountries: null,
    environments: null,
    selectedProliferationCountry: null,
    domain: null,
    selectedCat: null,
    selectedSubCat: null,
    selectedAdditionalSubCat: null,
    selectedEnvironment: null,
    tiers: [],
  };

  componentWillMount() {
    document.addEventListener('click', this.clickOutside, false);
  }

  componentDidMount() {
    this.fetchSubnav();
    window.addEventListener('resize', this.updateWidths);
  }

  componentDidUpdate(prevProps, prevState) {
    const { location: newRoute } = this.props;
    const { location: oldRoute } = prevProps;
    const newUrl = getUrl(newRoute);
    const oldUrl = getUrl(oldRoute);
    if (newUrl !== oldUrl) {
      this.fetchSubnav();
    }
    const { unselected: oldList } = prevState;
    const { unselected: newList } = this.state;
    if (oldList !== newList) {
      this.updateWidths();
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWidths);
    document.removeEventListener('click', this.clickOutside, false);
  }

  setUniqueDomains() {
    const { topCats } = this.state;
    console.info("topCats:");
      console.dir(topCats);

    const domainArray = Object.values(topCats).map((x) => ({
      icon: x.name,
      name: x.name,
      variable: x.variable,
    }));

    this.setState({ domains: domainArray });
  }

  /**
   * Get the widths of the selected and unselected button sections so
   * we can use them for the unselected button scrolling behaviour
   */
  updateWidths = () => {
    if (!this.mainContainer || !this.unselectedContainer) {
      return;
    }

    const { mainWidth, selectedWidth, unselectedWidth } = this.state;

    if (mainWidth !== this.mainContainer.offsetWidth) {
      this.setState({ mainWidth: this.mainContainer.offsetWidth });
    }

    let currentSelectedWidth = 0;
    if (this.selectedContainer) {
      currentSelectedWidth = this.selectedContainer.offsetWidth;
    }

    if (selectedWidth !== currentSelectedWidth) {
      this.setState({ selectedWidth: currentSelectedWidth });
    }

    const unselectedChildren = Array.from(this.unselectedContainer.childNodes);
    const currentUnselectedWidth = unselectedChildren.reduce(
      (acc, cur) => acc + cur.offsetWidth + 4,
      0
    );

    if (unselectedWidth !== currentUnselectedWidth) {
      this.setState({ unselectedWidth: currentUnselectedWidth });
    }

    this.updateScrollbarWidth();
  };

  /**
   * Fetch the appropriate subnavigation list to display
   */
  fetchSubnav = () => {
    const { location } = this.props;
    const routeParams = getParams(location);

    const utilBtns = [];

    getSubnav().then((response) => {
      const { links: topList, linkMap, reference, data, topCats } = response;
      const { wegCallback } = this.props;
      wegCallback(linkMap);
      let refTitle = reference;
      if (ctc) refTitle = 'CTC Equipment Guide';
      this.setCountryOptions(data);
      this.setDateCountries(data);
      this.setEnvironmentOptions(data);
      const { page } = routeParams;
      const additionalBtns = this.setTiers(page);
      console.info("topCats:");
      console.dir(topCats);

      this.setState({
        additionalBtns,
        utilBtns,
        topList,
        linkMap,
        routeParams,
        topCats,
      });

      this.setUniqueDomains();
      this.getNavLists();
    });
  };

  /**
   * Determine which buttons should be displayed in which list
   */
  getNavLists = () => {
    const { topList, routeParams, countries, proliferationCountries, domains } =
      this.state;
    if (routeParams.page && countries) {
      const catList = routeParams.page
        .split('&')
        .map((cat) => cat.split(' ')[1]);
      this.getCountries(countries, catList);
      this.getProliferationCountries(proliferationCountries, catList);
      this.getDomains(domains, catList);
      this.getCategories(topList, catList);
    } else {
      this.setState({
        country: null,
        selectedProliferationCountry: null,
        domain: null,
        selectedCat: null,
        selectedSubCat: null,
        selectedAdditionalSubCat: null,
      });
    }
  };

  /**
   * Toggle a tier category for WEG filters
   */
  clickTier = (tier) => {
    const { routeParams } = this.state;
    const { history } = this.props;
    const { page } = routeParams;
    let newList = [];
    if (page) {
      const catList = page.split('&');
      const tierIndex = catList.indexOf(tier);
      if (tierIndex > -1) {
        catList.forEach((cat) => {
          if (cat !== tier) {
            newList.push(cat);
          }
        });
      } else {
        const categoryList = [];
        const tierList = [];
        catList.forEach((cat) => {
          if (cat.indexOf('Tier') > -1) {
            tierList.push(cat);
          } else {
            categoryList.push(cat);
          }
        });
        tierList.push(tier);
        tierList.sort();
        newList = tierList.concat(categoryList);
      }
    } else {
      newList.push(tier);
    }
    const newRoute = {
      section: 'WEG',
      type: 'List',
      page: newList.join('&'),
    };
    const toUrl = setUrl('WEG', newRoute);
    history.push(toUrl);
  };

  /**
   * Close the print popup when the user clicks outside of it
   */
  clickOutside = (event) => {
    const { printRef } = this.state;
    if (printRef.isOpen) {
      if (printRef.node && printRef.node.contains(event.target)) return;
      this.togglePrint();
    }
  };

  /**
   * Toggle the display of the print popup
   */
  togglePrint = () => {
    const { printRef } = this.state;
    printRef.isOpen = !printRef.isOpen;
    this.setState({ printRef });
  };

  /**
   * Set a reference to the print pop up container for use with the `clickOutside` event
   */
  setPrintRef = (node) => {
    const { printRef } = this.state;
    printRef.node = node;
    this.setState({ printRef });
  };

  setCountryOptions = (data) => {
    const countriesData = data.content.children.origin.children;
    const countries = [];
    Object.keys(countriesData).forEach((countryKey) => {
      const country = countriesData[countryKey];
      if (!country.name.startsWith('Notional')) countries.push(country);
    });
    countries.sort(this.compareByName());
    // push Other to bottom
    const position = countries.findIndex((country) => country.name === 'Other');
    countries.push(countries.splice(position, 1)[0]);
    return this.setState({ countries });
  };

  getProliferationCountriesFromSubnav = (subNavRes) => {
    const proliferationCountries = [];
    const regions = subNavRes.content.children.proliferation.children;
    Object.keys(regions).forEach((regionKey) => {
      const regionObj = regions[regionKey];
      const regionName = regionObj.name;
      Object.keys(regionObj.children).forEach((countryKey) => {
        const countryObj = regionObj.children[countryKey];
        countryObj.countryName = countryObj.name;
        countryObj.regionName = regionName;
        countryObj.countryId = 1;
        countryObj.proliferationCountryISO = findCountryCode(
          countryObj.name
        ).toLowerCase();
        delete countryObj.name;
        proliferationCountries.push(countryObj);
      });
    });
    return proliferationCountries;
  };

  getProliferationCountriesFromDateRegions = (dateRegions) => {
    const proliferationCountries = [];
    dateRegions.forEach((region) => {
      region.countries.forEach((country) => {
        if (!country.title.includes('DATE')) {
          proliferationCountries.push({
            countryName: country.title,
            regionName: region.title,
            countryId: 1,
            identifier: country.identifier,
          });
        }
      });
    });
    return proliferationCountries;
  };

  setProliferationOptions = (subNavRes, dateRegions) => {
    const proliferationCountries = [
      ...this.getProliferationCountriesFromSubnav(subNavRes),
      ...this.getProliferationCountriesFromDateRegions(dateRegions),
    ];
    proliferationCountries.sort((a, b) => {
      const nameA = a.countryName.toLowerCase();
      const nameB = b.countryName.toLowerCase();
      if (nameA < nameB) return -1;
      if (nameA > nameB) return 1;
      return 0;
    });
    return this.setState({ proliferationCountries });
  };

  setEnvironmentOptions = (data) => {
    const envs = data.content.children.environment.children;
    const environments = [];
    Object.keys(envs).forEach((key) => {
      const envObj = envs[key];
      envObj.icon = envObj.name;
      environments.push(envObj);
    });
    return this.setState({ environments });
  };

  setCountry = (data) => {
    this.setState({ country: data }, () => {
      this.setURL();
    });
  };

  removeCountry = () => {
    this.setState({ country: null }, () => {
      this.setURL();
    });
  };

  setProliferationCountry = (data) => {
    this.setState({ selectedProliferationCountry: data }, () => {
      this.setURL();
    });
  };

  removeProliferationCountry = () => {
    this.setState({ selectedProliferationCountry: null }, () => {
      this.setURL();
    });
  };

  setDomain = (domain) => {
    this.removeCategory();
    this.setState({ domain }, () => {
      this.setURL();
    });
  };

  removeDomain = () => {
    this.removeCategory();
    this.setState({ domain: null }, () => {
      this.setURL();
    });
  };

  setCategory = (selectedCat) => {
    this.removeSubCategory();
    this.removeAdditionalSubCategory();
    this.checkDomain(selectedCat);
    this.setState({ selectedCat }, () => {
      this.setURL();
    });
  };

  removeCategory = () => {
    this.removeSubCategory();
    this.removeAdditionalSubCategory();
    this.setState({ selectedCat: null }, () => {
      this.setURL();
    });
  };

  setSubCategory = (selectedSubCat) => {
    this.removeAdditionalSubCategory();
    this.checkDomain(selectedSubCat);
    this.setState({ selectedSubCat }, () => {
      this.setURL();
    });
  };

  removeSubCategory = () => {
    this.removeAdditionalSubCategory();
    this.setState({ selectedSubCat: null }, () => {
      this.setURL();
    });
  };

  setAdditionalSubCategory = (selectedAdditionalSubCat) => {
    this.checkDomain(selectedAdditionalSubCat);
    this.setState({ selectedAdditionalSubCat }, () => {
      this.setURL();
    });
  };

  removeAdditionalSubCategory = () => {
    this.setState({ selectedAdditionalSubCat: null }, () => {
      this.setURL();
    });
  };

  setEnvironment = (selectedEnvironment) => {
    this.setState({ selectedEnvironment }, () => {
      this.setURL();
    });
  };

  removeEnvironment = () => {
    this.removeCategory();
    this.setState({ selectedEnvironment: null }, () => {
      this.setURL();
    });
  };

  checkDomain = (category) => {
    const { domain } = this.state;
    const isCategoryOutsideDomain =
      domain && domain.selected.name !== category.selected.domain;
    if (!domain || isCategoryOutsideDomain) {
      this.setState({
        domain: {
          selected: {
            icon: category.selected.domain,
            name: category.selected.domain,
            variable: category.selected.domainVariable,
          },
          type: 2,
        },
      });
    }
  };

  setURL = () => {
    const {
      country,
      selectedProliferationCountry,
      domain,
      selectedCat,
      selectedSubCat,
      selectedAdditionalSubCat,
      selectedEnvironment,
      tiers,
    } = this.state;

    const { history } = this.props;
    const newRoute = {};

    const catList = [];
    if (tiers.length) catList.push(tiers.join('&'));
    if (country) catList.push(`ORIGIN_${country.selected.variable}`);
    if (selectedProliferationCountry) {
      const { identifier, variable } = selectedProliferationCountry.selected;
      if (identifier) {
        catList.push(`OE_${identifier}`);
      } else {
        catList.push(`PRO_${variable}`);
      }
    }
    if (domain) catList.push(`DOM_${domain.selected.variable}`);
    if (selectedCat) catList.push(`DOM_${selectedCat.selected.variable}`);
    if (selectedSubCat) catList.push(`DOM_${selectedSubCat.selected.variable}`);
    if (selectedAdditionalSubCat)
      catList.push(`DOM_${selectedAdditionalSubCat.selected.variable}`);
    if (selectedEnvironment)
      catList.push(`DOM_${selectedEnvironment.selected.variable}`);

    if (catList.length) {
      const page = catList.join('&');
      if (!/^[s&]+$/.test(page)) {
        newRoute.type = 'List';
        newRoute.page = page;
      }
    }
    const toUrl = setUrl('WEG', newRoute);
    history.push(toUrl);
  };

  compareByName = () => (a, b) => {
    const nameA = a.name.toLowerCase();
    const nameB = b.name.toLowerCase();
    if (nameA < nameB) {
      return -1;
    }
    if (nameA > nameB) {
      return 1;
    }
    return 0;
  };

  setDateCountries = (data) => {
    const enableDateCountries = false;
    if (enableDateCountries) {
      getDateProliferations().then((regions) => {
        this.setProliferationOptions(data, regions);
      });
    } else {
      const regions = [];
      this.setProliferationOptions(data, regions);
    }
  };

  setTiers = (page) => {
    const tiers = [];
    const additionalBtns = [];
    if (page) {
      const catList = page.split('&');
      catList.forEach((cat) => {
        if (cat.indexOf('Tier') > -1) {
          tiers.push(cat);
        }
      });
      this.setState({ tiers });
    }
    this.setState({ tiers });
    additionalBtns.push(
      <Tiers key="tier-btns" list={tiers} isButton click={this.clickTier} />
    );
    return additionalBtns;
  };

  getCategories = (topList, catList) => {
    let newSubcats;
    const searchObj = topList.subLinks ? topList.subLinks : topList;
    const categoryObj = searchObj.find((obj) => catList.includes(obj.variable));
    if (categoryObj !== undefined) {
      const selectedCat = { selected: categoryObj, type: 'Category' };
      if (selectedCat) this.setState({ selectedCat });
      const subCatObj = this.getSubcategory(categoryObj, catList);
      this.getSubcategory(subCatObj, catList, 2);
    } else {
      this.setState({
        selectedCat: null,
        selectedSubCat: null,
        selectedAdditionalSubCat: null,
      });
    }
    return newSubcats;
  };

  getDomains = (domains, catList) => {
    const domainObj = domains.find((obj) => catList.includes(obj.variable));
    if (domainObj) {
      const domain = { selected: domainObj, type: 'Domain' };
      setTimeout(() => this.setState({ domain }), 1);
    } else {
      this.setState({ domain: null });
    }
  };

  getProliferationCountries = (proliferationCountries, catList) => {
    const proliferationCountryObj = proliferationCountries.find((obj) =>
      catList.includes(obj.variable)
    );
    if (proliferationCountryObj) {
      const selectedProliferationCountry = {
        selected: proliferationCountryObj,
        type: 'Proliferation Country',
      };
      setTimeout(() => this.setState({ selectedProliferationCountry }), 10);
    } else {
      this.setState({ selectedProliferationCountry: null });
    }
  };

  getCountries = (countries, catList) => {
    const countryObj = countries.find((obj) => catList.includes(obj.variable));
    if (countryObj) {
      const country = { selected: countryObj, type: 'Country' };
      setTimeout(() => this.setState({ country }), 10);
    } else {
      this.setState({ country: null });
    }
  };

  getSubcategory = (categoryObj, catList, stateName = 1) => {
    let subCatObj = null;
    if (categoryObj) {
      subCatObj = categoryObj.subLinks.find((obj) =>
        catList.includes(obj.variable)
      );
      const selectedSubCat = subCatObj
        ? { selected: subCatObj, type: 'Category' }
        : null;
      if (stateName === 1) this.setState({ selectedSubCat });
      else this.setState({ selectedAdditionalSubCat: selectedSubCat });
    } else if (stateName === 1) this.setState({ selectedSubCat: null });
    else this.setState({ selectedAdditionalSubCat: null });
    return subCatObj;
  };

  subLinksWithIcons(subLinks) {
    const { linkMap } = this.state;
    const subLinksWithIcons = [];
    subLinks.forEach((link) => {
      if (!link.icon) {
        let currentLink = link;
        while (currentLink) {
          if (currentLink.icon) {
            link.icon = currentLink.icon;
            subLinksWithIcons.push(link);
            break;
          }
          if (!currentLink.parent) {
            break;
          }
          currentLink = linkMap[currentLink.parent];
        }
      } else {
        subLinksWithIcons.push(link);
      }
    });
    subLinksWithIcons.sort(this.compareByName());
    return subLinksWithIcons;
  }

  filterCategories(domain) {
    const { topList } = this.state;
    let categories = null;
    if (domain) {
      categories = topList.filter((cat) => cat.domain === domain.selected.name);
    } else {
      categories = topList;
    }
    categories.sort(this.compareByName());
    return categories;
  }

  render() {
    const {
      additionalBtns,
      utilBtns,
      country,
      countries,
      proliferationCountries,
      selectedProliferationCountry,
      domains,
      domain,
      selectedCat,
      selectedSubCat,
      selectedAdditionalSubCat,
      environments,
      selectedEnvironment,
    } = this.state;


    const homeButton = null;

    const testrender = (
      <div className="category-container">
        {!this.ctc && (
          <>
            <Dropdown
              className="environment-select"
              title="Environment"
              tooltip="Select Special Environments"
              setSelection={this.setEnvironment}
              removeSelection={this.removeEnvironment}
              selectionItems={environments}
              selectedData={selectedEnvironment}
            />
            <Dropdown
              className="country-select"
              title="Origin"
              icon="Country"
              tooltip="Select Country"
              setSelection={this.setCountry}
              removeSelection={this.removeCountry}
              selectionItems={countries}
              selectedData={country}
            />
            <Dropdown
              className="proliferation-select"
              title="Proliferation"
              icon="Country"
              tooltip="Select Proliferation Country"
              setSelection={this.setProliferationCountry}
              removeSelection={this.removeProliferationCountry}
              selectionItems={proliferationCountries}
              selectedData={selectedProliferationCountry}
            />
            <Dropdown
              className="domain-select"
              title="Domain"
              icon="Domain"
              tooltip="Select Domain"
              setSelection={this.setDomain}
              removeSelection={this.removeDomain}
              selectionItems={domains}
              selectedData={domain}
              useDropdownIcons
            />
          </>
        )}
        <Dropdown
          className="category-select"
          title={this.catDropdownTitle}
          tooltip={`Select ${this.catDropdownTitle}`}
          setSelection={this.setCategory}
          removeSelection={this.removeCategory}
          selectionItems={this.filterCategories(domain)}
          selectedData={selectedCat}
        />
        {!this.ctc && selectedCat && selectedCat.selected.subLinks && (
          <Dropdown
            className="category-select"
            title="Subcategory"
            tooltip="Select Subcategory"
            setSelection={this.setSubCategory}
            removeSelection={this.removeSubCategory}
            selectionItems={this.subLinksWithIcons(
              selectedCat.selected.subLinks
            )}
            selectedData={selectedSubCat}
          />
        )}
        {selectedSubCat && selectedSubCat.selected.subLinks && (
          <Dropdown
            className="category-select"
            title="Subcategory"
            tooltip="Select Subcategory"
            setSelection={this.setAdditionalSubCategory}
            removeSelection={this.removeAdditionalSubCategory}
            selectionItems={this.subLinksWithIcons(
              selectedSubCat.selected.subLinks
            )}
            selectedData={selectedAdditionalSubCat}
          />
        )}
      </div>
    );

    return (
      <div className="subnav">
        <div className="subnav-utils" key="subnav-utils" ref={this.utilsRef}>
          {utilBtns}
        </div>
        <div className="vert-line" />
        {!this.ctc && additionalBtns}
        {homeButton}
        {testrender}
      </div>
    );
  }
}

// Define data type of the properties passed to the component
WegSubNavigation.propTypes = {
  wegCallback: PropTypes.func,
  /**
   * Represents where the app is now to determine when navigation happens.
   * Provided by react-router-dom and the withRouter method
   */
  location: RouterPropTypes.location.isRequired,
  /**
   * Library that lets you easily manage session history anywhere JavaScript runs
   * Provided by react-router-dom and the withRouter metho
   */
  history: RouterPropTypes.history.isRequired,
  /**
   * Passed to the Print component for printing the currently displayed search results
   */
  paramList: PropTypes.array.isRequired,
};

// Set default values of the optional properties
WegSubNavigation.defaultProps = {
  wegCallback: () => {},
};

export default withRouter(WegSubNavigation);
