import React, { useState, useEffect } from 'react';
// import DOMPurify from "dompurify";
import Button from 'UI/Button';
import { htmlFromFragment } from 'Utils/Transforms/DotCMS';
import './InstallModal.css';

export default function InstallModal({ close, isFullScreen }) {
  const INSTALL_INSTRUCTIONS = [
    { type: 'p', lineHeight: 'normal', children: [{ text: '' }] },
    {
      type: 'p',
      lineHeight: 'normal',
      children: [{ text: 'How to Install the Application:', bold: true }],
    },
    {
      type: 'p',
      lineHeight: 'normal',
      children: [
        {
          text: 'To install the WEG app for on-the-go access and offline use, select your desired platform (Desktop or Mobile) and watch the short instructional video that guides you through the installation process. The video covers step-by-step instructions for supported browsers (Chrome and Edge) and mobile operating systems (iOS and Android).',
        },
      ],
    },
    { type: 'p', lineHeight: 'normal', children: [{ text: '' }] },
    {
      type: 'p',
      lineHeight: 'normal',
      children: [
        { text: 'How to Import Latest Data for the Application:', bold: true },
      ],
    },
    {
      type: 'p',
      lineHeight: 'normal',
      children: [
        {
          text: "After installing the WEG app, import the latest data to ensure you have the most up-to-date information. This initial import creates a snapshot of the current data, and you'll need to repeat this process periodically to keep your data current and synchronized with the WEG.",
        },
      ],
    },
    { type: 'p', lineHeight: 'normal', children: [{ text: '' }] },
    {
      type: 'p',
      lineHeight: 'normal',
      children: [{ text: 'How to Remove the Application:', bold: true }],
    },
    {
      type: 'p',
      lineHeight: 'normal',
      children: [
        {
          text: 'To uninstall the WEG app, go to "Add or Remove Programs" on your desktop or access your device\'s Settings menu on mobile, then locate the app and follow the prompts to uninstall or remove it.',
        },
      ],
    },
  ];

  const getHtml = () => {
    return htmlFromFragment(JSON.stringify(INSTALL_INSTRUCTIONS));
  };

  const desktopButton = (
    <button
      className="install-video-button"
      onClick={() =>
        alert('This will open a video with desktop installation instructions.')
      }
    >
      <div style={{ minWidth: '27px' }}>
      <img src="desktop_icon.png" alt="desktop icon" />
      </div>
      <span>DESKTOP INSTALLATION</span>
    </button>
  );

  const mobileButton = (
    <button
      className="install-video-button"
      onClick={() =>
        alert(
          'This will open a video with smartphone installation instructions.'
        )
      }
    >
      <div style={{ minWidth: '24px' }}>
      <img src="mobile_icon.png" alt="mobile icon" />
      </div>
      <span>MOBILE INSTALLATION</span>
    </button>
  );

  return (
    <div className={`instructions-modal${isFullScreen ? ' fullscreen' : ''}`}>
      <div className="instructions-modal-header-container">
        <div className="instructions-modal-header-left">
          <div style={{ minWidth: '27px' }}>
            <img src="app_icon.svg" className="invert-color" />
          </div>
          <div className="instructions-modal-header">
            <div>
              <h3>ODIN WEG Application Information</h3>
            </div>
          </div>
        </div>
        <Button
          className="close-modal"
          title="Close Modal"
          click={close}
          icon="close"
          hideTitle
        />
      </div>

      <div className="instructions-modal-content">
        <div className={`blurb`}>
          <div dangerouslySetInnerHTML={{ __html: getHtml() }}></div>
        </div>

        <div className="instructions-button-container">
          {desktopButton}
          {mobileButton}
        </div>
      </div>
    </div>
  );
}
