/**
 * Converts the passed location object (from react-router-dom) into an object with routing info
 * `{
 *   section: (string) The name of the current section of the website,
 *   params: (object) Any additional paramerts passed to via the URL,
 *   showSearch: (bool) Toggles the search box in the header bar,
 *   url: (string) The relative URL of the current page
 * }`
 */
export const getRouteInfo = (data) => {
  const { pathname } = data;
  const { VITE_ctc } = import.meta.env;
  let ctc = true;
  if (VITE_ctc === 'false') {
    ctc = false;
  }

  let modifiedPathname = pathname;
  // make sure we don't accidentally have an extra param if the path ends with a /
  if (modifiedPathname[modifiedPathname.length - 1] === '/') {
    modifiedPathname = modifiedPathname.slice(0, modifiedPathname.length - 1);
  }
  const urlArray = modifiedPathname.replace('/', '').split('/');
  let section = '';
  let showSearch = false;
  const params = {};
  if (urlArray.length > 0) {
    let [sectionName, param1, param2, param3] = urlArray;
    const underscoreRegEx = new RegExp('_', 'g');
    if (sectionName) sectionName = sectionName.replace(underscoreRegEx, ' ');
    if (param1) param1 = param1.replace(underscoreRegEx, ' ');
    if (param2) param2 = param2.replace(underscoreRegEx, ' ');
    if (param3) param3 = param3.replace(underscoreRegEx, ' ');
    section = sectionName;
    if (urlArray.length > 1) {
      showSearch = true;
      switch (section) {
        case 'WEG':
          params.type = param1;
          params.page = param2;
          if (urlArray.length > 3) {
            const pageCont = [];
            urlArray.forEach((nameString, index) => {
              if (index > 2) pageCont.push(nameString);
            });
            params.page += `/${pageCont.join('/')}`;
          }
          if (params.page && params.page.indexOf('::') > -1) {
            const sepRegEx = new RegExp('::', 'g');
            params.page = params.page.replace(sepRegEx, '/');
          }
          break;

        case 'TC':
          params.tc = param1;
          if (param2) params.article = param2;
          else params.article = param1;
          break;

        case 'DATE':
          params.region = param1;
          params.org = param2;
          if (param3) params.article = param3;
          else if (param2) params.article = param2;
          else params.article = param1;
          break;

        case 'FS':
          params.region = param1;
          params.org = param2;
          params.unit = param3;
          break;

        case 'VOA':
          params.article = param1;
          if (param2) params.article = param2;
          break;

        case 'Search':
          if (param1.toLowerCase() === 'region') {
            params.section = param1;
            params.regionCodeName = param2;
            params.searchString = param3;
          } else {
            params.section = param1;
            params.searchString = param2;
            params.filter = param3;
          }
          break;

        case 'How-To':
          params.section = param1;
          params.article = param2;
          break;

        case 'Error':
          params.type = param1;
          break;

        case 'FS-Search':
          showSearch = false;
          params.section = 'FS';
          break;

        default:
          break;
      }
    }

    if (section === 'FS' || (section === '' && ctc)) {
      showSearch = true;
    }
  }

  const dataObj = {
    section,
    params,
    showSearch,
    url: pathname,
  };

  return dataObj;
};

/**
 * Get the string value of the current section of the website the user is in.
 * Must pass in the location object from react-router-dom.
 */
export const getSection = (data) => {
  const { section } = getRouteInfo(data);
  return section;
};

/**
 * Get the string value of the current relative URL path of the page the user is in.
 * Must pass in the location object from react-router-dom.
 */
export const getUrl = (data) => {
  const { url } = getRouteInfo(data);
  return url;
};

/**
 * Get the object value of the current parameters from the url of the current page.
 * Must pass in the location object from react-router-dom.
 */
export const getParams = (data) => {
  const { params, section } = getRouteInfo(data);
  let sectionName = section;
  let howTo = false;
  if (section.toLowerCase() === 'how-to') {
    sectionName = params.section;
    howTo = true;
  }
  return {
    section: sectionName,
    howTo,
    ...params,
  };
};

/**
 * Create a relative URL with the set parameters
 * params:
 *   `section`: (String) The section to navigate to
 *   `params`: (Object) The parameters to set in the URL
 */
export const setUrl = (section, params) => {
  let url = '/';

  if (section) {
    url += section;

    if (params) {
      const paramList = [];
      switch (section.toLowerCase()) {
        case 'weg':
          if (params.type) {
            paramList.push(params.type.replace(/\s/g, '_'));
            if (params.page) {
              let { page: pageName } = params;
              pageName = pageName.replace(/\s/g, '_');
              if (pageName.indexOf('/') > -1) {
                const sepRegEx = new RegExp('/', 'g');
                pageName = pageName.replace(sepRegEx, '::');
              }
              paramList.push(pageName);
            }
          }
          break;

        case 'date':
          if (params.region) {
            paramList.push(params.region.replace(/\s/g, '_'));
            if (params.org) {
              paramList.push(params.org.replace(/\s/g, '_'));
            }
          }
          if (params.article) {
            paramList.push(params.article.replace(/\s/g, '_'));
          }
          break;

        case 'tc':
          if (params.tc) paramList.push(params.tc.replace(/\s/g, '_'));
          if (params.article && params.article !== params.tc) {
            paramList.push(params.article.replace(/\s/g, '_'));
          }
          break;

        case 'fs':
          if (params.region) {
            paramList.push(params.region.replace(/\s/g, '_'));
            if (params.org) {
              paramList.push(params.org.replace(/\s/g, '_'));
              if (params.unit) {
                paramList.push(params.unit.replace(/\s/g, '_'));
              }
            }
          }
          break;

        case 'voa':
          if (params.article) {
            paramList.push(params.article.replace(/\s/g, '_'));
          }
          break;

        case 'search':
          if (params.section) {
            if (params.section.toLowerCase() === 'search') {
              paramList.push('All');
            } else {
              paramList.push(params.section.replace(/\s/g, '_'));
              if (
                params.section.toLowerCase() === 'region' &&
                params.regionCodeName
              ) {
                paramList.push(params.regionCodeName);
              }
            }
            if (params.searchString) {
              paramList.push(params.searchString);
              if (params.filter) {
                paramList.push(params.filter.replace(/\s/g, '_'));
              }
            }
          }
          break;

        case 'how-to':
          if (params.section) {
            paramList.push(params.section.replace(/\s/g, '_'));
            if (params.article) {
              paramList.push(params.article.replace(/\s/g, '_'));
            }
          }
          break;

        case 'error':
          if (params.type) {
            paramList.push(params.type.replace(/\s/g, '_'));
          }
          break;

        default:
          break;
      }

      url += `/${paramList.join('/')}`;
    }
  }

  return url;
};
