// Dependencies
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';

// Components
import ModalTrigger from './ModalTrigger';
import ModalContent from './ModalContent';

// Assets
import './Modal.css';

/**
 * Render children content as a button to open a modal
 */
class Modal extends Component {
  state = {
    isOpen: false,
    node: null,
  };

  componentDidMount() {
    const { autoOpen } = this.props;
    if (autoOpen) {
      this.openModal();
    }
  }

  /**
   * Get the modal contents from the parent component and add it to the DOM
   */
  openModal = () => {
    const { open } = this.props;
    const isOpen = true;
    this.setState({ isOpen });
  };

  /**
   * Close the modal and remove it from the DOM
   */
  closeModal = () => {
    const { close } = this.props;
    close();
    this.setState({ isOpen: false });

    if (window.parent) {
      window.parent.postMessage('closeiFrame');
    }
  };

  /**
   * Close the modal when the escape key is pressed
   */
  keyDownHandler = (event) => event.keyCode === 27 && this.closeModal();

  /**
   * Close the modal if the user has clicked outside of the content box
   */
  clickOutside = (event) => {
    const { node } = this.state;
    if (node && node.contains(event.target)) return;
    this.closeModal();
  };

  /**
   * Set a reference to the content container for use with the `clickOutside` event
   */
  setRef = (node) => this.setState({ node });

  render() {
    const {
      children,
      showPrint,
      showShare,
      additionalButtons,
      showClose,
      isFullScreen,
      identifier,
      open,
      autoOpen,
    } = this.props;
    const { isOpen } = this.state;

    const modalContent = isOpen ? open(this.closeModal) : null;

    return (
      <Fragment>
        {!autoOpen && <ModalTrigger open={this.openModal}>{children}</ModalTrigger>}

        {isOpen && (
          <ModalContent
            content={modalContent}
            close={this.closeModal}
            clickOutside={this.clickOutside}
            modalRef={this.setRef}
            showPrint={showPrint}
            showShare={showShare}
            additionalButtons={additionalButtons}
            showClose={showClose}
            isFullScreen={isFullScreen}
            identifier={identifier}
          />
        )}
      </Fragment>
    );
  }
}

// Define data type for each property passed to the component
Modal.propTypes = {
  /**
   * Content to display in the modal button
   * Defined as the elements inside the <Modal> open and closing tags.
   */
  children: PropTypes.node.isRequired,
  /** Passed method to get the content when the modal is opened */
  open: PropTypes.func.isRequired,
  /** A method to perform when the modal is closed */
  close: PropTypes.func,
  /** Opens the modal automatically */
  autoOpen: PropTypes.bool,
  /** Booleans for whether or not to show header buttons */
  showPrint: PropTypes.bool,
  showShare: PropTypes.bool,
  /** Additional components to render on the right side, if any */
  additionalButtons: PropTypes.node,
  showClose: PropTypes.bool,
  isFullScreen: PropTypes.bool,
  identifier: PropTypes.string,
};

// Set default values for optional properties
Modal.defaultProps = {
  close: () => {},
  autoOpen: false,
  showPrint: true,
  showShare: true,
  additionalButtons: null,
  showClose: true,
  isFullScreen: false,
  identifier: null,
};

export default Modal;
