// fuse.js
import Fuse from 'fuse.js';
import { getAssets } from './indexedDB';

let fuse = null;
let fuseAssets = [];

export const initializeSearchIndex = async () => {
  console.log('initializeSearchIndex');
  const assets = await getAssets();
  console.info('initializeSearchIndex getAssets - assets retrieved from IndexedDB:');
  console.dir(assets);

  if (!assets || assets.length === 0) {
    console.warn('No assets found to initialize Fuse.js');
    return;
  }

  // Store assets for later use
  fuseAssets = assets;

  const options = {
    keys: [
      {name: 'name', weight: 0.5},
      {name: 'notes', weight: 0.2},
      {name: 'domain.name', weight: 0.1},
      {name: 'origin.name', weight: 0.1},
      // Add other relevant fields
    ],
    includeScore: true,
    threshold: 0.4
    // Remove or adjust threshold as needed
  };
  fuse = new Fuse(assets, options);

  console.info('initializeFuse - Fuse.js instance created');
};

export const getFuse = () => fuse;
export const getFuseAssets = () => fuseAssets;
