// Dependencies
import React, { Component } from 'react';
import PropTypes from 'prop-types';

// UI
import Button from 'UI/Button';
import Image from 'UI/Image';

// Components
import Tiers from 'Components/Tiers';
import { IndexedDBImage } from 'Components/IndexedDBImage'
import { getOnlineStatus } from 'dataAccess';


// Styles
import './AssetImages.css';

/** Displays the interface for navigating through mutliple images on WEG assets. */
class AssetImages extends Component {
  state = {
    slideIndex: 0,
    thumbIndex: 0,
    maxThumbs: 4, // Number of thumbnails to show
    isOnline: false
  };

  componentDidMount() {
    const { isOnline } = this.state;

    console.log("AssetImages componentDidMount")

    getOnlineStatus().then(online =>  {
      if (isOnline !== online ) this.setState({isOnline: online})
    });
  }

  componentDidUpdate() {
    const { isOnline } = this.state;

    console.log("AssetImages componentDidUpdate")

    getOnlineStatus().then(online =>  {
      if (isOnline !== online ) this.setState({isOnline: online})
    });
  }

  /**
   * Navigate through the available images, either left or right.
   */
  navigateSlides = (direction) => {
    const { slideIndex } = this.state;
    this.setState({
      slideIndex: slideIndex + direction,
    });
  };

  /**
   * Navigate through the available thumbnails, either left or right.
   */
  navigateThumbs = (direction) => {
    const { thumbIndex } = this.state;
    this.setState({
      thumbIndex: thumbIndex + direction,
    });
  };

  /**
   * Assign appropriate class names to the slides depending on whether
   * or not they are in view
   */
  slideClassNames = (index) => {
    const { slideIndex } = this.state;
    const classNames = ['slide'];
    if (slideIndex > index) classNames.push('slide-left');
    if (slideIndex < index) classNames.push('slide-right');
    return classNames.join(' ');
  };

  /** Displays a Chevron button for navigating left and right through images. */
  renderImageNavButton = (direction) => {
    console.info("AssetImages renderImageNavButton")
    const { images } = this.props;
    const { slideIndex } = this.state;

    const increase = direction > 0;

    const classNames = ['nav-button'];

    if (!increase) classNames.push('flipped');

    const invisible =
      (increase && slideIndex === images.length - 1) ||
      (!increase && slideIndex === 0);

    if (invisible) classNames.push('invisible');

    return (
      <Button
        className={classNames.join(' ')}
        title=""
        icon="chevron"
        click={invisible ? () => {} : () => this.navigateSlides(direction)}
      />
    );
  };

  /**
   * Create a slideshow of all images in the images array, and allow
   * them to be navigated through using left and right buttons
   */
  renderSlideShow = () => {
    console.info("ASsetImages renderSlideShow")
    const { images, clickImage, tiers } = this.props;
    const { isOnline } = this.state;
    console.info("WegAssetCard renderSlideShow isOnline:", isOnline);
    return (
      <div className="slideshow-container">
        {this.renderImageNavButton(-1)}
        <div className="slides">
          {/** Display the placeholder if the image array is empty */}
          {!images.length ? (
            <Image title="placeholder" file="placeholder.png" />
          ) : null}
          {/** Map out all of the images otherwise */}
          {images.map((image, index) => (
            <Button
            title=""
              key={image.name}
              className={this.slideClassNames(index)}
              click={() => clickImage(index)}
            >
              <IndexedDBImage src={image.url} alt={image.name} online={isOnline} />
            </Button>
          ))}
          <Tiers className="tiers" label="Tier: " list={tiers} />
        </div>
        {this.renderImageNavButton(1)}
      </div>
    );
  };

  /** Displays a Chevron button for navigating left and right through image thumbnails. */
  renderThumbNavButton = (direction) => {
    const { images } = this.props;
    const { thumbIndex, maxThumbs } = this.state;

    const increase = direction > 0;

    const classNames = ['nav-button', 'small'];

    if (!increase) classNames.push('flipped');

    const invisible =
      (increase && thumbIndex >= images.length - maxThumbs) ||
      (!increase && thumbIndex === 0);

    if (invisible) classNames.push('invisible');

    console.info("AssetImages renderThumbNavButton")

    return (
      <Button
        className={classNames.join(' ')}
        title=""
        icon="chevron"
        click={invisible ? () => {} : () => this.navigateThumbs(direction)}
      />
    );
  };

  /**
   * Show a set of thumbnails for additional images, along with left and
   * right buttons to navigate through them.
   */
  renderThumbnails = () => {
    const { images } = this.props;
    const { thumbIndex, isOnline } = this.state;
    console.info("WegAssetCard renderThumbnails isOnline:", isOnline);
    return (
      <div className="thumb-slideshow-container">
        {this.renderThumbNavButton(-1)}
        <div className="thumbs">
          {images.map((image, index) => (
            <Button
              title=""
              className="thumb"
              style={{ transform: `translateX(${index - thumbIndex}00%)` }}
              click={() => {
                this.setState({ slideIndex: index });
              }}
            >
              <IndexedDBImage src={image.url} alt={image.name} online={isOnline} />
            </Button>
          ))}
        </div>
        {this.renderThumbNavButton(1)}
      </div>
    );
  };

  render() {
    return (
      <div className="asset-images">
        {this.renderSlideShow()}
        {this.renderThumbnails()}
      </div>
    );
  }
}

AssetImages.propTypes = {
  // A function to perform when the currently displayed image is clicked
  clickImage: PropTypes.func.isRequired,
  // An array of images to make a slideshow with
  images: PropTypes.array,
  // The tiers array
  tiers: PropTypes.array,
};

AssetImages.defaultProps = {
  images: [],
  tiers: [],
};

export default AssetImages;
