const PWA_INSTRUCTIONS = [{"type":"p","lineHeight":"normal","children":[{"text":"How to Install the Application:","bold":true}],"align":"center"},{"type":"p","lineHeight":"normal","children":[{"text":"To install the WEG app for on-the-go access and offline use, select your desired platform (Desktop or Mobile) and watch the short instructional video that guides you through the installation process. The video covers step-by-step instructions for supported browsers (Chrome and Edge) and mobile operating systems (iOS and Android)."}],"align":"center"},{"type":"p","lineHeight":"normal","children":[{"text":""}],"align":"center"},{"type":"p","lineHeight":"normal","children":[{"text":"How to Import Latest Data for the Application:","bold":true}],"align":"center"},{"type":"p","lineHeight":"normal","children":[{"text":"After installing the WEG app, import the latest data to ensure you have the most up-to-date information. This initial import creates a snapshot of the current data, and you'll need to repeat this process periodically to keep your data current and synchronized with the WEG."}],"align":"center"},{"type":"p","lineHeight":"normal","children":[{"text":""}],"align":"center"},{"type":"p","lineHeight":"normal","children":[{"text":"How to Remove the Application:","bold":true}],"align":"center"},{"type":"p","lineHeight":"normal","children":[{"text":"To uninstall the WEG app, go to \"Add or Remove Programs\" on your desktop or access your device's Settings menu on mobile, then locate the app and follow the prompts to uninstall or remove it."}],"align":"center"}]
const PWA_MANAGEMENT = [{"type":"h3","lineHeight":"normal","children":[{"text":"MANAGE YOUR DATA","bold":true}],"align":"center"},{"type":"p","lineHeight":"normal","align":"center","children":[{"text":"Use the buttons below to manage your WEG app data:"}]},{"type":"p","lineHeight":"normal","align":"center","children":[{"text":"Import/Update Data: Get the latest information and updates by importing new data into the app. This is required initially and periodically to keep your data current."}]},{"type":"p","lineHeight":"normal","align":"center","children":[{"text":"Clear Data Storage: Remove all existing data from the app, freeing up storage space. Note: This will delete all cached data and may require re-importing data to access WEG information."}]}]
const PWA_OFFLINE = [{"type":"h4","lineHeight":"normal","children":[{"text":"You are offline.","bold":true}]}];

const getSummary = async (category) => {
  let blurb;
  switch (category) {
    case 'pwa-announcement':
      blurb = PWA_ANNOUNCEMENT;
      break;
    case 'pwa-instructions':
      blurb = PWA_INSTRUCTIONS;
      break;
    case 'pwa-management':
      blurb = PWA_MANAGEMENT;
      break;
    case 'pwa-offline':
      blurb = PWA_OFFLINE;
      break;
    default:
      blurb = PWA_OFFLINE;
  }

  return { content: JSON.stringify(blurb) };
};

const MockBlurb = ({ section, additionalClass }) => {
    const [ summary, setSummary ] = useState({});

    const summaryCategories = {
        tc: 'tp-summary',
        voa: 'voa-summary',
        weg: 'weg-summary',
        date: 'date-summary',
        home: 'home-summary',
        'pwa-announcement': 'pwa-announcement',
        'pwa-offline': 'pwa-offline'
    };

    useEffect(() => {
        const category = summaryCategories[section.toLowerCase()];
        getSummary(category).then(res => setSummary(res))
    }, [section]);

    const getHtml = () => {
      console.log('MockBlurb getHtml');
      console.dir(summary)
        if (summary?.content) {
            const { content } = summary;
            const html = htmlFromFragment(content);
            return DOMPurify.sanitize(html);
        }
        return null;
    }

    return (
        <div className={`blurb ${additionalClass}`}>
            <div dangerouslySetInnerHTML={{ __html: getHtml() }}></div>
        </div>
    );
}

export default MockBlurb;
