import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Icon from 'UI/Icon';
import SearchBox from 'UI/SearchBox';
import Blurb from 'Components/Blurb';
import MockBlurb from 'Components/MockBlurb';
import PWAButton from 'Components/PWAButton';
import { usePWAStatus } from 'Context';
import './SplashPage.css';
import { resetLocalDB, countAssets } from 'dataAccess';

const SplashPage = (props) => {
  const { section } = props;
  const { VITE_ctc } = import.meta.env;
  const { isPWA, isInstalled } = usePWAStatus();
  const [clearDisabled, setClearDisabled] = useState(true);
  let ctc = true;
  if (VITE_ctc === 'false') {
    ctc = false;
  }

  useEffect(() => {
    countAssets().then((res) => {
      console.info('SpashPage countAssets res:');
      console.dir(res);
      setClearDisabled(res < 1);
    });
    const handleImportStatusChanged = (event) => {
      setClearDisabled(event.ongoing);
      console.info('handleImportStatusChanged: ', event);
    };

    window.addEventListener('importStatusChanged', handleImportStatusChanged);

    return () => {
      window.removeEventListener(
        'importStatusChanged',
        handleImportStatusChanged
      );
    };
  }, []);

  const handleOpenImport = () => {
    console.info('opening import modal!');
    const event = new CustomEvent('openImportModal');
    window.dispatchEvent(event);
  };

  const handleOpenClear = () => {
    console.info('opening clear modal!');
    const event = new CustomEvent('openClearModal');
    window.dispatchEvent(event);
  };

  const getIcon = () => {
    let iconName = null;
    switch (section.toLowerCase()) {
      case 'fs':
        iconName = 'force-structure';
        break;
      case 'tc':
        iconName = 'training-circulars';
        break;
      default:
        iconName = section.toLowerCase();
        break;
    }
    if (iconName) {
      return (
        <div className="splash-icon">
          <Icon file={iconName} />
        </div>
      );
    }
    return null;
  };

  const getTitle = () => {
    switch (section.toLowerCase()) {
      case 'date':
        return 'DATE Knowledge Base';
      case 'fs':
        return 'Force Structure';
      case 'tc':
        return 'Training Publications';
      case 'weg':
        if (ctc) return 'CTC Equipment Guide';
        return 'Worldwide Equipment Guide';
      case 'voa':
        return 'Virtual OPFOR Academy';
      default:
        return null;
    }
  };

  const handleOpenInstructions = () => {
    console.log('handleOpenInstructions called');
    const event = new CustomEvent('openInstallModal');
    window.dispatchEvent(event);
  };

  const installButton = (
    <button onClick={handleOpenInstructions} className="pwa-button">
      <div style={{ minWidth: '28px' }}>
        <img
          src="download_button.png"
          alt="download button"
          style={{ width: '24px', height: '24px', marginRight: '8px' }}
        />
      </div>

      <span style={{ fontWeight: '600', fontSize: '14px', color: '#000' }}>
        Install WEG Application
      </span>
    </button>
  );

  const updateButton = (
    <button onClick={handleOpenImport} className="pwa-button spin">
      <div style={{ minWidth: '28px' }}>
        <img
          src="refresh_icon.png"
          alt="refresh icon"
          className="invert-color smaller"
        />
      </div>

      <span style={{ fontWeight: '600', fontSize: '14px', color: '#000' }}>
        Import/Update Data
      </span>
    </button>
  );

  const clearButton = (
    <button
      onClick={handleOpenClear}
      disabled={clearDisabled}
      className="pwa-button"
    >
      <img src="erase_icon.png" alt="erase icon" className="smaller" />
      <span style={{ fontWeight: '600', fontSize: '14px', color: '#000' }}>
        Clear Data Storage
      </span>
    </button>
  );

  return (
    <div className={`splash-page ${section.toLowerCase()}`}>
      <div className="splash-title">
        {getIcon()}
        {getTitle()}
      </div>
      <SearchBox section={section} />
      {navigator.onLine ? (
        <Blurb section={section} additionalClass="splash-text" />
      ) : (
        <MockBlurb section="pwa-offline" additionalClass="splash-text" />
      )}
      <div
        style={{
          padding: '10px',
        }}
      />
      {!isPWA && (
        <MockBlurb section="pwa-announcement" additionalClass="splash-text" />
      )}
      <div
        style={{
          padding: '10px',
        }}
      />
      <div className="pwa-buttons">
        {/* {(true || !isPWA) && installButton}
        {(true || isPWA) && updateButton}
        {(true || isPWA) && clearButton} */}
        {!isPWA && installButton}
        {isPWA && updateButton}
        {isPWA && clearButton}
      </div>
      <div
        style={{
          padding: '10px',
        }}
      />
    </div>
  );
};

SplashPage.propTypes = {
  /**
   * The section splash page to display
   */
  section: PropTypes.string,
};

SplashPage.defaultProps = {
  section: null,
};

export default SplashPage;
