let uniqueID = 0;
const idList = [];

/**
 * Get a random unique ID string to assign to a DOM element. Returns a string
 */
export const getUID = (string = 'id') => {
  const time = new Date().getTime();
  uniqueID += 1;
  const uid = `${string}-${uniqueID}-${time}`;
  idList.push(uid);
  return uid;
};

/**
 * Determine if the ID string is unique among generated DOM id strings
 */
export const idIsUnique = (id) => idList.indexOf(id) > -1;
