import { getFuse, getFuseAssets } from './fuse';
import { getSubnavLocal } from './indexedDB';

let cachedSubnav = null;
let cachedLinks = null;
let domainVarToKeyMap = null;
let originVarToKeyMap = null;
let proliferationVarToKeyMap = null;
let domainDepthMap = {};

const assignDepths = (categoryMap, categoryKey, currentDepth = 0) => {
  domainDepthMap[categoryKey] = currentDepth;
  const category = categoryMap[categoryKey];
  if (
    category &&
    category.children &&
    Object.keys(category.children).length > 0
  ) {
    Object.keys(category.children).forEach((childKey) => {
      assignDepths(category.children, childKey, currentDepth + 1);
    });
  }
};

const ensureMappings = async () => {
  if (!cachedSubnav || !cachedLinks) {
    cachedSubnav = await getSubnavLocal('flatWegSubnav');
    const wegSubnavData = await getSubnavLocal('wegSubnav');

    if (cachedSubnav && wegSubnavData) {
      cachedLinks = wegSubnavData.data.content.children;

      Object.keys(cachedLinks.domain.children).forEach((key) => {
        assignDepths(cachedLinks.domain.children, key, 0);
      });

      domainVarToKeyMap = buildSubnavVarToKeyMap(cachedSubnav);
      const originSection = cachedLinks.origin.children;
      originVarToKeyMap = buildVariableToKeyMap(originSection);

      const proliferationSection = cachedLinks.proliferation.children;
      proliferationVarToKeyMap = buildProlifVariableToKeyMap(proliferationSection);
    } else {
      console.warn('Failed to load subnav and links from WEG database.');
    }
  }
};

const classifyFilters = (filterVars) => {
  const categoryFilters = [];
  let originFilter = '';
  const proliferationFilters = [];

  filterVars.forEach((filterVar) => {
    if (domainVarToKeyMap[filterVar]) {
      categoryFilters.push(domainVarToKeyMap[filterVar]);
    } else if (originVarToKeyMap[filterVar]) {
      originFilter = originVarToKeyMap[filterVar];
    } else if (proliferationVarToKeyMap[filterVar]) {
      proliferationFilters.push(proliferationVarToKeyMap[filterVar]);
    } else {
      console.warn(`Unknown filter var: ${filterVar}`);
    }
  });

  return { categoryFilters, originFilter, proliferationFilters };
};

const filterByDateRanges = (assets, dateRanges) => {
  return assets.filter((asset) => {
    const introductionDate = new Date(asset.dateOfIntroduction);
    return dateRanges.some((range) => {
      const fromDate = new Date(range.from);
      const toDate = new Date(range.to);
      return introductionDate >= fromDate && introductionDate <= toDate;
    });
  });
};

const applyDomainFilters = (assets, domainFilters) => {
  if (domainFilters.length === 0) return assets;

  return assets.filter((asset) => {
    if (!asset.domain) return false;

    const assetDomainkeys = new Set([
      ...(asset.domain || []).flatMap((d) =>
        typeof d === 'object' ? Object.keys(d) : []
      ),
    ]);

    let assetDomainDepthMap = {};

    for (const domain of assetDomainkeys) {
      const depth = domainDepthMap[domain] || 0;
      assetDomainDepthMap[depth] = domain;
    }

    let filterDomainDepthMap = {};

    for (const domain of domainFilters) {
      const depth = domainDepthMap[domain] || 0;
      filterDomainDepthMap[depth] = domain;
    }

    const targetDepth = domainFilters.length - 1;
    return (
      assetDomainDepthMap[targetDepth] === filterDomainDepthMap[targetDepth]
    );
  });
};


const applyOriginFilter = (assets, originFilter) => {
  if (!originFilter) return assets;

  return assets.filter((asset) => {
    if (!asset?.origin) return false;
    return asset.origin.some((o) => o[originFilter]);
  });
};

const applyProliferationFilters = (assets, proliferationFilters) => {
  if (proliferationFilters.length === 0) return assets;

  const proliferationKeysSet = new Set(proliferationFilters);

  return assets.filter((asset) => {
    if (!asset?.proliferation) return false;
    const assetProlifKeys = new Set(
      asset.proliferation.map((prolif) => Object.keys(prolif)[0])
    );

    return [...proliferationKeysSet].some((key) => assetProlifKeys.has(key));
  });
};

const buildSubnavVarToKeyMap = (flatSubnav = []) => {
  const variableToKeyMap = Object.create(null);
  for (const key in flatSubnav) {
    const entry = flatSubnav[key];
    if (entry.variable) {
      variableToKeyMap[entry.variable] = key;
    }
  }
  return variableToKeyMap;
};

const buildVariableToKeyMap = (input = {}) => {
  const varToKeyMap = {};
  for (const key in input) {
    const obj = input[key];
    varToKeyMap[obj.variable] = obj.key;
  }
  return varToKeyMap;
};

const buildProlifVariableToKeyMap = (input = {}) => {
  const varToKeyMap = {};
  for (const entry in input) {
    const obj = input[entry];
    varToKeyMap[obj.variable] = obj.key;
    for (const child in obj.children) {
      const subEntry = obj.children[child];
      varToKeyMap[subEntry.variable] = subEntry.key;
    }
  }
  return varToKeyMap;
};

export const searchAssetsWithFilters = async (
  filters = {},
  offset = 0,
  limit = 10
) => {
  const fuseInstance = getFuse();
  const fuseAssets = getFuseAssets();

  if (!fuseInstance) {
    console.warn('Fuse.js is not initialized.');
    return { wegList: [], totalHits: 0 };
  }

  let results;

  if (filters.searchTerm && filters.searchTerm.trim() !== '') {
    results = fuseInstance
      .search(filters.searchTerm)
      .map((result) => result.item);
  } else {
    results = [...fuseAssets];
  }

  if (filters.dateRanges && filters.dateRanges.length > 0) {
    results = filterByDateRanges(results, filters.dateRanges);
    if (results.length === 0) {
      return { wegList: [], totalHits: 0 };
    }
  }

  if (filters.categories?.length > 0) {
    await ensureMappings();

    const { categoryFilters, originFilter, proliferationFilters } =
      classifyFilters(filters.categories);

    results = applyOriginFilter(results, originFilter);
    if (results.length === 0) {
      return { wegList: [], totalHits: 0 };
    }

    results = applyProliferationFilters(results, proliferationFilters);
    if (results.length === 0) {
      return { wegList: [], totalHits: 0 };
    }

    results = applyDomainFilters(results, categoryFilters);
    if (results.length === 0) {
      return { wegList: [], totalHits: 0 };
    }
  }

  const paginatedResults = results.slice(offset, offset + limit);

  return { wegList: paginatedResults, totalHits: results.length };
};
