// Dependencies
import React from 'react';
import { Tooltip as ReactTooltip } from 'react-tooltip'
import PropTypes from 'prop-types';

// Styles
import './Tooltip.css';

/**
 * Display a tooltip on hover either as a string or HTML elements
 */
const Tooltip = (props) => {
  const { children, id, place } = props;
  return (
    <ReactTooltip id={id} place={place} html>
      {children}
    </ReactTooltip>
  );
};

// Define the data type for each property passed to the component
Tooltip.propTypes = {
  /** DOM element ID that corresponds to the `data-for` string of the element being hovered */
  id: PropTypes.string.isRequired,
  /**
   * The string or elements to display inside the tooltip box
   * Defined as the elements inside the <Tooltip> open and closing tags.
   */
  children: PropTypes.oneOfType([PropTypes.string, PropTypes.node]).isRequired,
  /** Where the tooltip should appear */
  place: PropTypes.oneOf(['top', 'right', 'bottom', 'left']),
};

// Set default values for optional properties
Tooltip.defaultProps = {
  place: 'bottom',
};

export default Tooltip;
