import React, { useState, useEffect } from 'react';
import DOMPurify from "dompurify";
import { getSummary } from 'API';
import { htmlFromFragment } from 'Utils/Transforms/DotCMS';

import './blurb.css';


const Blurb = ({ section, additionalClass }) => {
    const [ summary, setSummary ] = useState({});

    const summaryCategories = {
        tc: 'tp-summary',
        voa: 'voa-summary',
        weg: 'weg-summary',
        date: 'date-summary',
        home: 'home-summary'
    };

    useEffect(() => {
        const category = summaryCategories[section.toLowerCase()];
        getSummary(category).then(res => setSummary(res))
    }, [section]);

    const getHtml = () => {
      console.log('Blurb getHtml');
      console.dir(summary)
        if (summary?.content) {
            const { content } = summary;
            const html = htmlFromFragment(content);
            return DOMPurify.sanitize(html);
        }
        return null;
    }

    return (
        <div className={`blurb ${additionalClass}`}>
            <div dangerouslySetInnerHTML={{ __html: getHtml() }}></div>
        </div>
    );
}

export default Blurb;
